import { CurriculumItem } from "@haystackdev/cicf-content-api";
import CardParagraph from "components/CardParagraph";
import MediaTypeSquaredIcon from "components/MediaTypeSquaredIcon";
import Spacer from "components/Spacer";
import { textForMediaType } from "helpers";
import HomeContentCard from "features/Home/Home/HomeContentCard";
import React from "react";
import { useNavigate } from "react-router-dom";
import { LEARNING_PATH } from "topLevelRoutes";
import { LEARNING_PATHWAY_DETAIL_PATH, TOUCHSTONE_DETAIL_PATH } from "features/Learning/LearningRoutes";

export default function HomeContentCardLearningItem({
    item,
    disabled,
} : {
    item: CurriculumItem,
    disabled?: boolean,
}) {
    const nav = useNavigate();
    function onButtonPress() {
        nav('/' + LEARNING_PATH + '/' + TOUCHSTONE_DETAIL_PATH + '/' + item.id)
    }

    return (
        <HomeContentCard
            headerText={item.title}
            buttonText="Begin"
            onButtonPress={onButtonPress}
            buttonDisabled={disabled}
        >
            <CardParagraph>
                {item.description}
            </CardParagraph>
            <Spacer v={16}/>
            <MediaTypeSquaredIcon 
                type={item.mediaType}
                text={textForItem(item)}
            />
        </HomeContentCard>
    )
}

function textForItem(item: CurriculumItem) {
    return `${textForMediaType(item.mediaType)}, ${item.estimatedTimeInMinutes}m`
}