import AddNoteModal from "modals/AddNoteModal";
import useModalStore, { Modal, modalComponentMap } from "state/modals"
import styled from "styled-components"
import { backgroundColor } from "theme"



export default function ModalContainer() {
    const presentingModal = useModalStore((s)=>s.presentingModal);
    if(presentingModal===null) return null;
    
    const C = modalComponentMap[presentingModal];
    return (
        <Container>
            <C />
        </Container>
    )
}

const Container = styled.div`
    position: fixed;
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: flex-start;
    padding-top: 140px;
    align-items: center;
    flex-direction: column;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: ${backgroundColor['modal-overlay']};
`