import { Curriculum, CurriculumItem } from "@haystackdev/cicf-content-api";



export function getFeaturedLearningItem(curriculum: Curriculum): {
    pathwayIndex: number,
    itemId: string,
    item: CurriculumItem
} | null {
    for( var j = 0; j < curriculum.pathways.length; j++) {
        const p = curriculum.pathways[j];
        for( var i = 0; i < p.items.length; i++) {
            if(!p.items[i].isCompleted)
                return {pathwayIndex: j, itemId: p.items[i].id, item: p.items[i]};
        }
    }
    if(curriculum.pathways[0] && curriculum.pathways[0].items[0]) {
        const p = curriculum.pathways[0];
        const r = {pathwayIndex: j, itemId: p.items[0].id, item: p.items[0]};
        return r;
    }
    return null
}