import styled from "styled-components";

interface Props {
    sizing: [number, number, number]
    color?: string
}

const CICFTextSpan = styled.span<Props>`
    font-weight: ${p=>p.sizing[0]};
    font-size: ${p=>p.sizing[1]}px;
    line-height: ${p=>p.sizing[2]}px;
    ${p=>p.color?`color: ${p.color};`:''}
`

export default CICFTextSpan;