import { UserProfile } from "@haystackdev/cicf-content-api";
import useAssessmentResultsQuery from "@haystackdev/cicf-content-api/lib/queries/query/assessmentResultsQuery";
import AssessmentResultShort from "@haystackdev/cicf-content-api/lib/types/AssessmentResultShort";
import Card from "components/Card";
import CICFButton from "components/CICFButton";
import AssessmentFinishedGraphic from "components/Graphics/AssessmentFinishedGraphic";
import ThumbsUpGraphic from "components/Graphics/ThumbsUp";
import QuerySuspended from "components/QuerySuspended";
import Spacer from "components/Spacer";
import { ASSESSMENT_RESULTS_PATH } from "features/Assessment/AssessmentRoutes";
import { useNavigate } from "react-router-dom";
import styled from "styled-components";
import { text } from "styleUtilities";
import { ASSESSMENT_PATH, HOME_PATH } from "topLevelRoutes";

export default function AssessmentThankYouContent({
    previousAssessments,
} : {
    previousAssessments: AssessmentResultShort[]
}) {
    const assessmentResultsQuery = useAssessmentResultsQuery(previousAssessments.length > 1?previousAssessments.map(e=>e.id).slice(previousAssessments.length - 2, previousAssessments.length):[]);
    
    const firstTime = !(previousAssessments.length > 1);
    const nav = useNavigate();
    function onPress() {
        if(firstTime) nav('/' + HOME_PATH);
        else nav('/' + ASSESSMENT_PATH + '/' + ASSESSMENT_RESULTS_PATH);
    }



    return (
        <QuerySuspended
            queries={[assessmentResultsQuery]}
        >
            {assessmentResultsQuery.data && 
                <ThankYouCard variant='primary'>
                    <Title>
                        Thank you for taking the assessment!
                    </Title>
                    
                        {firstTime?
                            <Paragraph>You will be able to take the assessment again once you complete the first learning pathway to measure your progress</Paragraph>
                            :
                            <Paragraph>
                                You previously took the assessment on <strong>{(new Date(previousAssessments[previousAssessments.length - 2].finishedTimestamp).toLocaleDateString('en-us'))}</strong>! Let's see your assessment summary.
                            </Paragraph>
                        }
                    <AssessmentFinishedGraphic/>
                    <Spacer v={40}/>
                    <CICFButton 
                        text={firstTime?'Back Home':'View Summary'}
                        onClick={onPress}
                    />

                </ThankYouCard>
            }
        </QuerySuspended>    
    );
}


const Title = styled.h1`
    text-align: center;
    max-width: 70%;
    ${text(600, 32, 44, 'card-title')}
    margin-bottom: 28px;
`

const Paragraph = styled.p`
    text-align: center;
    max-width: 70%;
    ${text(400, 16, 24, 'card-paragraph')}
    margin-bottom: 40px;

`

const ThankYouCard = styled(Card)`
    margin-top: 60px;
    width: 560px;
    padding: 40px;
    align-items: center;
`