import { contentCopy, useRacialIdentitiesQuery, useUpdateUserProfileMutation, useUserProfileQuery } from "@haystackdev/cicf-content-api";
import CICFButton from "components/CICFButton";
import QuerySuspended from "components/QuerySuspended";
import Spacer from "components/Spacer";
import { ONBOARDING_BUTTON_TEXT } from "constants/onboardingText";
import { nextOnboardingPath } from "features/Onboarding/onboardingHelpers";
import OnboardingPage from "features/Onboarding/Pages/OnboardingPage";
import OnboardingRacialIdentityCheckbox from "features/Onboarding/Pages/OnboardingRacialIdentity/OnboardingRacialIdentityCheckbox";
import React, { Fragment, useState } from "react";
import { useNavigate } from "react-router-dom";
import styled from "styled-components";
import { text } from "styleUtilities";

export default function OnboardingRacialIdentity() {
    const racialIdentitiesQuery = useRacialIdentitiesQuery();
    const [checkedIdentitiesSet, setCheckedIdentitiesSet] = useState<Set<string>>(new Set());
    const profileQuery = useUserProfileQuery();
    const updateProfileMutation = useUpdateUserProfileMutation();
    const nav = useNavigate();

    function onCheckedIdentityChange(identity: string) {
        var newSet = new Set(checkedIdentitiesSet);
        if (checkedIdentitiesSet.has(identity)) newSet.delete(identity);
        else newSet.add(identity);
        setCheckedIdentitiesSet(newSet);
    }

    function onSaveClick() {
        updateProfileMutation.mutateAsync({
            ...profileQuery.data!,
            demographics: {
                ...profileQuery.data!.demographics,
                ethnicities: Array.from(checkedIdentitiesSet),
            }
        }).then(()=>{
            nav(nextOnboardingPath(true));
        })
    }

    const canSave = !!Array.from(checkedIdentitiesSet).length

    return (
        <OnboardingPage
            title="Racial Identity"
            subtitle={contentCopy.onboarding.racialIdentity.message}
        >
            <QuerySuspended
                queries={[racialIdentitiesQuery, profileQuery]}
            >
                {racialIdentitiesQuery.data &&
                    <CheckboxContainer>
                        <EthnicityText>
                            Ethnicity (Select one or more)
                        </EthnicityText>
                        {racialIdentitiesQuery.data.map((identity) => {
                            return (
                                <Fragment
                                    key={identity}
                                >
                                    <Spacer v={16} />
                                    <OnboardingRacialIdentityCheckbox
                                        value={checkedIdentitiesSet.has(identity)}
                                        onChange={() => onCheckedIdentityChange(identity)}
                                        label={identity}
                                    />
                                </Fragment>
                            )
                        })}
                    </CheckboxContainer>
                }
                <Spacer
                    v={40}
                />
                <CICFButton
                    text={ONBOARDING_BUTTON_TEXT}
                    onClick={onSaveClick}
                    disabled={!canSave}
                    variant={canSave?'primary':'disabled'}
                    loading={updateProfileMutation.isLoading}
                />
            </QuerySuspended>
        </OnboardingPage>
    )
}

const CheckboxContainer = styled.div`
    align-self: stretch;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
`

const EthnicityText = styled.h1`
    ${text(600, 16, 24, 'card-title')}
    align-self: flex-start;
`

