import { useState } from "react";
import { UserProfile, useUserProfileQuery } from "@haystackdev/cicf-content-api";
import styled from "styled-components";
import CICFInput from "components/CICFInput";
import CICFButton from "components/CICFButton";
import useOnKeyDown from "hooks/useOnKeyDown";
import Icon from "components/Icon";
import { backgroundColor } from "theme";

const InterestsCard = ({
  profile,
  editingInterests,
  isEditing,
  onInterestsChange
}: {
  profile: UserProfile,
  editingInterests: string[] | null,
  isEditing: boolean,
  onInterestsChange: (interests: string[]) => void;
}) => {

  const showInterests = editingInterests ? editingInterests : profile.demographics.interests;
  const editing = !!isEditing;
  const [inputText, setInputText] = useState<string>("");

  function add() {

    if (inputText && !showInterests.find(e => e == inputText)) {
      onInterestsChange([...showInterests, inputText]);
    }
    setInputText('');
  }

  function onAddPress() {
    add();
  }

  function onInputEnterPress() {
    add();
  }

  function onDeleteInterestClick(interest: string) {
    const i = showInterests.findIndex((e)=>e==interest);
    if(i !== -1) {
      const n = [...showInterests];
      n.splice(i, 1);
      onInterestsChange(n)
    }
  }

  return (
    <div
      style={{
        display: "flex",
        justifyContent: "center",
      }}
    >
      <div
        style={{
          width: "551px",
          paddingTop: 20,
          paddingBottom: 20,
          paddingLeft: 20,
          marginTop: 24,
          backgroundColor: "#FFF",
          borderRadius: 10,
          boxShadow: "0px 8px 15px rgba(0, 0, 0, 0.05)",
        }}
      >
        <h3 style={{ margin: 0, marginBottom: 6, fontSize: "18px", fontWeight: 600 }}>
          Interests
        </h3>

        {showInterests.length ? (
          <div
            style={{
              display: "flex",
              flexDirection: "row",
              flexWrap: "wrap",
            }}
          >
            {showInterests.map((interest) => {
              return (
                <span
                  style={{
                    marginRight: "12px",
                    marginBottom: "0px",
                    width: "fit-content",
                    backgroundColor: "#F9F9FB",
                    borderRadius: 15,
                    marginTop: 6.5,
                    padding: "4px 14px",
                    fontWeight: 400,
                    display: 'inline-flex',
                    flexDirection: 'row',
                    alignItems: 'center',
                  }}
                  key={interest}
                >
                  {interest}
                  {isEditing &&
                    <button
                      className="pressable"
                      style={{ marginRight: -4, marginLeft: 4, width: 16, height: 16, padding: 0, }}
                      onClick={()=>onDeleteInterestClick(interest)}
                    >
                      <Icon
                        icon='x'
                        size={16}
                      />
                    </button>
                  }

                </span>


              );
            })}
          </div>
        ) : (
          <p>No interests to show</p>
        )}
        {editing ? (
          <NewInterestsRow>
            <CICFInput
              value={inputText}
              onChange={(v) => {
                setInputText(v);
              }}
              style={{
                fontSize: "16px",
                lineHeight: "24px",
                color: "#474554",
                height: 32,
                width: 300,
                marginRight: 10,
                alignSelf: "center",
              }}
              placeholder="Enter interest..."
              enterPressCallback={onInputEnterPress}
            ></CICFInput>
            <CICFButton
              style={{
                height: 32,
              }}
              text='Add'
              onClick={onAddPress}
            />
          </NewInterestsRow>
        ) : null}
      </div>
    </div>
  );
};



const NewInterestsRow = styled.div`
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 32px;
  margin-top: 10px;
`

export default InterestsCard;
