import CICFButton from "components/CICFButton";
import CICFTextArea from "components/CICFTextArea";
import Spacer from "components/Spacer";
import ToggleSwitch from "components/ToggleSwitch";
import ModalBase from "modals/ModalBase";
import { useState } from "react";
import styled from "styled-components";
import { text } from "styleUtilities";
import { contentCopy, useUpdateTouchstoneNoteMutation, useUserProfileQuery } from '@haystackdev/cicf-content-api';
import useModalStore from "state/modals";

export default function EditNoteModal() {
    const modalState = useModalStore((s) => s.editTouchstoneNoteModalState);
    if (!modalState) throw new Error("Add note modal opened with no state.");
    const hide = useModalStore((s) => s.hidePresentingModal)

    const note = modalState.editing;
    const touchstoneId = modalState.editing.associatedWithContentId;


    const [text, setText] = useState<string>(note.text);
    const [makePublic, setMakePublic] = useState<boolean>(note.isPublic);

    const userProfileQuery = useUserProfileQuery();
    const updateNoteMutation = useUpdateTouchstoneNoteMutation()


    function onChangeText(target: HTMLTextAreaElement) {
        setText(target.value);
    }

    function onSavePress() {
       
        updateNoteMutation.mutateAsync({

            text,
            touchstoneId,
            isPublic: makePublic,
            id: note.id
        }).then((response) => {
            // if(response.wasFlagged) {
            //     window.alert(`${contentCopy.noteFlaggedMessage.title}. ${contentCopy.noteFlaggedMessage.message}.`)
            // }
            hide();
        }).catch(() => {
            window.alert("Couldn't add note, check your internet and/or try again.")
        })
    }

    const saveDisabled = !text || !userProfileQuery.data;

    return (
        <ModalBase
            title='Edit Note'
        >
            <CICFTextArea
                value={text}
                onChange={(e) => onChangeText(e.target as HTMLTextAreaElement)}
            />
            <Spacer
                v={30}
            />
            <MakePublicRow>
                <MakePublicLabel>
                    Make this note public
                </MakePublicLabel>
                <ToggleSwitch
                    value={makePublic}
                    onChange={setMakePublic}
                />
            </MakePublicRow>
            <Spacer
                v={31}
            />
            <ButtonsRow>
                <Button
                    text="Save Changes"
                    onClick={onSavePress}
                    variant={saveDisabled ? 'disabled' : 'primary'}
                    disabled={saveDisabled}
                    loading={updateNoteMutation.isLoading}
                />
            </ButtonsRow>

        </ModalBase>
    )
}

const MakePublicRow = styled.div`
    display: flex;
    align-self: stretch;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
`

const MakePublicLabel = styled.span`
    ${text(400, 16, 19, 'card-title')}
`

const Button = styled(CICFButton)`
 
`

const ButtonsRow = styled.div`
    display: flex;
    align-self: stretch;
    justify-content: center;
    flex-direction: row;
`
