import { Event } from "@haystackdev/cicf-content-api";
import Spacer from "components/Spacer";
import EventCard from "components/EventCard";
import EventsNoEvents from "features/Events/EventsPage/EventsNoEvents";
import EventsSectionHeader from "features/Events/EventsPage/EventsSectionHeader";
import { useState } from "react";
import styled from "styled-components";
import { flexGridContainer } from "styleUtilities";
import EventSectionMode from "types/EventSectionMode";

export default function EventsSection({
    title,
    events,
}: {
    title: string,
    events: Event[],
}) {
    const [mode, setMode] = useState<EventSectionMode>('card');
    return (
        <>
            <EventsSectionHeader
                titleText={title}
                mode={mode}
                onModeToggle={setMode}
            />
            <Spacer
                v={20}
            />
            {!events.length &&
                <>
                    <EventsNoEvents />
                    <Spacer v={28} />
                </>
            }
            <CardContainer>
                {events.map((e) => (
                    <EventCard
                        event={e}
                        mode={mode}
                        key={e.id}
                    />
                ))}
            </CardContainer>

        </>
    )
}

const CardContainer = styled.div`
    ${flexGridContainer({
    nColumns: 2,
    margin: 24
})}
`