import { useUserProfileQuery, useUpdateProfileImageMutation } from "@haystackdev/cicf-content-api";
import CICFButton from "components/CICFButton";
import QuerySuspended from "components/QuerySuspended";
import Spacer from "components/Spacer";
import UploadImageDragAndDrop from "components/UploadImageDragAndDrop";
import { nextOnboardingPath } from "features/Onboarding/onboardingHelpers";
import OnboardingPage from "features/Onboarding/Pages/OnboardingPage";
import React, { useState } from "react";
import { useQueryClient } from "react-query";
import { useNavigate } from "react-router-dom";
import styled from "styled-components";
import { text } from "styleUtilities";

export default function OnboardingProfilePhoto() {
    const userProfileQuery = useUserProfileQuery();
    const profileQuery = userProfileQuery;
    const navigate = useNavigate();
    const updateProfileImageMutation = useUpdateProfileImageMutation();
    const [imageUri, setImageUri] = useState<string | null>(null);
    const [imageBase64, setImageBase64] = useState<string | null>(null);
    const [imageFile, setImageFile] = useState<File | null>(null);
    const [imageType, setImageType] = useState<'image/jpeg' | 'image/png' | null>(null);
    function onUploadClick() {
        if (!imageBase64) alert("Try pressing upload again.")
        updateProfileImageMutation.mutateAsync({
            imageBase64: imageBase64!.split(',')[1],
            contentType: imageType!
        }).then(() => {
            navigate(nextOnboardingPath(true))
        })
    }

    function onImageChange(file: File, uri: string) {
        setImageUri(uri);
        setImageFile(file);
    }

    return (

        <QuerySuspended
            queries={[userProfileQuery]}
        >
            {profileQuery.data &&
                <OnboardingPage
                    title={`Hi ${profileQuery.data.firstName}, pick a profile picture`}
                >
                    <UploadImageDragAndDrop
                        imageUrl={imageUri}
                        onFileChange={onImageChange}
                        onBase64Change={(base64, fileType)=>{
                            setImageBase64(base64);
                            setImageType(fileType);
                        }}
                    />
                    {imageFile &&
                        <>
                            <UploadAgainText>
                                To replace the photo, drag and drop a new image or click on the photo.
                            </UploadAgainText>
                            <Spacer
                                v={16}
                            />
                        </>
                    }

                    <CICFButton
                        text="Upload"
                        onClick={onUploadClick}
                        disabled={!imageFile}
                        variant={!imageFile ? 'disabled' : 'primary'}
                        loading={updateProfileImageMutation.isLoading}
                    />
                </OnboardingPage>
            }
        </QuerySuspended>
    );
};

const UploadAgainText = styled.p`
    ${text(400, 14, 24)}
`

