import { OnboardingChecklist,  } from "@haystackdev/cicf-content-api";
import CardStyledComponentDiv from "components/Card";
import CICFButton from "components/CICFButton";
import CICFTextSpan from "components/CICFTextSpan";
import Spacer from "components/Spacer";
import OnboardingWidgetCheck from "features/Home/Home/OnboardingWidget/OnboardingWidgetCheck";
import OnboardingWidgetProgressBar from "features/Home/Home/OnboardingWidget/OnboardingWidgetProgressBar";
import { nextOnboardingPathForChecklist } from "features/Onboarding/onboardingHelpers";
import { useNavigate } from "react-router-dom";
import styled from "styled-components";
import { textColor } from "theme";


export default function OnboardingWidget({
    checklist,
}: {
    checklist: OnboardingChecklist
}) {
    const nComplete = checklist.items.filter((e) => e.isCompleted).length;

    const nav = useNavigate()

    function onButtonPress() {
        if(!checklist.nextItem) return;
        nav(nextOnboardingPathForChecklist(checklist));
    }

    return (
        <CardStyledComponentDiv
            variant="secondary"
        >
            <OnboardingWidgetProgressBar
                nComplete={nComplete}
                total={checklist.items.length}
            />
            <Spacer
                v={16}
            />
            <CICFTextSpan
                sizing={[400, 14, 24]}
                color={textColor['secondary-card-text']}
            >
                {`${nComplete} of ${checklist.items.length} complete`}
            </CICFTextSpan>
            <Spacer
                v={16}
            />
            <CheckboxContainer>
                {checklist.items.map((e) => (
                    <OnboardingWidgetCheck
                        checked={e.isCompleted}
                        text={e.displayText}
                        key={e.displayText}
                    />
                ))}
            </CheckboxContainer>
            <Spacer
                v={32}
            />
            {checklist.nextItem &&
                <CICFButton
                    text={checklist.nextItem.actionDisplayText}
                    variant='secondary'
                    onClick={onButtonPress}
                />
            }

        </CardStyledComponentDiv>
    )
}

const CheckboxContainer = styled.div`
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
`



