import { HTMLAttributes, HTMLProps } from 'react';
import styled from 'styled-components';
import { backgroundColor, dimensions, borderColors } from '../../theme';
import {boxShadows} from 'theme';
import { Link } from 'react-router-dom';
import CardLockedOverlay from 'components/CardLockedOverlay';

type CardVariant = 'primary' | 'secondary';

export interface CardProps extends HTMLAttributes<HTMLDivElement> {
    variant: CardVariant,
    link?: string,
    hideBorder?: boolean;
    locked?: boolean;
    lockedMessage?: string;
}

export default function Card({
    link,
    children,
    locked,
    lockedMessage,
    ...rest
} : CardProps) {
    const C = link && !locked?CardStyledComponentA:CardStyledComponentDiv as any;
    return (
        <C
            to={link?link:undefined}
            {...rest}
        >
            {children}
            {locked &&
                <CardLockedOverlay 
                    text={lockedMessage}
                />
            }
        </C>
    )
}

const CardStyledComponentDiv = styled.div<CardProps>`
    background-color: ${props=>backgroundColor[props.variant=='primary'?'primary-card':'secondary-card']};
    border-style: ${props=>props.variant=='primary'?'solid':'none'};
    border-radius: ${dimensions.cardBorderRadius}px;
    border-width: ${({variant, hideBorder}) => variant === 'primary' && !hideBorder ? dimensions.primaryCardBorderWidth : 0}px;
    border-color: ${borderColors['primary-card']};
    align-self: stretch;
    display: flex;
    padding: 20px;
    overflow: hidden;
    position: relative;
    flex-direction: column;
    box-shadow: ${boxShadows.card};
`

const CardStyledComponentA = styled(Link)<CardProps>`
    background-color: ${props=>backgroundColor[props.variant=='primary'?'primary-card':'secondary-card']};
    border-style: ${props=>props.variant=='primary'?'solid':'none'};
    border-radius: ${dimensions.cardBorderRadius}px;
    border-width: ${props=>props.variant=='primary' && !props.hideBorder?dimensions.primaryCardBorderWidth:0}px;
    border-color: ${borderColors['primary-card']};
    align-self: stretch;
    display: flex;
    padding: 20px;
    overflow: hidden;
    position: relative;
    flex-direction: column;
    box-shadow: ${boxShadows.card};
`

export const CardCentered = styled(Card)<CardProps>`
    align-self: center;
    width: 560px;
    align-items: center;
    display: flex;
    padding: 40px;
`