import TestPasswordResult, { PasswordStrength } from "@haystackdev/cicf-content-api/lib/types/TestPasswordResult";
import Spacer from "components/Spacer";


const theme = {
  feedItemBackgroundColor: "white",
  activityIndicatorColor: "red",
  colorPrimary: "orange",
  yellow: {
    light: "#FFF8D4",
    dark: "#FEDB2A",
  },
  offBlack: "#211D3A",
  backgroundGrey: "#FAFAFA",
  lightText: "#474554",
  mediumGrey: "#B9B9B9",
  fadedText: "#B9B9B9", // same as medium grey
  darkGrey: "#474554",
  lightGrey: "#E6E8EC",
  passwordStrength: {
    blue: "#69A8F0",
    red: "#FF7C86",
    green: "#15D896",
  },
  baseGrey: "#999999",
  completedGreen: "#6BB21D",
  offWhite: "#F9F9FB",
};

const SPACING = 5;

function colorForStrength(strength: PasswordStrength | undefined) {
  switch (strength) {

    case 'good':
      return theme.passwordStrength.blue;
    case 'great':
      return theme.passwordStrength.green;
    case 'bad':
      return theme.passwordStrength.red;
  }
  return theme.fadedText
}

function IndicatorBar({
  color,
  isActive,
  style = {
    height: '2px',
    width: 'calc((100% - 10px) / 3)',
    backgroundColor: isActive ? color : theme.fadedText, // changed from theme.fadedText, to ""
  },
}: {
  color: string;
  isActive: boolean;
  style?: React.CSSProperties; // changed from any
}) {
  return <div style={style} />;
}

export default function PasswordStrengthIndicator({
  testPasswordResult,
  containerStyle = { width: "100%", flexDirection: "row", display: 'flex', alignSelf: 'stretch', },
}: {
  testPasswordResult: TestPasswordResult | undefined;
  containerStyle?: React.CSSProperties; // changed from any
}) {

  const color = colorForStrength(testPasswordResult?.strength);

  return (
    <div style={{
      width: '100%',
    }}>
      <div style={containerStyle}>
        <IndicatorBar
          // style={{ marginRight: 4 }}
          isActive={!!testPasswordResult}
          color={color}
        />
        <Spacer h={SPACING} />
        <IndicatorBar
          // style={{ marginRight: 4 }}
          isActive={!!testPasswordResult && (testPasswordResult.strength == 'good' || testPasswordResult.strength == 'great')}
          color={color}
        />
        <Spacer h={SPACING} />
        <IndicatorBar
          isActive={!!testPasswordResult && testPasswordResult.strength == 'great'}
          color={color}
        />
      </div>

      <div
        style={{
          color,
          marginTop: 6,
          fontSize: 12,
          minHeight: 20,
        }}
      >
        {
          (() => {
            if (!testPasswordResult) return '';
            switch (testPasswordResult.strength) {
              case 'bad':
                return testPasswordResult.feedback;
              case 'good':
                return testPasswordResult.feedback;
              case 'great':
                return 'Great password'
              default:
                return ''

            }
          })()
        }
      </div>
    </div>
  );
}
