import { useCurriculumInfo, useCurriculumQuery } from "@haystackdev/cicf-content-api";
import Page from "components/Page";
import QuerySuspended from "components/QuerySuspended";
import Spacer from "components/Spacer";
import LearningPathwayDetailContent from "features/Learning/LearningPathwayDetailPage/LearningPathwayDetailContent";
import LearningPathwayDetailHeader from "features/Learning/LearningPathwayDetailPage/LearningPathwayDetailHeader";
import { useMemo } from "react";
import { useParams } from "react-router-dom";
import LearningPathwayDetailUrlParameters from "types/UrlParameters/LearningPathwayDetailUrlParameters";


export default function LearningPathwayDetailPage() {
    const urlParams = useParams<LearningPathwayDetailUrlParameters>();
    const curriculumQuery = useCurriculumQuery();
    const curriculumInfo = useCurriculumInfo();
    const pathway = useMemo(()=>{
        if(!curriculumQuery.data) return;
        const p = curriculumQuery.data.curriculum.pathways.find((e)=>e.id==urlParams.pathwayId);
        return p;
    }, [curriculumQuery.data])
    return (
        <Page>
            <QuerySuspended
                queries={[curriculumQuery]}
            >
                {curriculumQuery.isFetched &&
                    (pathway ? // Make sure pathway is found in data.
                        <LearningPathwayDetailContent 
                            pathway={pathway}
                            lockedItemIds={curriculumInfo.lockedItemIdsSet}
                        />
                        :
                        // Here pathway wasn't found, return something like "Pathway not found."
                        <h1>
                            Pathway not found.
                        </h1>
                    )
                }
            </QuerySuspended>
        </Page>
    )
}
