import { borderColors, textColor, boxShadows } from "theme";

export function shadow(shadow: keyof typeof boxShadows) {
    return `
        box-shadow: ${boxShadows[shadow]};
    `
}

export function maxLines(n: number) {
    return `
    line-clamp: ${n}; 
    -webkit-line-clamp: ${n};
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;`
}

export function flexGridContainer({
    nColumns,
    margin,
    marginBottom,
}: {
    nColumns: number,
    margin: number,
    marginBottom?: number,
}) {
    return `
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-self: stretch;

    & > * {
        width: calc((100% - ${(nColumns - 1) * margin}px) / ${nColumns});
        margin-right: ${margin}px;
        margin-bottom: ${typeof marginBottom !== 'undefined'?marginBottom:margin}px;
    }

    & > *:nth-child(${nColumns}n + ${nColumns}) {
        margin-right: 0px;
    }
    `
}

export function border({
    radius=1000,
    color,
    width=1,
} : {
    radius?:number,
    color: keyof typeof borderColors,
    width?: number,
}) {
    return `
        border-style: solid;
        border-width: ${width}px;
        border-radius: ${radius}px;
        border-color: ${borderColors[color]};
    `
}

export function text(weight: number, size: number, lineHeight: number, color?: keyof typeof textColor) {
    return `
        font-size: ${size}px;
        font-weight: ${weight};
        line-height: ${lineHeight}px;
        ${color?`color: ${textColor[color]};`:''}
    `
}