import Icon from "components/Icon";
import { dayAbbreviations, days, getMonthNameFromNumber } from "features/Events/EventsPage/Calendar/calendarUtils";
import { useEffect } from "react";
import styled from "styled-components";
import { text } from "styleUtilities";
import Month from "types/Month";

const DAYS_PADDING_HORIZONTAL = 18;

export default function EventCalendarHeader({
    onLeftPress,
    onRightPress,
    month,
}: {
    onLeftPress: () => void,
    onRightPress: () => void,
    month: Month
}) {

    return (
        <Container>
            <MonthContainer>
                <ChangeMonthButton
                    onClick={onLeftPress}
                >
                    <Icon
                        size={24}
                        icon='chevron-left'
                    />
                </ChangeMonthButton>
                <Text>
                    {getMonthNameFromNumber(month.month) + ', ' + month.year}
                </Text>
                <ChangeMonthButton
                    onClick={onRightPress}
                >
                    <Icon
                        size={24}
                        icon='chevron-right'
                    />
                </ChangeMonthButton>
            </MonthContainer>
            <DaysOfWeekContainer>
                {days.map((e)=>{
                    return (
                        <DayOfWeek
                            key={e}
                        >
                            {dayAbbreviations[e]}
                        </DayOfWeek>
                    )
                })}
            </DaysOfWeekContainer>
        </Container>
    )
}

const Container = styled.div`
    display: flex;
    flex-direction: column;
    
`

const DaysOfWeekContainer = styled.div`
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 0px ${DAYS_PADDING_HORIZONTAL}px;
    margin-top: 6px;
    border: 2px none #E6E8EC;
    border-bottom-style: solid;
`

const DayOfWeek = styled.span`
    ${text(400, 14, 24, 'card-title')}
    width: 32px;
    justify-content: center;
    align-items: center
`

const MonthContainer = styled.div`
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    padding: 12px 14px 0px 14px;
`

const Text = styled.h3`
    ${text(500, 20, 24, 'card-title')}
`

const ChangeMonthButton = styled.button`
    width: 24px;
    height: 24px;
    outline: none;
    border: 0;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    background-color: inherit;
`
