import React, { useState } from "react";
import chevronUp from 'assets/icons/chevron-down.svg';
import { backgroundColor, borderColors } from "theme";
import styled from "styled-components";
import { text } from "styleUtilities";
import Card from "components/Card";



export default function DropdownSelect({
    choices,
    selected,
    placeholder,
    onChange,
    style,
    dropdownStyle,
    choicesSpacing=0,
    variant='default',
    containerStyle,
}: {
    choices: {
        value: string,
        label: string,
    }[],
    onChange: (v: string | null) => void,
    selected: string | null,
    placeholder: string,
    style?: React.CSSProperties,
    containerStyle?: React.CSSProperties
    choicesSpacing?: number,
    dropdownStyle?: React.CSSProperties
    variant?: 'default' | 'outline'
}) {
    const [open, setOpen] = useState<boolean>(false);
    console.log("Selected, all")
    console.log(selected, )
    console.log(choices)
    const labelForValue = selected === null ? placeholder : choices.find((e) => e.value == selected)!.label;

    return (
        <div style={{position: 'relative', display: 'flex', flexDirection: 'column', alignSelf: 'stretch', ...containerStyle}}>
            <div
                style={{
                    width: '100%',
                    display: 'inline-flex',
                    flexDirection: 'row',
                    justifyContent: 'space-between',
                    alignItems: 'center',
                    height: 48,
                    borderRadius: 8,
                    backgroundColor: '#F9F9FB',
                    position: 'relative',
                    alignSelf: 'stretch',
                    paddingLeft: 16,
                    
                    paddingRight: 12,
                    ...(variant=='outline'?{
                        backgroundColor: 'white', borderWidth: 1, borderStyle: 'solid', borderColor: borderColors['text-input']
                    } : {}),
                    ...style
                }}
                onClick={() => {
                    setOpen((old) => !old)
                }}
                
            >
                <CICFText
                    style={{
                        color: selected === null ? '#474554' : '#211D3A',
                        userSelect: 'none',
                    }}
                >
                    {labelForValue}
                </CICFText>
                <img
                    style={{ width: 24, height: 24, transform: open ? 'rotate(180deg)' : undefined }}
                    src={chevronUp}
                />
            </div>
            {
                open && 
                <Menu
                    style={{
                        width: '100%',
                        display: 'inline-flex',
                        flexDirection: 'column',
                        position: 'absolute',
                        overflow: 'hidden',
                        top: 48+choicesSpacing,
                        zIndex: 10000000,
                        
                    }}
                    variant="primary"
                    hideBorder={true}
                >
                    {
                        choices.map((o)=>{
                            return (
                                <Choice style={{
                                    width: '100%',
                                    height: 45,
                                    flexDirection: 'row',
                                    display: 'flex',
                                    alignItems: 'center',
                                    paddingLeft: 16,
                                }}
                                    className='select-menu-item'
                                    onClick={()=>{
                                        onChange(o.value);
                                        setOpen(false);
                                    }}
                                    key={o.value}
                                >
                                    <CICFText
                                        style={{
                                            userSelect: 'none',
                                        }}
                                    >
                                        {o.label}
                                    </CICFText>
                                </Choice>
                            )
                        })
                    }
                </Menu>
            }
        </div>
    )
}

const CICFText = styled.label`
    ${text(400, 14, 24)}
`

const Menu = styled(Card)`
    padding: 0;
`

const Choice = styled.button`
    &:hover {
        background-color: ${backgroundColor['dropdown-item-hover']};
    }
`