import { FEATURE_FLAG_CHANGE_PASSWORD } from "@haystackdev/cicf-content-api";
import SettingsAddress from "features/Settings/SettingsAddress";
import SettingsDeleteAccount from "features/Settings/SettingsDeleteAccount";
import SettingsEmail from "features/Settings/SettingsEmail";
import SettingsPassword from "features/Settings/SettingsPassword";
import React from "react";
import { Navigate, Route, Routes } from "react-router-dom";
import { SETTINGS_PATH } from "topLevelRoutes";
import RouteMap from "types/RouteMap";

export const SETTINGS_EMAIL_ADDRESS = 'email';
export const SETTINGS_PASSWORD = 'password';
export const SETTINGS_LOCATION = 'location';
export const SETTINGS_DELETE_ACCOUNT = 'delete-account';

export const settingsRoutesMap: RouteMap = {
    ...(FEATURE_FLAG_CHANGE_PASSWORD?{
        [SETTINGS_PASSWORD]: SettingsPassword,
    }:{}),
    [SETTINGS_LOCATION]: SettingsAddress,
    [SETTINGS_DELETE_ACCOUNT]: SettingsDeleteAccount,
}

export default function SettingsRoutes() {
    return (
        <Routes>
            {Object.entries(settingsRoutesMap).map(([routeName, Component]) => {
                return (
                    <Route
                        key={routeName}
                        element={
                            <Component/>
                        }
                        path={routeName}
                    />
                )
            })}
            <Route 
                path={'/'}
                element={<Navigate to={`/${SETTINGS_PATH}/${Object.keys(settingsRoutesMap)[0]}`}/>}
            />
        </Routes>
    )
}