import { Event, useSavedEventIdsQuery, useSaveEventMutation } from "@haystackdev/cicf-content-api";
import { EventsQueryData } from "@haystackdev/cicf-content-api/lib/queries/query/eventsQuery";
import { SaveEventRequest } from "@haystackdev/cicf-content-api/lib/requests/saveEvent";
import CICFButton from "components/CICFButton";
import EventLocationIconWithText from "components/EventLocationIconWithText";
import EventPriceIconWithText from "components/EventPriceIconWithText";
import Spacer from "components/Spacer";
import { openLinkInNewTab } from "helpers";
import React from "react";
import { Link } from "react-router-dom";
import styled from "styled-components";
import { text } from "styleUtilities";
import { eventImageAspectRatio, touchstoneImageAspectRatio } from "theme";

export default function EventDetailContent({
    event,
    isSaved,
}: {
    event: Event,
    isSaved: boolean,
}) {
    const saveEventMutation = useSaveEventMutation();


    function onSaveEventClick() {
        saveEventMutation.mutateAsync({
            eventId: event.id,
            type: isSaved ? 'unsave' : 'save',
        })
    }

    function onGoToEventSiteClick() {
        openLinkInNewTab(event.eventUrl)
    }
    return (
        <>
            <DateTimeText>
                {(new Date(event.timestamp)).toLocaleString('en-us', {
                    'weekday': 'short',
                    'month': 'long',
                    day: 'numeric',
                    'hour': 'numeric',
                    minute: 'numeric'
                })}
            </DateTimeText>
            <Header>
                {event.title}
            </Header>
            <Image
                src={event.imageUrl}
            />
            <SubHeaderContainer>
                <SubHeaderLeft>
                    <EventLocationIconWithText
                        event={event}
                    />
                    <Spacer h={13} />
                    <EventPriceIconWithText
                        event={event}
                    />
                </SubHeaderLeft>
                <CICFButton
                    variant="text-only"
                    text={isSaved ? 'Unsave Event' : 'Save Event'}
                    loading={saveEventMutation.isLoading}
                    onClick={onSaveEventClick}
                />
            </SubHeaderContainer>
            <Spacer
                v={26}
            />
            <DescriptionLabel>
                Description
            </DescriptionLabel>
            <Description>
                {event.description}
            </Description>
            {event.accessUrl &&
                <>
                    <DescriptionLabel>
                        Event Access
                    </DescriptionLabel>
                    <a
                        href={event.accessUrl}
                        
                    >
                        <Description>
                            {event.accessUrl}
                        </Description>
                    </a>

                </>
            }
            {event.location &&
                <>
                    <DescriptionLabel>
                        Event Location
                    </DescriptionLabel>
                    <Description>
                        {event.location}
                    </Description>
                </>

            }

            <Spacer
                v={52}
            />
            <CICFButton
                text='Go to Event Site'
                variant="primary"
                onClick={onGoToEventSiteClick}
            />
        </>
    )
}


const Header = styled.h1`
    ${text(600, 32, 44, 'page-header')}
`

const DateTimeText = styled.span`
    ${text(400, 14, 24, 'event-detail-time')}
    margin-top: 60px;
`

const SubHeaderContainer = styled.div`
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-top: 26px;
`

const SubHeaderLeft = styled.span`
    flex-direction: row;
    align-item: center;
`

const Image = styled.img`
    width: 100%;
    aspect-ratio: ${eventImageAspectRatio};
    object-fit: cover;
    border-radius: 7px;
    margin-top: 30px;
`

const DescriptionLabel = styled.h2`
    ${text(400, 14, 24, 'page-content')}
    margin-top: 20px;
`

const Description = styled.p`
    ${text(400, 16, 24, 'page-content')}
    margin-top: 12px;
`
