import { contentCopy, useGendersQuery, useUpdateUserProfileMutation, useUserProfileQuery } from "@haystackdev/cicf-content-api";
import CICFButton from "components/CICFButton";
import DropdownSelect from "components/DropdownSelect";
import QuerySuspended from "components/QuerySuspended";
import Spacer from "components/Spacer";
import { ONBOARDING_BUTTON_TEXT } from "constants/onboardingText";
import { nextOnboardingPath } from "features/Onboarding/onboardingHelpers";
import OnboardingPage from "features/Onboarding/Pages/OnboardingPage";
import React, { useState } from "react";
import { useNavigate } from "react-router-dom";



export default function OnboardingGenderIdentity() {
    const [selected, setSelected] = useState<string | null>(null);

    const updateProfileMutation = useUpdateUserProfileMutation();
    const userProfileQuery = useUserProfileQuery();
    const gendersQuery = useGendersQuery();

    const nav = useNavigate();

    function onSaveClick() {
        if (selected === null) return;
        updateProfileMutation.mutateAsync({
            ...userProfileQuery.data!,
            demographics: {
                ...userProfileQuery.data!.demographics,
                gender: selected,
            }
        }).then(() => {
            nav(nextOnboardingPath(true))
        })
    }

    const canSave = selected !== null;

    return (
        <OnboardingPage
            title="Gender Identity"
            subtitle={contentCopy.onboarding.selectGender.message}
        >
            <QuerySuspended
                queries={[gendersQuery]}
            >
                {gendersQuery.data &&
                    <>
                        <Spacer
                            v={42}
                        />
                        <DropdownSelect
                            variant="outline"
                            choices={gendersQuery.data.map((e) => {
                                return {
                                    value: e,
                                    label: e,
                                }
                            })}
                            selected={selected}
                            onChange={setSelected}
                            placeholder='Choose a gender'
                        />
                        <Spacer v={40} />
                        <CICFButton
                            text={ONBOARDING_BUTTON_TEXT}
                            onClick={onSaveClick}
                            loading={updateProfileMutation.isLoading}
                            disabled={!canSave || !userProfileQuery.isFetched}
                            variant={canSave ? 'primary' : 'disabled'}
                        />
                    </>
                }

            </QuerySuspended>

        </OnboardingPage>
    )
}