import { UserProfile, homescreenStateFromProfile, useHomescreenState } from "@haystackdev/cicf-content-api";
import { UseQueryResult } from 'react-query';
import { useMemo } from "react";
import OnboardingWidget from "features/Home/Home/OnboardingWidget";
import SignPledgeCard from "features/Home/Home/SignPledgeCard";
import HomeDataCard from "features/Home/Home/HomeDataCard";
import Spacer from "components/Spacer";



export default function HomeScreenTopSection({
    userProfile,
    savedEventsCount,
    touchstonesCompleteCount,
}: {
    userProfile: UserProfile,
    savedEventsCount: number,
    touchstonesCompleteCount: number,
}) {
    const homescreenState = useHomescreenState();
    return (
        <>
            {homescreenState?.topWidget == 'onboarding' &&
                <OnboardingWidget
                    checklist={homescreenState.onboardingChecklist!}
                />
            }
            {homescreenState?.topWidget == 'sign-pledge' &&
                <SignPledgeCard />
            }
            {homescreenState.topWidget!=='none' &&
                <Spacer 
                    v={16}
                />

            }
            <HomeDataCard
                savedEventsCount={savedEventsCount}
                touchstonesCompleteCount={touchstonesCompleteCount}
            />


        </>
    )
}