import { usePasswordResetEmailMutation } from "@haystackdev/cicf-content-api";
import AuthCard from "components/AuthCard";
import CICFButton from "components/CICFButton";
import CICFInput from "components/CICFInput";
import Page from "components/Page";
import Spacer from "components/Spacer";
import { EMAIL_PLACEHOLDER } from "constants/inputContants";
import { useState } from "react";
import { useNavigate } from "react-router-dom";
import { PASSWORD_RESET_EMAIL_BASE_PATH } from "topLevelRoutes";


export default function ResetPassword() {
    const [email, setEmail] = useState<string>('');
    const [emailValid, setEmailValid] = useState<boolean>(false);
    const [error, setError] = useState<string>('');

    const requestPasswordResetEmailMutation = usePasswordResetEmailMutation();

    const nav = useNavigate();

    function onRequestPasswordResetClick() {
        requestPasswordResetEmailMutation.mutateAsync({
            email,
            platform: 'desktop'
        }).then((r)=>{
            if(r.success) {
                nav('/' + PASSWORD_RESET_EMAIL_BASE_PATH + '/' + email)
            } else {
                setError('Invalid email.')
            }
        })
    }

    return (
        <Page>
            <AuthCard
                title="Reset Password"
                subtitle="Enter the email associated with your account. We will send you an email with account reset instructions."
            >
                <CICFInput 
                    value={email}
                    setValid={setEmailValid}
                    onChange={setEmail}
                    type='email'
                    validationType="email"
                    placeholder={EMAIL_PLACEHOLDER}
                    icon='mail-dark'
                    errorMessage={error}
                />
                <Spacer v={60}/>
                <CICFButton 
                    text="Request Reset Link"
                    disabled={!emailValid}
                    variant={emailValid?'primary':'disabled'}
                    loading={requestPasswordResetEmailMutation.isLoading}
                    onClick={onRequestPasswordResetClick}
                />
            </AuthCard>
        </Page>
    )
}