import { Curriculum, UserProfile } from "@haystackdev/cicf-content-api";
import CardParagraph from "components/CardParagraph";
import { timestampToDateString } from "helpers";
import HomeContentCard from "features/Home/Home/HomeContentCard";
import canTakeAssessment from "ux/canTakeAssessment";
import { useNavigate } from "react-router-dom";
import { ASSESSMENT_PATH } from "topLevelRoutes";
import AssessmentGraphic from "components/Graphics/AssessmentGraphic";
import styled from "styled-components";




export default function HomeContentCardAssessment({
    userProfile,
    curriculum,
}: {
    userProfile: UserProfile,
    curriculum: Curriculum,
}) {
    const canTakeAssessmentResult = canTakeAssessment(userProfile, curriculum);
    const navigate = useNavigate();

    function onTakeAssessmentPress() {
        console.log("Take assessment press. ")
        navigate('/' + ASSESSMENT_PATH)
    }

    return (
        <HomeContentCard
            headerText="Assessments"
            buttonText={buttonText(userProfile, curriculum)}
            buttonDisabled={!canTakeAssessmentResult.canTakeAssessment}
            onButtonPress={onTakeAssessmentPress}
        >
            <AssessmentGraphicContainer>
                <AssessmentGraphic />
            </AssessmentGraphicContainer>

            <CardParagraph>
                {paragraphText(userProfile, curriculum)}
            </CardParagraph>
            {!canTakeAssessmentResult.canTakeAssessment &&
                <CardParagraph>
                    {canTakeAssessmentResult.whyNot}
                </CardParagraph>
            }
        </HomeContentCard>
    )
}

function paragraphText(profile: UserProfile, curriculum: Curriculum): string {
    if (profile.previousAssessmentResults.length) return `You recently took the assessment on\n${timestampToDateString(profile.previousAssessmentResults[profile.previousAssessmentResults.length - 1].finishedTimestamp)}`;
    return "Take the assessment to measure your continued progress."
}

function buttonText(profile: UserProfile, curriculum: Curriculum) {
    if (profile.previousAssessmentResults.length) return "Retake Assessment";
    return "Take Assessment";
}

const AssessmentGraphicContainer = styled.span`
    align-self: center;
    margin-top: 8px;
    margin-bottom: 16px;
`