import React from "react";
import styled from 'styled-components';
import CardStyledComponentDiv, {CardProps} from 'components/Card';
import CICFButton from 'components/CICFButton';
import CardHeader from "components/CardHeader";
import Spacer from "components/Spacer";

interface Props extends Omit<CardProps, 'variant'> {
    headerText: string,
    buttonText: string,
    buttonDisabled?: boolean,
    onButtonPress: ()=>void,
}

export default function HomeContentCard({
    headerText,
    buttonText,
    buttonDisabled,
    children,
    onButtonPress,
    ...props
}: Props) {
    return (
        <CardStyledComponentDiv
            variant="primary"
            {...props}
        >
            <CardHeader>
                {headerText}
            </CardHeader>
            <Spacer v={16}/>
            {children}
            <ContentCardButton
                text={buttonText}
                onClick={onButtonPress}
            />
        </CardStyledComponentDiv>
    )
}



export const ContentCardButton = styled(CICFButton)`
    margin-top: 16px;
`