import EventsPage from "features/Events/EventsPage";
import LearningPathwaysPage from "features/Learning/LearningPathways";
import LearningRoutes from "features/Learning/LearningRoutes";
import CreateAccount from "./features/Authorization/CreateAccount";
import Home from "./features/Home/Home";
import Login from "./features/Authorization/Login";
import EventsRoutes from "features/Events/EventsRoutes";
import TakeAssessmentPage from "features/Assessment/TakeAssessmentPage";
import AssessmentRoutes from "features/Assessment/AssessmentRoutes";
import Pledge from "features/Pledge";
import OnboardingRoutes from "features/Onboarding/OnboardingRoutes";
import SettingsRoutes from "features/Settings/SettingsRoutes";
import SettingsContainer from "features/Settings/SettingsContainer";
import ChangeEmailRoutes from "features/ChangeEmail/ChangeEmailRoutes";
import UserProfile from "features/UserProfile/UserProfile";
import CheckEmail from "features/Authorization/CheckEmail";
import EmailConfirmed from "features/Authorization/EmailConfirmed";
import ResetPasswordNewPassword from "features/Authorization/ResetPasswordNewPassword";
import PasswordHasBeenReset from "features/Authorization/PasswordHasBeenReset";
import PasswordResetEmailSent from "features/Authorization/PasswordResetEmailSent";
import ResetPassword from "features/Authorization/ResetPassword";


type RouteMap = {[routeName: string]: ()=>React.ReactElement};

export const LOGIN_PATH = 'login';
export const CREATE_ACCOUNT_PATH = 'create-account';
export const LEARNING_PATH = 'learning';
export const HOME_PATH = 'home';
export const EVENTS_PATH = 'events';
export const ASSESSMENT_PATH = 'assessment';
export const PLEDGE_BASE_PATH = 'pledge';
export const PLEDGE_FULL_PATH = PLEDGE_BASE_PATH + '/:pledgeId';
export const ONBOARDING_PATH = 'onboarding';
export const SETTINGS_PATH = 'settings';
export const CHANGE_EMAIL_PATH = 'change-email';
export const PROFILE_PATH = 'profile';
export const CONFIRM_EMAIL_BASE_PATH = 'confirm-email'
export const CONFIRM_EMAIL_PATH = CONFIRM_EMAIL_BASE_PATH + '/:email';
export const EMAIL_CONFIRMED_BASE_PATH = 'email-confirmed';
export const EMAIL_CONFIRMED_PATH = EMAIL_CONFIRMED_BASE_PATH + '/:token';
export const CHANGE_PASSWORD_BASE_PATH = 'change-password';
export const CHANGE_PASSWORD_PATH = CHANGE_PASSWORD_BASE_PATH + '/:token';
export const PASSWORD_CHANGED_PATH = 'password-changed';
export const PASSWORD_RESET_EMAIL_BASE_PATH = 'password-reset-email-sent';
export const PASSWORD_RESET_EMAIL_PATH = PASSWORD_RESET_EMAIL_BASE_PATH + '/:email';
export const PASSWORD_RESET_PATH = 'forgot-password';

export const topLevelAuthRoutes: RouteMap = {
    [LOGIN_PATH]: ()=><Login />,
    [CREATE_ACCOUNT_PATH]: ()=><CreateAccount />,
    [CHANGE_PASSWORD_PATH]: ResetPasswordNewPassword,
    [PASSWORD_CHANGED_PATH]: PasswordHasBeenReset,
    [PASSWORD_RESET_EMAIL_PATH]: PasswordResetEmailSent,
    [PASSWORD_RESET_PATH]: ResetPassword,
}   

export const protectedRoutesWithNav: RouteMap = {
    [HOME_PATH]: ()=><Home/>,
    [LEARNING_PATH]: ()=><LearningRoutes />,
    [EVENTS_PATH]: ()=><EventsRoutes />,
}

export const protectedRoutesWithoutNav: RouteMap = {
    [ASSESSMENT_PATH]: AssessmentRoutes,
    [PLEDGE_BASE_PATH]: Pledge,
    [PLEDGE_FULL_PATH]: Pledge,
    [ONBOARDING_PATH]: OnboardingRoutes,
    [PROFILE_PATH]: UserProfile,
    [SETTINGS_PATH]: SettingsContainer
}

export const alwaysRenderedRoutes: RouteMap = {
    [CHANGE_EMAIL_PATH]: ChangeEmailRoutes,
    [EMAIL_CONFIRMED_PATH]: EmailConfirmed,
    [CONFIRM_EMAIL_PATH]: CheckEmail,
}

export function routeDisplayName(route: keyof typeof topLevelAuthRoutes | keyof typeof protectedRoutesWithNav): string {
    switch(route) {
        case LOGIN_PATH:
            return 'Login';
        case CREATE_ACCOUNT_PATH:
            return 'Sign Up';
        case LEARNING_PATH:
            return 'Learning';
        case HOME_PATH:
            return 'Home';
        case EVENTS_PATH:
            return 'Events';
        case SETTINGS_PATH: 
            return 'Settings'
    }
    return 'Route Name'
}