import TouchstoneNote from '@haystackdev/cicf-content-api/lib/types/TouchstoneNote';
import produce from 'immer';
import AddNoteModal from 'modals/AddNoteModal';
import DeleteTouchstoneNoteModal from 'modals/DeleteTouchstoneNoteModal';
import EditNoteModal from 'modals/EditNoteModal';
import SharePledgeModal from 'modals/SharePledgeModal';
import create from 'zustand';

export type Modal = 'add-note' | 'edit-note' | 'delete-note' | 'share-pledge';

interface AddNoteModalState {
    touchstoneId: string,
}

interface EditTouchstoneNoteModalState {
    editing: TouchstoneNote,
}

interface DeleteTouchstoneNoteModalState {
note: TouchstoneNote
}


interface ModalStore {
    presentingModal: Modal | null,
    hidePresentingModal: ()=>void,
    showAddNoteModal: (touchstoneId: string)=>void,
    addNoteModalState: AddNoteModalState | null,

    editTouchstoneNoteModalState: EditTouchstoneNoteModalState | null,
    showEditTouchstoneNote: (touchstone: TouchstoneNote)=>void,

    deleteTouchstoneNoteModalState: DeleteTouchstoneNoteModalState | null,
    showDeleteTouchstoneNote: (note: TouchstoneNote)=>void,

    showSharePledgeModal: ()=>void,

}   

const useModalStore = create<ModalStore>((set)=>({
    presentingModal: null,
    hidePresentingModal: ()=>{
        set(old=>produce(old, draft=>{
            draft.presentingModal = null;
        }))
    },
    showAddNoteModal: (touchstoneId: string,)=>{
        set(old=>produce(old, draft=>{
            draft.presentingModal = 'add-note';
            draft.addNoteModalState = {
                touchstoneId,
            }
        }))
    },
    addNoteModalState: null,


    editTouchstoneNoteModalState: null,
    showEditTouchstoneNote: (touchstone: TouchstoneNote)=>{
        set(old=>produce(old, draft=>{
            draft.editTouchstoneNoteModalState = {
                editing: touchstone
            };
            draft.presentingModal = 'edit-note';
        }))
    },
    
    deleteTouchstoneNoteModalState: null,
    showDeleteTouchstoneNote: (note: TouchstoneNote)=>{
        set(old=>produce(old, draft=>{
            draft.presentingModal = 'delete-note';
            draft.deleteTouchstoneNoteModalState = {
                note,
            }
        }))
    },

    showSharePledgeModal: ()=>{
        set(old=>produce(old, draft=>{
            draft.presentingModal='share-pledge';
        }))
    }
}))

export const modalComponentMap: {[modal in Modal]: ()=>JSX.Element} = {
    'add-note': AddNoteModal,
    'edit-note': EditNoteModal,
    'delete-note': DeleteTouchstoneNoteModal,
    'share-pledge': SharePledgeModal
}

export default useModalStore;