import { useChangePasswordMutation, usePasswordStrength } from "@haystackdev/cicf-content-api";
import CICFButton from "components/CICFButton";
import CICFInput from "components/CICFInput";
import PasswordStrengthIndicator from "components/PasswordMeter";
import Spacer from "components/Spacer";
import SettingsCard from "features/Settings/SettingsCard";
import { useState } from "react";
import styled from "styled-components";
import { text } from "styleUtilities";

export default function SettingsPassword() {
    const changePasswordMutation = useChangePasswordMutation();

    const [newPassword, setNewPassword] = useState<string>('');
    const [confirmNewPassword, setConfirmNewPassword] = useState<string>('');
    const [confirmNewPasswordError, setConfirmNewPasswordError] = useState<string>('');

    const [oldPasswordError, setOldPasswordError] = useState<string>('');
    const [oldPassword, setOldPassword] = useState<string>('');

    const [newPasswordValid, setNewPasswordValid] = useState<boolean>(false);
    const [confirmNewPasswordValid, setConfirmNewPasswordValid] = useState<boolean>(false);
    const [oldPasswordValid, setOldPasswordValid] = useState<boolean>(false);

    const passwordStrength = usePasswordStrength(newPassword);

    function onSaveClick() {
        if(confirmNewPassword !== newPassword) {
            setConfirmNewPasswordError(`Passwords don't match.`);
            setConfirmNewPassword('');
            return;
        }

        changePasswordMutation.mutateAsync({
            newPassword: newPassword,
            oldPassword: oldPassword
        }).then((r)=>{
            if(!r.success) setOldPasswordError(r.failReason!);
            alert('Password updated successfully.')
        })
    }

    const validInputs = newPasswordValid && oldPasswordValid && confirmNewPasswordValid;

    return (
        <SettingsCard title="Password">
            <CICFInput 
                value={oldPassword}
                onChange={setOldPassword}
                placeholder='Old password'
                type={'password'}
                errorMessage={oldPasswordError}
                validationType='password'
                setValid={setOldPasswordValid}
                forceVisibleButton={true}
                icon="lock"
                required={true}
            />
            <NewPasswordText>
                New password
            </NewPasswordText>
            <CICFInput 
                value={newPassword}
                onChange={setNewPassword}
                placeholder='New password'
                type={'password'}
                validationType='password'
                setValid={setNewPasswordValid}
                forceVisibleButton={true}
                icon="lock"
                required={true}
            />
            <Spacer v={16}/>
            <CICFInput 
                value={confirmNewPassword}
                onChange={setConfirmNewPassword}
                placeholder='Confirm new password'
                type={'password'}
                validationType='password'
                errorMessage={confirmNewPasswordError}
                forceVisibleButton={true}
                setValid={setConfirmNewPasswordValid}
                icon="lock"
                required={true}
            />
            <Spacer v={16}/>
            <PasswordStrengthIndicator 
                testPasswordResult={passwordStrength.testResult}
            />
            <Spacer v={16}/>
            <CICFButton 
                text="Save"
                onClick={onSaveClick}
                disabled={!validInputs}
                variant={!validInputs?'disabled':'primary'}
                loading={changePasswordMutation.isLoading}
            />
            
        </SettingsCard>
    )
}

const NewPasswordText = styled.span`
    ${text(500, 14, 24, 'card-paragraph')}
    margin: 16px 0px 8px 0px;
`