import { useCurriculumQuery } from "@haystackdev/cicf-content-api";
import Page from "components/Page";
import QuerySuspended from "components/QuerySuspended";
import dev from "dev";
import LearningPathwaysDisplay from "features/Learning/LearningPathways/LearningPathwaysDisplay";
import LearningPathwaysHeader from "features/Learning/LearningPathways/LearningPathwaysHeader";
import React, { useState } from "react";

export default function LearningPathwaysPage() {
    const curriculumQuery = useCurriculumQuery();
    const [loaded, setLoaded] = useState<boolean>(false);
    return (
        <QuerySuspended
            queries={[curriculumQuery]}
            setLoaded={setLoaded}
        >
            {loaded &&
                <Page>
                    <LearningPathwaysHeader />
                    <LearningPathwaysDisplay 
                        curriculumData={curriculumQuery.data!}
                    />
                </Page>
            }
        </QuerySuspended>
    );
}