import { ImgHTMLAttributes } from "react";
import styled from "styled-components";
import Logo from 'assets/logo-overlay.png';

export default function MVMTShimmer(prop: ImgHTMLAttributes<HTMLImageElement>) {
    return <Image src={Logo} className='shimmer'/>
}

export function MVMTShimmerFixed() {
    return <ImageFixed src={Logo} className='shimmer'/>
}

const Image = styled.img`
    width: 150px;
    height: 150px;
    align-self: center;
`

const ImageFixed = styled(Image)`
    position: fixed;
    top: calc(50vh - 75px);
    left: calc(50vw - 75px);
`