import { CurriculumItemMediaType } from "@haystackdev/cicf-content-api";
import ArticleIcon from 'assets/media-type/article.svg';
import PodcastIcon from 'assets/media-type/podcast.svg';
import VideoIcon from 'assets/media-type/video.svg';
import { HTMLAttributes } from "react";
import styled from "styled-components";
import { backgroundColor, dimensions } from "theme";

type Map = { [k in CurriculumItemMediaType]: string };
const iconMap: Map = {
    'article': ArticleIcon,
    'podcast': PodcastIcon,
    'video': VideoIcon,
    'in-app': ArticleIcon,
}
const DEFAULT_CONTAINER_SIZE = 24;
const DEFAULT_ICON_SIZE = 12;

export default function MediaTypeSquaredIcon({
    type,
    iconContainerSize = DEFAULT_CONTAINER_SIZE,
    iconSize = DEFAULT_ICON_SIZE,
    textProps,
    text,

}: {
    type: CurriculumItemMediaType,
    iconContainerSize?: number,
    iconSize?: number,
    textProps?: HTMLAttributes<HTMLSpanElement>,
    text?: string,
    
}) {
    return (
        <Container>
            <ImageContainer
                size={iconContainerSize}
            >
                <Image
                    size={iconSize}
                    src={iconMap[type]}
                />
            </ImageContainer>
            <Text
                {...textProps}
            >
                {text}
            </Text>
        </Container>
    )
}

interface ContainerProps {
    size?: number,

}

interface ImageProps {
    size?: number,
}


const Container = styled.div`
    display: inline-flex;
    flex-direction: row;
    align-items: center;
    align-self: center;
`

const Text = styled.span`
    font-size: 14px;
    line-height: 16px;
    margin-left: 10px;
`

const ImageContainer = styled.div<ContainerProps>`
    width: ${(p) => p.size ? p.size : DEFAULT_CONTAINER_SIZE}px;
    height: ${(p) => p.size ? p.size : DEFAULT_CONTAINER_SIZE}px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    border-radius: ${dimensions.mediaTypeIconBorderRadius}px;
    background-color: ${backgroundColor['media-type-icon-small']};
`

const Image = styled.img<ImageProps>`
    width: ${(p) => p.size ? p.size : DEFAULT_ICON_SIZE}px;
    height: ${(p) => p.size ? p.size : DEFAULT_ICON_SIZE}px;
`