import {
  useGendersQuery,
  UserProfile,
  useUserProfileQuery,
} from "@haystackdev/cicf-content-api";
import DropdownSelect from "components/DropdownSelect";
import QuerySuspended from "components/QuerySuspended";
import Spacer from "components/Spacer";

export function GenderCard({
  profile,
  isEditing,
  selectedGender,
  onChangeSelectedGender,
}: {
  onChangeSelectedGender: (v: string | null) => void,
  isEditing: boolean,
  profile: UserProfile,
  selectedGender: string | null,
}) {
  // This makes sense
  const showGender = selectedGender !== null || !profile.demographics.gender ? selectedGender : profile.demographics.gender;
  const gendersQuery = useGendersQuery();

  return (
    <div style={{ display: "flex", justifyContent: "center", zIndex: 10000 }}>
      <div
        style={{
          width: "551px",
          padding:20,
          backgroundColor: "#FFF",
          borderRadius: 10,
          boxShadow: "0px 8px 15px rgba(0, 0, 0, 0.05)",
        }}
      >

        <h3 style={{ margin: 0, fontSize: "18px", fontWeight: 600 }}>Gender</h3>
        <Spacer v={16}/>
        {isEditing ? (
          <QuerySuspended
            queries={[gendersQuery]}

          > 
            {gendersQuery.data && 
              <DropdownSelect 
                variant="default"
                onChange={onChangeSelectedGender}
                selected={selectedGender}
                placeholder='Select gender...'
                choices={gendersQuery.data.map((e)=>({
                  label: e,
                  value: e,
                }))}
              />
            }
          </QuerySuspended>

        ) : (
          <div
            style={{
              marginTop: 12,
            }}
          >
            {showGender
              ? showGender
              : "Haven't selected a gender."}
          </div>
        )}
      </div>
    </div>
  );
}
