import InputUser from 'assets/icons/input/user.svg';
import Trash from 'assets/icons/trash.svg';
import ChevronLightRight from 'assets/icons/chevron-right-light.svg';


export type IconWithTextIcon = 'wt-location' | 'wt-online' | 'wt-award' | 'wt-ticket-price' | 'wt-touchstones' | 'wt-time' | 'wt-questions';
export type OtherIcons = 'onboarding-check' | 'trash' | 'chevron-right-light' | 'mail-dark' | 'input-user' | 'mail' | 'user' | 'white-x' | 'chevron-left' | 'lock' | 'eye' | 'eye-off' | 'chevron-right' | 'arrow-right' | 'play-button' | 'access-content-check' | 'x' | 'chevron-down-black' | 'touchstone-completed-thumb';
export type HomeDataCardIcon = 'hc-bookmark' | 'hc-ribbon' | 'hc-thumb';
export type MediaTypeIcon = 'mt-article' | 'mt-podcast' | 'mt-video' | 'mt-article-outline' | 'mt-podcast-outline' | 'mt-video-outline' | 'mt-guidepost' | 'mt-guidepost-outline';
export type EventsSectionHeaderIcon = 'es-list' | 'es-card';

export type AllIcons = IconWithTextIcon | OtherIcons | HomeDataCardIcon | MediaTypeIcon | EventsSectionHeaderIcon;

interface ImageProps {
    size: number,
}

export default function Icon({
    icon,
    size,
    ...rest
} : {
    icon: AllIcons,
    size: number,
} & HTMLAttributes<HTMLImageElement>) {
    return (
        <Image
            src={(icon in iconMap)?iconMap[icon as keyof typeof iconMap]:icon}
            size={size}
            {...rest}
        />
    )
}

const iconMap: {[key in AllIcons]: string} = {
    'mt-guidepost': MTGuidepost,
    'mt-guidepost-outline': MTGuidepostOutline,
    'eye': Eye,
    'eye-off': EyeOff,
    'white-x': WhiteX,
    'wt-location': WTLocationIcon,
    'wt-ticket-price': WTTicketPrice,
    'wt-touchstones': WTTouchstones,
    'wt-award': WTAward,
    'wt-time': WTTime,
    'wt-online': WTOnline,
    'wt-questions': WTQuestions,
    'onboarding-check': OnboardingCheck,
    'hc-bookmark': HCBookmark,
    'hc-ribbon': HCRibbon,
    'hc-thumb': HCThumb,
    'arrow-right': ArrowRight,
    'mt-article': MTArticle,
    'mt-podcast': MTPodcast,
    'mt-video': MTVideo,
    'mt-article-outline': MTArticleOutline,
    'mt-podcast-outline': MTPodcastOutline,
    'mt-video-outline': MTVideoOutline,
    'play-button': PlayButton,
    'access-content-check': AccessContentCheck,
    'x': XIcon,
    'es-card': ESCard,
    'es-list': ESList,
    'chevron-left': ChevronLeft,
    'chevron-right': ChevronRight,
    'chevron-down-black': ChevronDownBlack,
    'touchstone-completed-thumb': TouchstoneCompletedThumb,
    'lock': Lock,
    'user': User,
    'mail': Mail,
    'input-user': InputUser,
    'mail-dark': MailDark,
    'trash': Trash,
    'chevron-right-light': ChevronLightRight
};

const Image = styled.img<ImageProps>`
    width: ${p=>p.size}px;
    height: ${p=>p.size}px;
`


