import { GetEventsResponse } from "@haystackdev/cicf-content-api/lib/requests/getEvents";
import { Fragment, useEffect, useState } from "react";
import styled from "styled-components";
import { datesAreSame } from "helpers";
import Calendar from "features/Events/EventsPage/Calendar";
import Month from "types/Month";
import { monthFromDate } from "features/Events/EventsPage/Calendar/calendarUtils";
import { EventsQueryData } from "@haystackdev/cicf-content-api/lib/queries/query/eventsQuery";
import EventCard from "components/EventCard";
import Spacer from "components/Spacer";
import { text } from "styleUtilities";

export default function EventsRightColumn({
    eventsQueryData,
    selectedDate,
    onSelectedDateChange,
} : {
    eventsQueryData: EventsQueryData,
    selectedDate: Date,
    onSelectedDateChange: (v: Date)=>void,
}) {
    const [selectedMonth, setSelectedMonth] = useState<Month>(monthFromDate(new Date()));

    return (
        <Container>
            <Calendar 
                selectedDate={selectedDate}
                month={selectedMonth}
                onChangeMonth={setSelectedMonth}
                onChangeDate={onSelectedDateChange}
                queryData={eventsQueryData}
            />
            <FeaturedEventsContainer>
                <FeaturedEventsText>
                    Featured events
                </FeaturedEventsText>
                {eventsQueryData.featuredIds.map((e)=>(
                    <Fragment
                        key={e}
                    >
                        <EventCard
                            key={e}
                            event={eventsQueryData.idToEventMap[e]}
                        />
                        <Spacer v={12}/>
                    </Fragment>
                ))}
            </FeaturedEventsContainer>
        </Container>
    )
}


const Container = styled.div`
    display: flex;
    flex: 1;
    flex-direction: column;
`

const FeaturedEventsContainer = styled.div`
    display: flex;
    align-self: stretch;
    margin-top: 38px;
    flex-direction: column;
`

const FeaturedEventsText = styled.h3`
    ${text(600, 18, 24, 'page-header')}
    margin-bottom: 18px;
`