import Card from "components/Card";
import { CircularProgressbar, buildStyles } from "react-circular-progressbar";
import styled from "styled-components";
import { text } from "styleUtilities";
import { backgroundColor } from "theme";

export default function LearningProgressIndicator({
    percentageComplete,
    size=120,
}: {
    percentageComplete: number,
    size?: number,
}) {
    return (
        <div style={{
            width: size,
            height: size,
            position: 'relative',
        }}>
            <CircularProgressbar
                value={percentageComplete}
                styles={{
                    ...buildStyles({
                        pathColor: backgroundColor['primary-progress-bar'],
                        trailColor: backgroundColor['primary-progress-bar-background'],
                        rotation: 0.75,
                        strokeLinecap: 'round',
                    }),
                }}
            />
            <div style={{
                width: size,
                height: size,
                display: 'inline-flex',
                justifyContent: 'center',
                alignItems: 'center',
                position: 'absolute',
                top: 0,
                left: 0,
            }}>
                <Text>
                    {percentageComplete + '%'}
                </Text>
            </div>
        </div>
    )
}

const Text = styled.span`
    ${text(600, 24, 32, 'card-title')}
`