import { inputConstants } from "@haystackdev/cicf-content-api";
import { HTMLAttributes, TextareaHTMLAttributes, } from "react";
import styled from "styled-components";
import { border } from "styleUtilities";
import { backgroundColor } from "theme";

interface CICFTextAreaProps extends TextareaHTMLAttributes<HTMLTextAreaElement> {
    height?: number,
}

const TextArea = styled.textarea<CICFTextAreaProps>`
    height: ${p=>p.height?p.height:250}px;
    border-radius: 20px;
    background-color: ${p=>p.disabled?'inherit':backgroundColor['text-area']};
    align-self: stretch;
    padding: 25px 29px;
    outline: none;
    ${p=>p.disabled?border({
        radius: 20,
        color: 'text-input',
        width: 1
    }):'border-style: none;'}
    resize: none;
    &:focus {
        outline: none!important;
        ${border({
            radius: 20,
            color: 'text-input-focused',
            width: 1
        })}
    }
`

export default function CICFTextArea(props: CICFTextAreaProps) {
    return <TextArea maxLength={inputConstants.textAreaMaxLength} {...props}/>
}