import { ButtonHTMLAttributes, HTMLAttributes } from 'react';
import styled from 'styled-components';
import { boxShadows, fontSizes, loadingSpinnerLightColor } from '../../theme';
import { backgroundColor, textColor, loadingSpinnerColor } from 'theme';
import { border } from 'styleUtilities';
import ClipLoader from 'react-spinners/ClipLoader';

type ButtonVariant = 'primary' | 'secondary' | 'outline' | 'disabled' | 'text-only';

interface Props extends ButtonHTMLAttributes<HTMLButtonElement> {
  text: string,
  variant?: ButtonVariant,
  loading?: boolean,
  hideShadow?: boolean,
}

export default function CICFButton({
  text,
  variant = 'primary',
  disabled,
  loading,
  ...props
}: Props) {

  return (
    <Button
      variant={variant}
      disabled={disabled || loading}
      {...props}
    >
      {loading &&
        <span style={{
          alignSelf: 'center',
          justifyContent: 'center',
          alignItems: 'center',
          position: 'absolute',
        }}>
          <ClipLoader size={20} color={variant == 'primary' || variant == 'disabled'? loadingSpinnerLightColor : loadingSpinnerColor} />
        </span>
        
      }


      <strong style={loading?{opacity: 0}:undefined}>
        {text}
      </strong>

    </Button>
  )
}

interface ButtonProps {
  variant: ButtonVariant,
  hideShadow?: boolean,
}
// background-color: ${({ disabled, variant }) => (disabled ? "#BDBDBD" : backgroundColor['button-primary']};
const Button = styled.button<ButtonProps>`

  background-color: ${({ variant }) => backgroundColorForVariant(variant)};
  color: ${({ variant }) => textColorForVariant(variant)};
  border: none;
  border-radius: 10px;
  flex-direction: row;
  display: inline-flex;
  justify-content: center;
  height: ${p => p.variant !== 'text-only' ? 55 : 0}px;
  align-items: center;
  align-self: center;
  font-size: ${fontSizes.buttonText}px;
  font-weight: 500;
  cursor: ${p => p.disabled ? 'default' : 'pointer'};
  box-shadow: ${p => p.variant == 'text-only' || p.hideShadow || p.variant == 'outline' ? 0 : boxShadows.button};
  flex: none;
  text-align: center;
  padding: 0px 15px;
  ${p => p.variant == 'outline' ? border({
  radius: 10,
  width: 1,
  color: 'button-outline'
}) : ''}
`;

function backgroundColorForVariant(variant: ButtonVariant): string {

  switch (variant) {
    case 'primary':
      return backgroundColor['button'];
    case 'secondary':
      return backgroundColor['button-secondary'];
    case 'disabled':
      return backgroundColor['button-disabled'];
    case 'text-only':
      return 'inherit';
  }
  return 'inherit'
}

function textColorForVariant(variant: ButtonVariant): string {
  if (variant === 'disabled') return textColor['button-disabled'];
  if (variant === 'text-only') return textColor['text-only-button']
  return textColor['button'];
}
