import { usePasswordResetTokenValidQuery, usePasswordStrength, useResetPasswordMutation } from "@haystackdev/cicf-content-api";
import Card from "components/Card";
import CICFButton from "components/CICFButton";
import CICFInput from "components/CICFInput";
import Page from "components/Page";
import PasswordStrengthIndicator from "components/PasswordMeter";
import QuerySuspended from "components/QuerySuspended";
import Spacer from "components/Spacer";
import IS_MOBILE_BREAKPOINT from "constants/overlayBreakpoints";
import React, { useEffect, useState } from "react";
import { useNavigate, useParams } from "react-router-dom";
import styled from "styled-components";
import { text } from "styleUtilities";
import { PASSWORD_CHANGED_PATH } from "topLevelRoutes";
import ChangePasswordUrlParameters from "types/UrlParameters/ChangePasswordUrlParameters";

export default function ResetPasswordNewPassword() {
  const token = useParams<ChangePasswordUrlParameters>().token!;
  const resetPasswordMutation = useResetPasswordMutation();

  const [password, setPassword] = useState<string>('');
  const [confirmPassword, setConfirmPassword] = useState<string>('');
  const [passwordValid, setPasswordValid] = useState<boolean>(false);
  const [confirmPasswordValid, setConfirmPasswordValid] = useState<boolean>(false);

  const [confirmPasswordError, setConfirmPasswordError] = useState<string>('');
  const passwordStrength = usePasswordStrength(password);

  const [forceBadLink, setForceBadLink] = useState<boolean>(false);

  const navigate = useNavigate();

  const canSubmit = passwordValid && confirmPasswordValid;

  function onSubmitClick() {
    if (confirmPassword !== password) {
      setConfirmPasswordError('Passwords do not match.')
      return;
    }
    resetPasswordMutation.mutateAsync({
      resetToken: token,
      newPassword: password,
    }).then((r) => {
      if (r.success) {
        navigate('/' + PASSWORD_CHANGED_PATH);
      } else {
        setForceBadLink(true);
      }
    })
  }

  useEffect(() => {
    if (window.matchMedia(`(max-width: ${IS_MOBILE_BREAKPOINT}px)`)) {
        window.location.href = 'cicf://reset-password/' + token
    };
}, [])

  return (
    <Page>
      <Container
        variant="primary"
      >
        <>
          <Title>
            New Password
          </Title>
          <Subtitle>
            Enter your new password below to change your password.
          </Subtitle>
          <CICFInput
            placeholder='New Password'
            type={'password'}
            forceVisibleButton={true}
            validationType="password"
            value={password}
            onChange={setPassword}
            setValid={setPasswordValid}
            icon='lock'
          />
          <Spacer
            v={30}
          />
          <CICFInput
            placeholder='Confirm New Password'
            icon='lock'
            type={'password'}
            forceVisibleButton={true}
            validationType="password"
            value={confirmPassword}
            onChange={setConfirmPassword}
            setValid={setConfirmPasswordValid}
            errorMessage={confirmPasswordError}
          />
          <Spacer v={20} />
          <PasswordStrengthIndicator
            testPasswordResult={passwordStrength.testResult}
          />
          <Spacer v={60} />
          <CICFButton
            disabled={!canSubmit}
            variant={canSubmit ? 'primary' : 'disabled'}
            onClick={onSubmitClick}
            loading={resetPasswordMutation.isLoading || passwordStrength.busy}
            text="Reset Password"
          />
        </>
      </Container>
    </Page>

  )
}

const Container = styled(Card)`
  width: 560px;
  align-items: flex-start;
  padding: 50px;
  align-self: center;
`

const Title = styled.h1`
  ${text(600, 32, 44, 'card-title')}
  margin-bottom: 8px;
`

const Subtitle = styled.p`
  ${text(400, 16, 24, 'card-paragraph')}
  margin-bottom: 50px;
`