import CICFTextSpan from "components/CICFTextSpan"
import Icon from "components/Icon"
import Spacer from "components/Spacer"
import styled from "styled-components"
import { backgroundColor, borderColors, textColor } from "theme"

export default function OnboardingWidgetCheck({
    text,
    checked,
} : {
    text: string,
    checked: boolean,
}) {
    return (
        <Container>
            <CheckContainer
                checked={checked}
            >
                {!!checked &&
                    <Icon 
                        size={12}
                        icon='onboarding-check'
                    />
                }
            </CheckContainer>
            <Spacer 
                h={10}
            />
            <CICFTextSpan
                sizing={[400, 14, 24]}
                color={textColor['secondary-card-text']}
                
            >
                {text}
            </CICFTextSpan>
        </Container>
    )
}

interface CheckContainerProps {
    checked: boolean,
}

const CheckContainer = styled.span<CheckContainerProps>`
    width: 20px;
    height: 20px;
    justify-content: center;
    align-items: center;
    border-style: ${p=>!p.checked?'solid':'none'};
    border-width: 1px;
    border-color: ${borderColors['onboarding-checkbox-unchecked']};
    border-radius: 20px;
    background-color: ${p=>p.checked?backgroundColor['onboarding-checkbox-checked']:'transparent'};
`

const Container = styled.span`
    flex-direction: row;
    align-items: center;
`