import React, { useEffect, useState } from "react";
import { useNavigate, useParams } from "react-router-dom";

//Imports

//Images
import Confirm from "assets/emailConfirmIcon.png";
import styled from "styled-components";
import EmailConfirmedUrlParameters from "types/UrlParameters/EmailConfirmedUrlParameters";
import { text } from "styleUtilities";
import Page from "components/Page";
import EmailConfirmedGraphic from "components/Graphics/EmailConfirmedGraphic";
import { LOGIN_PATH } from "topLevelRoutes";
import CICFButton from "components/CICFButton";
import { contentCopy, useConfirmEmailChangeMutation, useConfirmEmailMutation } from "@haystackdev/cicf-content-api";
import QuerySuspended from "components/QuerySuspended";
import IS_MOBILE_BREAKPOINT from "constants/overlayBreakpoints";
import EmailConfirmedMobile from "features/Authorization/EmailConfirmed/EmailConfirmedMobile";

export default function EmailConfirmed() {
  const navigate = useNavigate();
  const [loaded, setLoaded] = useState<boolean>(false);
  const [success, setSuccess] = useState<boolean>(true);
  const token = useParams<EmailConfirmedUrlParameters>().token!;
  const confirmEmailMutation = useConfirmEmailMutation();

  function onLoginPress() {
    navigate('/' + LOGIN_PATH)
  }

  useEffect(() => {
    confirmEmailMutation.mutateAsync({ token }).then((r) => {
      setLoaded(true);
      setSuccess(r.success);
    })
  }, [])



  return (
    <>
    <EmailConfirmedMobile/>
    <Page>
      
      <CreateContainer>
        <>

          <ContainerHeader>
            Email Confirmed!
          </ContainerHeader>
          <ContainerText>
            {contentCopy.authorization.emailVerified.message}
          </ContainerText>
          <EmailIconContainer>
            <EmailIcon>
              <EmailConfirmedGraphic />
            </EmailIcon>
          </EmailIconContainer>
          <CICFButton
            text='Log In'
            onClick={onLoginPress}
          />
        </>
      </CreateContainer>
    </Page>
    </>
    )

  return (

    <QuerySuspended
      queries={[confirmEmailMutation]}
      forceLoading={!loaded}
    >
      {loaded && success ?
        <Page>
          <EmailConfirmedMobile />
          <CreateContainer>
            <>

              <ContainerHeader>
                Email Confirmed!
              </ContainerHeader>
              <ContainerText>
                {contentCopy.authorization.emailVerified.message}
              </ContainerText>
              <EmailIconContainer>
                <EmailIcon>
                  <EmailConfirmedGraphic />
                </EmailIcon>
              </EmailIconContainer>
              <CICFButton
                text='Log In'
                onClick={onLoginPress}
              />
            </>
          </CreateContainer>
        </Page>
        :
        <>
          <ContainerHeader>
            Couldn't confirm email
          </ContainerHeader>
          <ContainerText>
            Your link was likely invalid.
          </ContainerText>
        </>

      }
    </QuerySuspended>


  );
};


export const CreateContainer = styled.div`
  width: 560px;
  background: #ffffff;
  border: 1px solid #e6e8ec;
  box-sizing: border-box;
  box-shadow: 0px 6px 24px -6px rgba(0, 0, 0, 0.1);
  border-radius: 20px;
  justify-content: center;
  position: relative;
  padding: 40px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: auto;
  @media (max-width: ${IS_MOBILE_BREAKPOINT}px) {
    display: none;
  }
`;

export const ContainerHeader = styled.header`
  display: flex;
  color: #211d3a;
  font-size: 1.115em;
  flex: none;
  order: 0;
  flex-grow: 0;
  font-family: Inter;
  font-style: normal;
  font-weight: 600;
  line-height: 44px;
  justify-content: center;
  margin-bottom: 8px;
  ${text(600, 32, 44, 'card-title')}
`;

export const ContainerText = styled.p`
  display: flex;
  margin-left: 100px;
  margin-right: 100px;
  justify-content: center;
  text-align: center;
  color: #474554;
  line-height: 24px;
`;

export const EmailIconContainer = styled.div`
  background: #fff8d4;
  border-radius: 107.917px;
  width: 200px;
  height: 200px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  margin: 50px auto;
`;

export const EmailIcon = styled.div`
  mix-blend-mode: multiply;
`;

export const Image = styled.img`
  width: 173.37px;
  height: 199.65px;
`;

export const Button = styled.button`
  background: #fedb2a;
  width: 147px;
  height: 55px;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 10px auto;
  cursor: pointer;
  font-weight: 500;
  font-size: 14px;
  box-shadow: 0px 12px 40px -12px rgba(0, 0, 0, 0.1);
  margin-top: 74px;
  border: none;
`;

