import { UseQueryResult } from "react-query";
import React from "react";
import FlexColumn from "components/FlexColumn";
import HomeColumnTitle from "features/Home/Home/HomeColumnTitle";
import EventCard from "components/EventCard";
import QuerySuspended from "components/QuerySuspended";
import { Event } from "@haystackdev/cicf-content-api";
import { GetEventsResponse } from "@haystackdev/cicf-content-api/lib/requests/getEvents";
import Spacer from "components/Spacer";
import CICFButton from "components/CICFButton";
import { useNavigate } from "react-router-dom";
import { EventsQueryData } from "@haystackdev/cicf-content-api/lib/queries/query/eventsQuery";

export default function EventsColumn({
    query,
}: {
    query: UseQueryResult<EventsQueryData>
}) {
    const navigate = useNavigate();
    function onViewMoreEventsPress() {
        navigate('/events')
    }
    return (
        <FlexColumn>
            <HomeColumnTitle>
                Featured Events
            </HomeColumnTitle>
            <QuerySuspended
                queries={[query]}
            >
                {query.isSuccess &&
                    <>
                        {query.data!.events.slice(0, 2).map((e) => (
                        <React.Fragment
                            key={e.id}
                        >
                            <EventCard
                                event={e}
                                key={e.id}
                            />
                            <Spacer
                                v={16}
                                key={'sp-'+e.id}
                            />
                        </React.Fragment>
                        ))}
                        <CICFButton
                            onClick={onViewMoreEventsPress}
                            text='View More Events'
                        />
                    </>
                }
            </QuerySuspended>
        </FlexColumn>
    )
}

