import styled from "styled-components";
import { border } from "styleUtilities";
import { backgroundColor, boxShadows } from "theme";

interface MultiswitchProps<State> {
    items: State[],
    value: State,
    onChange: (v: State)=>void,
    height?: number,
    width?: number,
    radius?:number
}

export default function Multiswitch<State>({
    items,
    value,
    onChange,
    height=48,
    width=500,
    radius=12,
} : MultiswitchProps<State>) {
    return (
        <Container
            height={height}
            width={width}
            radius={radius}
        >
            {items.map((e,i)=>(
                <Button
                    selected={value===e}
                    onClick={()=>onChange(e)}
                    radius={radius}
                    key={'' + i}
                >
                    {e}
                </Button>
            ))}
        </Container>
    )
}

interface StyleProps {
    height: number,
    width: number,
    radius: number,
}

const Container = styled.span<StyleProps>`
    display: inline-flex;
    align-self: center;
    flex-direction: row;
    align-items: center;
    height: ${p=>p.height}px;
    width: ${p=>p.width}px;
    background-color: ${backgroundColor['multiswitch-container']};
    ${p=>border({
        color: 'multiswitch',
        width: 4,
        radius: p.radius,
    })}
`

interface ButtonProps {
    selected: boolean,
    radius: number,
}

const Button = styled.button<ButtonProps>`
    background-color: ${({selected})=>backgroundColor[selected?'multiswitch-switch':'multiswitch-container']};
    ${({selected})=>selected?`box-shadow: ${boxShadows.multiswitch};`:''}
    border-radius: ${p=>p.radius}px;
    flex: 1;
    border: 0;
    height: 100%;
    
`

