import { CurriculumItem } from "@haystackdev/cicf-content-api";
import Card from "components/Card";
import Icon from "components/Icon";
import IconWithText from "components/IconWithText";
import Spacer from "components/Spacer";
import { TOUCHSTONE_DETAIL_PATH } from "features/Learning/LearningRoutes";
import { iconForMediaType, textForMediaType } from "helpers";
import { useNavigate } from "react-router-dom";
import styled from "styled-components";
import { maxLines, text } from "styleUtilities";
import { backgroundColor, boxShadows } from "theme";
import { LEARNING_PATH } from "topLevelRoutes";

export default function LearningPathwayDetailUpNext({
    item,
}: {
    item: CurriculumItem | undefined,
}) {
    const nav = useNavigate();

    function onPlayClick() {
        nav('/' + LEARNING_PATH + '/' + TOUCHSTONE_DETAIL_PATH + '/' + item?.id)
    }
    
    return (
        <Container>
            {item &&
                <>
                    <Header>
                        Up Next
                    </Header>
                    <Spacer
                        v={16}
                    />
                    <NextCard
                        variant="secondary"
                    >
                        <NextCardLeftContainer>
                            <NextCardHeader>
                                {item!.title}
                            </NextCardHeader>
                            <SmallTextWithIconsRow>
                                <IconWithText
                                    text={textForMediaType(item!.mediaType)}
                                    icon={iconForMediaType(item!.mediaType)}
                                />
                                <Spacer h={17}/>
                                <IconWithText
                                    text={`${item!.estimatedTimeInMinutes} minutes`}
                                    icon={'wt-time'}
                                />
                            </SmallTextWithIconsRow>
                        </NextCardLeftContainer>
                        <PlayButton
                            onClick={onPlayClick}
                        >
                            <Icon 
                                icon='play-button'
                                size={25}
                            />
                        </PlayButton>
                    </NextCard>
                </>
            }
        </Container>
    )
}

const Header = styled.h4`
    ${text(600, 18, 24, 'page-header')}
`

const Container = styled.div`
    flex-direction: column;
    flex: 1;
    display: inline-flex;
`

const NextCard = styled(Card)`
    flex-direction: row;
    justify-content: space-between;
    flex: 1;
    align-self: stretch;
    align-items: center;
    padding: 20px 40px;
`

const NextCardHeader = styled.h3`
    ${text(600, 22, 32, 'card-title')}
    ${maxLines(2)}
`

const NextCardLeftContainer = styled.div`
    display: inline-flex;
    flex-direction: column;
    max-width: 60%;
    justify-content: space-between;
    align-self: stretch;
`

const SmallTextWithIconsRow = styled.span`
    display: inline-flex;
    flex-direction: row;
`

const PlayButton = styled.button`
    width: 60px;
    height: 60px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    box-shadow: ${boxShadows.playButton};
    background-color: ${backgroundColor['play-button']};
    border-radius: 8px;
    border: 0;
    &:hover {
        cursor: pointer;
    }
`
