
import EventDetailPage from 'features/Events/EventDetailPage';
import EventsPage from 'features/Events/EventsPage';
import {Routes, Route, Navigate} from 'react-router-dom';
import RouteMap from 'types/RouteMap';


type RouteName = '' | ':eventId';

const eventsRoutesMap: RouteMap<RouteName> = {
    ':eventId': ()=><EventDetailPage/>,
    '': ()=><EventsPage/> // Make sure this is last since it's the default.
}

export default function EventsRoutes() {
    return (
        <Routes>
            {Object.keys(eventsRoutesMap).map((routeName: any)=>{
                const Component = eventsRoutesMap[routeName as RouteName];
                return (
                    <Route
                        element={<Component/>}
                        path={'/'+routeName}
                        key={routeName}
                    />
                )
            })}
        </Routes>
    )
}