import { contentCopy } from "@haystackdev/cicf-content-api";
import CICFButton from "components/CICFButton";
import EmailConfirmedGraphic from "components/Graphics/EmailConfirmedGraphic";
import IS_MOBILE_BREAKPOINT from "constants/overlayBreakpoints";
import styled from "styled-components";
import { text } from "styleUtilities";

import { useEffect } from "react";
import Spacer from "components/Spacer";

export default function EmailConfirmedMobile() {

    function onClick() {
        window.location.href = 'cicf://'
    }
    useEffect(() => {
        if (window.matchMedia(`(max-width: ${IS_MOBILE_BREAKPOINT}px)`)) {
            onClick();
        };
    }, [])
    return (
        <CreateContainer>
            <ContainerHeader>
                Email Confirmed!
            </ContainerHeader>
            <ContainerText>
                {contentCopy.authorization.emailVerified.message}
            </ContainerText>
            <EmailIconContainer>
                <EmailIcon>
                    <EmailConfirmedGraphic />
                </EmailIcon>
            </EmailIconContainer>
            <ContainerText>
                Return to the mobile application to begin your learning journey.
            </ContainerText>
            <Spacer v={16}/>
            <CICFButton
                onClick={onClick}
                text='Open App'
            />
        </CreateContainer>
    )
}


