import React from "react";
import styled from "styled-components";
import { backgroundColor, dimensions } from "theme";
import Card from "components/Card";
import SettingsGraphic from "components/Graphics/SettingsGraphic";
import { shadow, text } from "styleUtilities";
import SettingsMenuItem from "features/Settings/SettingsMenu/SettingsMenuItem";
import { settingsRoutesMap } from "features/Settings/SettingsRoutes";
import { SETTINGS_PATH } from "topLevelRoutes";
import settingsRoutesDisplayNames from "features/Settings/settingsRoutesDisplayNames";
import { useLocation } from "react-router-dom";

export default function SettingsMenu() {
    const location = useLocation();
    const pathSplit = location.pathname.split('/');
    const settingsPath = pathSplit[pathSplit.length - 1]
    return (
        <Menu 
            variant="primary"
        >
            <Graphic />
            <SettingsText>
                Account Settings
            </SettingsText>
            {Object.keys(settingsRoutesMap).map((e)=>{
                return (
                    <SettingsMenuItem 
                        to={'/'+SETTINGS_PATH+'/'+e}
                        text={settingsRoutesDisplayNames[e]}
                        selected={settingsPath==e}
                        key={e}
                    />
                )
            })}
        </Menu>
    )
}

const Menu = styled(Card)`
    display: flex;
    flex-direction: column;
    background-color: ${backgroundColor['settings-menu']};
    align-items: flex-start;
    width: 472px;
    border-radius: 0px;
    align-self: inherit;
    padding-top: 24px;
    padding-left: 64px;
    padding-right: 64px;
    height: calc(100vh - ${dimensions.navbarHeight}px);
`


const Graphic = styled(SettingsGraphic)`
    align-self: center;
`

const SettingsText = styled.h2`
    margin-top: 24px;
    margin-bottom: 4px;
    ${text(600, 24, 44, 'settings-menu')}
`