import Organization from "@haystackdev/cicf-content-api/lib/types/Organization";
import LabelledCheckbox from "components/LabelledCheckbox";
import { updateSet } from "helpers";
import { useOnClickOutside } from "hooks/useOnClickOutside";
import styled from "styled-components";
import { shadow } from "styleUtilities";
import { backgroundColor, boxShadows } from "theme";


export default function OrganizationInputSearchResults({
    results,
    selectedSet,
    onSelectChange,
    onClickOutside,
} : {
    results: Organization[],
    selectedSet: Set<string>,
    onSelectChange: (v: Set<string>)=>void,
    onClickOutside: ()=>void,
}) {

    function onChange(organizationName: string) {
        onSelectChange(updateSet(selectedSet, organizationName, !selectedSet.has(organizationName)))
    }

    const {props} = useOnClickOutside(onClickOutside);

    console.log(results);

    return (
        <Container
            {...props}
        >
            {results.map((e)=>(
                <Checkbox 
                    label={e}
                    value={selectedSet.has(e)}
                    onChange={()=>onChange(e)}
                    key={e}
                
                />
            ))}
        </Container>
    )
}

const Container = styled.div`
    position: absolute;
    top: 100%;
    right: 0;
    left: 0;
    border-radius: 10px;
    background-color: ${backgroundColor['profile-dropdown-menu']};
    display: flex;
    flex-direction: column;
    ${shadow('card')}
`

const Checkbox = styled(LabelledCheckbox)`
    padding: 16px;
`
