function QuestionSection({
  question,
  previousAnswer,
  previousAnswerTimestamp,
  currentAnswer,
}: {
  previousAnswer: number | null;
  currentAnswer: number;
  previousAnswerTimestamp?: number;
  question: Question;
}) {

  if (previousAnswer !== null && !previousAnswerTimestamp)
    throw new Error(
      "QuestionSection error: if previous answer is not null, a previousAnswerTimestamp must be provided."
    );

  return (
    <>
      <p
        style={{ fontSize: 18, fontWeight: 600, color: "#000", marginTop: 24 }}
      >
        {question.text}
      </p>
      <AnswerContainer
        title={"Your answer this time: "}
        text={question.answers[currentAnswer]}
      />
      {previousAnswer !== null && (
        <AnswerContainer
          title={`Your previous answer (${longDateString(
            new Date(previousAnswerTimestamp!),
            false
          )}):`}
          text={`${question.answers[previousAnswer]}`}
        />
      )}
    </>
  );
}

export default function AssessmentResultsContent({
  newResult,
  oldResult,
}: {
  newResult?: AssessmentResult,
  oldResult?: AssessmentResult,
}) {
  const navigate = useNavigate();


  const previousQuestionMap = useMemo(() => {
    if (!oldResult) return {};
    var r: { [questionId: string]: number } = {}; // Value is the selected answers index.
    // Index 0 will be the previous assessment.
    oldResult.answers.forEach((a: any, i: any) => {
      r[a.questionId] = a.selectedAnswerIndex;
    });
    return r;
  }, [oldResult]);


  if (!newResult || !oldResult) {
    // There are no assessments for this user... They must've navigated here directly.
    return (
      <Navigate to='/' />
    )
  }


  return (
    <Container>
      <div>
        <p
          style={{
            color: "#474554",
            lineHeight: "24px",
            fontSize: "16px",
            marginTop: 16,
          }}
        >
          {"Below are your assessment results compared with your previous answers."}
        </p>
      </div>
      {newResult.answers.map((a, i: any) => {
        return (
          <QuestionSection
            key={a.questionId}
            previousAnswer={
              a.questionId in previousQuestionMap
                ? previousQuestionMap[a.questionId]
                : null
            }
            currentAnswer={a.selectedAnswerIndex}
            question={newResult.assessment.questions[i]}
            previousAnswerTimestamp={
              oldResult
                ? oldResult.finishedTimestamp
                : undefined
            }
          />
        );
      })}
      <Spacer
        v={24}
      />
      <CICFButton
        text="Back home"
        onClick={() => {
          navigate("/home");
        }}
      />
    </Container>

  )
}



