
import AssessmentResultsScreen from 'features/Assessment/AssessmentResults';
import AssessmentStartPage from 'features/Assessment/AssessmentStart';
import AssessmentThankYou from 'features/Assessment/AssessmentThankYou';
import TakeAssessmentPage from 'features/Assessment/TakeAssessmentPage';
import {Routes, Route, Navigate} from 'react-router-dom';
import RouteMap from 'types/RouteMap';


type RouteName = '' | 'take' | 'results' | 'thanks';

export const ASSESSMENT_RESULTS_PATH = 'results';
export const ASSESSMENT_THANK_YOU_PATH = 'thanks';
export const TAKE_ASSESSMENT_PATH = 'take';

const eventsRoutesMap: RouteMap<RouteName> = {
    [TAKE_ASSESSMENT_PATH]: TakeAssessmentPage,
    [ASSESSMENT_RESULTS_PATH]: AssessmentResultsScreen,
    '': AssessmentStartPage, 
    [ASSESSMENT_THANK_YOU_PATH]: AssessmentThankYou
}

export default function AssessmentRoutes() {
    return (
        <Routes>
            {Object.keys(eventsRoutesMap).map((routeName: any)=>{
                const Component = eventsRoutesMap[routeName as RouteName];
                return (
                    <Route
                        element={<Component/>}
                        path={'/'+routeName}
                        key={routeName}
                    />
                )
            })}
        </Routes>
    )
}