import create from 'zustand';
import produce from 'immer';
import { axiosLogin, axiosLogout } from '@haystackdev/cicf-content-api';
interface AuthStore {
    token: string | null,
    refreshToken: string | null,
    loggedIn: boolean,
    logout: ()=>void,
    login: (token: string, refreshToken: string)=>void,
    setAccessToken: (v: string)=>void,
}

const SESSION_STORAGE_TOKEN_KEY = 'token';
const SESSION_STORAGE_REFRESH_TOKEN_KEY = 'refreshToken';

const sessionStorageToken = sessionStorage.getItem(SESSION_STORAGE_TOKEN_KEY);
const sessionStorageRefreshToken = sessionStorage.getItem(SESSION_STORAGE_REFRESH_TOKEN_KEY)

console.log("sessionStorageToken is " + sessionStorageToken);
console.log("sessionRefreshToken is " + sessionStorageRefreshToken)

const useAuth = create<AuthStore>((set, get)=>({
    loggedIn: sessionStorageToken?true:false,
    logout: ()=>{
        set(old=>produce(old, draft=>{
            draft.loggedIn=false;
            draft.token = null;
            draft.refreshToken = null;
            sessionStorage.removeItem(SESSION_STORAGE_TOKEN_KEY);
            sessionStorage.removeItem(SESSION_STORAGE_REFRESH_TOKEN_KEY);
            axiosLogout();
        }))
    },
    login: (token, refreshToken)=>{
        set(old=>produce(old, draft=>{
            draft.loggedIn = true;
            draft.token = token;
            draft.refreshToken = refreshToken;
            loginWithStore(token, refreshToken);
            sessionStorage.setItem(SESSION_STORAGE_TOKEN_KEY, token);
            sessionStorage.setItem(SESSION_STORAGE_REFRESH_TOKEN_KEY, token);
        }))
    },
    setAccessToken: (v: string)=>{
        set({token: v})
    },
    token: sessionStorageToken,
    refreshToken: sessionStorageRefreshToken,
}))

function loginWithStore(_token?: string, _refreshToken?: string) {
    const state = useAuth.getState();
    var token = _token || state.token;
    var refreshToken = _refreshToken || state.refreshToken;
    if(token && refreshToken)
        axiosLogin({
            accessToken: token,
            refreshToken: refreshToken,
            onAuthorizationFail: ()=>{
                state.logout()
            },
            onAccessTokenRefresh: (v)=>{
                state.setAccessToken(v);
            }
        });
    else state.logout();
}

if(sessionStorageToken && sessionStorageRefreshToken) loginWithStore();

export default useAuth;