import CICFButton from "components/CICFButton";
import Spacer from "components/Spacer";
import React, { ChangeEvent, FormEvent, HTMLAttributes } from "react";
import styled from "styled-components";
import { text } from "styleUtilities";

export default function LabelledCheckbox({
    value,
    onChange,
    label,
    ...props
} : {
    value: boolean,
    onChange: (v:boolean)=>void,
    label: string,
} & Omit<HTMLAttributes<HTMLSpanElement>, 'onChange'>) {

    function onCheckboxChange(e: ChangeEvent<HTMLInputElement>) {
        onChange(e.target.checked);
    }

    return (
        <Container
            {...props}
        >
            <CheckBox 
                type='checkbox'
                checked={value}
                onChange={onCheckboxChange}
                id={label}
            />
            <Label
                htmlFor={label}
            >
                {label}
            </Label>
        </Container>
    )
}

const Container = styled.span`
    flex-direction: row;
    align-items: center;

`

const CheckBox = styled.input`
    width: 24px;
    height: 24px;
    margin-right: 10px;
`

const Label = styled.label`
    ${text(400, 14, 24, 'checkbox-label')}
    max-width: calc(100% - 34px);
`