import { useRulesOfEngagementQuery } from "@haystackdev/cicf-content-api";
import CICFButton from "components/CICFButton";
import RulesOfEngagementGraphic from "components/Graphics/RulesOfEngagementGraphic";
import QuerySuspended from "components/QuerySuspended";
import Spacer from "components/Spacer";
import React, { useState, useRef, useEffect } from "react";
import { Query } from "react-query";

//Imports

//Styles
import {
  OpacityBackground,
  RulesContainer,
  ContainerHeader,
  TermsContainer,
  TermsText,
  Button,
} from "./styles";

const RulesOfEngagement = ({ createAccount, loading }: {
  createAccount: () => void,
  loading: boolean,
}) => {
  const [bottomReached, setBottomReached] = useState(false);
  const rulesOfEngagementQuery = useRulesOfEngagementQuery();

  const innerRef = useRef();

  const onScroll = () => {
    if (innerRef.current && !bottomReached) {
      const { scrollTop, scrollHeight, clientHeight } = innerRef.current;
      setBottomReached(scrollTop + clientHeight >= scrollHeight);
    }
  };

  return (
    <OpacityBackground>
      <RulesContainer>
        <QuerySuspended
          queries={[rulesOfEngagementQuery]}
        >
          {rulesOfEngagementQuery.data &&
            <>
              <ContainerHeader>
                  Rules of Engagement
                
              </ContainerHeader>
              <RulesOfEngagementGraphic/>
              <Spacer v={30}/>
              <TermsContainer
                onScroll={() => {
                  onScroll();
                }}
                ref={innerRef as any}
              >
                <TermsText>
                  <p style={{whiteSpace: 'break-spaces'}}>{rulesOfEngagementQuery.data.rulesOfEngagementText}</p>
                </TermsText>
              </TermsContainer>
              <Spacer v={16}/>
              <CICFButton 
                disabled={!bottomReached}
                variant={bottomReached?'primary':'disabled'}
                onClick={createAccount}
                text='I Agree'
                loading={loading}
              />
              <Spacer
                v={20}
              />
            </>
          }
        </QuerySuspended>

      </RulesContainer>
    </OpacityBackground>
  );
};

export default RulesOfEngagement;
