import Card from "components/Card";
import Spacer from "components/Spacer";
import { ReactNode } from "react";
import styled from "styled-components";
import { text } from "styleUtilities";

export default function AuthCard({
    title,
    subtitle,
    children,
    className,
}: {
    title: string,
    subtitle?: string,
    children: ReactNode,
    className?: string,
}) {
    return (
        <Container
            variant='primary'
            className={className}
        >
            <Header>
                <Title>
                    {title}
                </Title>
                {subtitle &&
                    <>
                        <Spacer v={8}/>
                        <Subtitle>
                            {subtitle}
                        </Subtitle>
                    </>
                }
                <Spacer v={50}/>
            </Header>
            {children}
        </Container>
    )
}

const Container = styled(Card)`
    width: 560px;
    align-self: center;
    padding: 40px;
`

const Header = styled.div`
    align-self: stretch;
    justify-content: flex-start;
    flex-direction: column;
    display: flex;
`

const Title = styled.h1`
    ${text(600, 32, 44, 'card-title')}
`
const Subtitle = styled.p`
    ${text(400, 16, 24, 'card-paragraph')}
`