import { useUserProfileQuery } from "@haystackdev/cicf-content-api";
import TouchstoneNote from "@haystackdev/cicf-content-api/lib/types/TouchstoneNote";
import Spacer from "components/Spacer";
import { timestampToDateString } from "helpers";
import useModalStore from "state/modals";
import styled from "styled-components";
import { text } from "styleUtilities";

export default function TouchstoneDetailNote({
    touchstoneNote,
}: {
    touchstoneNote: TouchstoneNote,
}) {
    const userProfile = useUserProfileQuery();
    const showEditModal = useModalStore((s)=>s.showEditTouchstoneNote);
    const showDeleteModal = useModalStore((s)=>s.showDeleteTouchstoneNote)

    function onEditClick() {
        showEditModal(touchstoneNote)
    }

    function onDeleteClick() {
        showDeleteModal(touchstoneNote)
    }

    const isOwned = !!userProfile.data && userProfile.data.id == touchstoneNote.createdBy.id;

    return (
        <Container>
            <ProfilePhoto
                src={touchstoneNote.createdBy.profileImageUrl}
            />
            <Spacer
                h={16}
            />
            <ContentColumn>
                <ContentText>
                    {touchstoneNote.text}
                </ContentText>
                <Spacer v={8}/>
                <DateText>
                    {(new Date(touchstoneNote.createTime)).toLocaleDateString('en-us', {
                        month: 'long',
                        day: 'numeric',
                        year: 'numeric'
                    }) + ` by ${touchstoneNote.createdBy.name}` + (isOwned?(touchstoneNote.isPublic?' (Public Note)':' (Private Note)'):'')}
                </DateText>
                <Spacer
                    v={8}
                />
                {userProfile.data && isOwned &&
                    <ActionsRow>
                        <EditButton
                            onClick={onEditClick}
                        >
                            Edit
                        </EditButton>
                        <Spacer h={24} />
                        <DeleteButton
                            onClick={onDeleteClick}
                        >
                            Delete
                        </DeleteButton>
                    </ActionsRow>
                }
            </ContentColumn>
        </Container>
    )
}

const Container = styled.div`
    display: flex;
    flex-direction: row;
    margin-bottom: 32px;
`

const ProfilePhoto = styled.img`
    width: 48px;
    height: 48px;
    border-radius: 100px;
`

const ContentColumn = styled.span`
    max-width: calc(100% - 70px);
    flex-direction: column;
    min-height: 100%;
    justify-content: space-between;
`

const ContentText = styled.span`
    ${text(500, 14, 18, 'page-content')}
    white-space: break-spaces;
`

const DateText = styled.span`
    ${text(400, 14, 20, 'page-paragraph')}
    

`

const ActionsRow = styled.span`
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;

`

const EditButton = styled.button`
    ${text(500, 14, 16, 'touchstone-note-edit-button')}
    padding: 0;
    &:hover {
        cursor: pointer;
    }
`

const DeleteButton = styled.button`
    padding: 0;
    ${text(500, 14, 16, 'touchstone-note-delete-button')}
    &:hover {
        cursor: pointer;
    }
`