import { Curriculum, CurriculumItemMediaType, OnboardingChecklistItem } from "@haystackdev/cicf-content-api";
import { AllIcons, MediaTypeIcon } from "components/Icon";
import { timeInMilleseconds } from "constants/time";
import { ONBOARDING_ASSESSMENT_PATH, ONBOARDING_GENDER_IDENTITY_PATH, ONBOARDING_ORGANIZATION_PATH, ONBOARDING_PROFILE_PHOTO_PATH, ONBOARDING_RACIAL_IDENTITY_PATH, ONBOARDING_ZIP_PATH } from "features/Onboarding/OnboardingRoutes";

export function timestampToDateString(timestamp: number){
    return (new Date(timestamp)).toLocaleDateString('en-us', {
        month: 'numeric',
        year: 'numeric',
        day: '2-digit'
    })
}

export function textForMediaType(mediaType: CurriculumItemMediaType): string {
    switch(mediaType) {
        case 'article':
            return 'Online article';
        case 'podcast':
            return 'Online podcast';
        case 'video':
            return 'Online video';
        case 'in-app': 
            return 'Guidepost'
    }

}

export function iconForMediaType(mediaType: CurriculumItemMediaType): MediaTypeIcon{
    switch(mediaType) {
        case 'article':
            return 'mt-article-outline';
        case 'podcast':
            return 'mt-podcast-outline';
        case 'video':
            return 'mt-video-outline';
        case 'in-app':
            return 'mt-guidepost-outline';
    }
}

export function filledIconForMediaType(mediaType: CurriculumItemMediaType): MediaTypeIcon {
    switch(mediaType) {
        case 'article':
            return 'mt-article';
        case 'podcast':
            return 'mt-podcast';
        case 'video':
            return 'mt-video';
        case 'in-app':
            return 'mt-guidepost'
    }
}

export function getTouchstoneFromCurriculum(curriculum: Curriculum, touchstoneId: string) {
    for(var pathway of curriculum.pathways) {
        for(var item of pathway.items) {
            if(item.id===touchstoneId) return item;
        }
    }
    return;
}

export function datesAreSame(d1: Date, d2: Date) {
    return d1.getDate() == d2.getDate() && d1.getFullYear() == d2.getFullYear() && d1.getMonth() == d2.getMonth();
}

export function openLinkInNewTab(url: string) {
    const win = window.open(url);
    win?.focus();
}

export function pathForOnboardingChecklistItem(item: OnboardingChecklistItem) {
    switch(item.item) {
        case 'assessment':
            return ONBOARDING_ASSESSMENT_PATH;
        case 'ethnicity':
            return ONBOARDING_RACIAL_IDENTITY_PATH;
        case 'gender':
            return ONBOARDING_GENDER_IDENTITY_PATH;
        case 'organization':
            return ONBOARDING_ORGANIZATION_PATH;
        case 'profile-photo':
            return ONBOARDING_PROFILE_PHOTO_PATH;
        case 'zip-code':
            return ONBOARDING_ZIP_PATH;
    }
}

export function updateSet(set: Set<string>, v: string, add: boolean) {
    var newSet = new Set(set);
    if(add) newSet.add(v);
    else newSet.delete(v);
    return newSet;
}

export function minutesToHoursAndMinutes(minutes: number) {
    return {
        hours: Math.floor(minutes / 60),
        minutes: minutes % 60
    }
}