import CICFTextSpan from "components/CICFTextSpan";
import Icon, { IconWithTextIcon, MediaTypeIcon } from "components/Icon";
import Spacer from "components/Spacer";
import { HTMLAttributes } from "react";
import styled from "styled-components";
import { textColor } from "theme";


export default function IconWithText({
    icon,
    icon2,
    text,
    iconSize=16,
    textSize=[400, 14, 24],
    ...rest
}: {
    icon: IconWithTextIcon | MediaTypeIcon,
    icon2?: IconWithTextIcon,
    iconSize?: number,
    text: string,
    textSize?: [number, number, number]
} & HTMLAttributes<HTMLSpanElement>) {
    return (
        <Container
            {...rest}
        >
            <Icon
                icon={icon}
                size={iconSize}
            />
            {icon2 &&
                <>
                    <Spacer h={7}/>
                    <Icon
                        icon={icon2}
                        size={iconSize}
                    />
                </>

            }
            <Spacer
                h={7}
            />
            <CICFTextSpan
                sizing={textSize}
                color={textColor['icon-with-text']}
            >
                {text}
            </CICFTextSpan>
        </Container>
    )
}

const Container = styled.span`
    flex-direction: row;
    align-items: center;
    white-space: no-wrap;
    & span {
        white-space: no-wrap;
    }
`