import Card from "components/Card";
import Page from "components/Page";
import Spacer from "components/Spacer";
import React, { CSSProperties, ReactNode } from "react";
import { useNavigate } from "react-router-dom";
import styled from "styled-components";
import { text } from "styleUtilities";
import { HOME_PATH } from "topLevelRoutes";

export default function OnboardingPage({
    children,
    title,
    subtitle,

}: {
    children: ReactNode,
    title: string,
    subtitle?: string,

}) {
    const navigate = useNavigate();

    function onSkipPress() {
        navigate('/' + HOME_PATH)
    }

    return (
        <Container
        >
            <ContainerCard
                variant="primary"
            >
                <Header>
                    {title}
                </Header>
                {subtitle &&
                    <SubHeader>
                        {subtitle}
                    </SubHeader>
                }
                <Spacer v={32} />
                {children}

                <SkipButton
                    onClick={onSkipPress}
                >
                    Skip
                </SkipButton>

            </ContainerCard>
        </Container>
    )
}

const Container = styled(Page)`
    min-height: 100vh;
    display: flex;
`

const ContainerCard = styled(Card)`
    width: 560px;
    padding: 40px 50px;
    align-items: center;
    align-self: center;
    position: relative;
    overflow: visible;
`

const Header = styled.h1`
    ${text(600, 32, 44, 'card-title')}
    align-self: flex-start;
    max-width: 80%;
`
const SubHeader = styled.p`
    max-width: 80%;
    margin-top: 8px;
    align-self: flex-start;
    ${text(400, 16, 24, 'card-paragraph')}
`

const SkipButton = styled.button`
    ${text(400, 14, 24, 'onboarding-skip-button')}
    position: absolute;
    right: 50px;
    top: 40px;
    &:hover {
        cursor: pointer;
    }
`