import { useSavedEvents } from "@haystackdev/cicf-content-api";
import { EventsQueryData } from "@haystackdev/cicf-content-api/lib/queries/query/eventsQuery";
import { GetEventsResponse } from "@haystackdev/cicf-content-api/lib/requests/getEvents";
import { EventsContentState } from "features/Events/EventsPage/EventsContent";
import EventsSection from "features/Events/EventsPage/EventsSection";
import EventsSectionHeader from "features/Events/EventsPage/EventsSectionHeader";
import { useMemo, useState } from "react";
import styled from "styled-components";
import EventSectionMode from "types/EventSectionMode";


export default function EventsLeftColumn({
    eventsQueryData,
    eventsContentState,
    selectedEventDate,
}: {
    eventsQueryData: EventsQueryData,
    eventsContentState: EventsContentState,
    selectedEventDate: Date
}) {
    const events = useMemo(() => {
        const todaysDate = (new Date()).toDateString();
        return {
            today: todaysDate in eventsQueryData.eventsForDate? eventsQueryData.eventsForDate[todaysDate]:[],
            thisWeek: eventsQueryData.eventsThisWeek,
        };
    }, [eventsQueryData.events])

    const dateString = selectedEventDate.toDateString();
    const savedEvents = useSavedEvents();
    console.log("savedEvents: " + savedEvents);
    console.log("events: ")
    console.log(events);

    return (
        <Container>
            {eventsContentState == 'initial' &&
                <>
                    <EventsSection
                        events={events.today}
                        title="Today"
                    />
                    <EventsSection
                        events={events.thisWeek}
                        title="This Week"
                    />
                </>
            }
            {eventsContentState=='selected-date' && 
                <EventsSection 
                    title={dateString}
                    events={dateString in eventsQueryData.eventsForDate?eventsQueryData.eventsForDate[dateString]:[]}
                />
            }
            {eventsContentState=='saved-events'&&
                <EventsSection 
                    title={"Saved Events"}
                    events={savedEvents}
                />
            }

        </Container>
    )
}

const Container = styled.div`
    display: flex;
    flex-direction: column;
    align-self: stretch;
    flex: 2.18;
`