export default function LearningPathwayDetailTimeCard({
    timeInMinutes,
} : {
    timeInMinutes: number,
}) {
    const {hours, minutes} = minutesToHoursAndMinutes(timeInMinutes);

    return (
        <ContainerCard
            variant="primary"
            hideBorder={true}
        >
            <IconContainer>
                <Icon 
                    icon={'wt-time'}
                    size={28}
                />
            </IconContainer>
            <Spacer
                h={22}
            />
            <TextColumn>
                <NumberText>
                    {hours+' hours'}
                </NumberText>
                <NumberText>
                    {minutes+' minutes'}
                </NumberText>

            </TextColumn>
        </ContainerCard>
    )
}

