import { UserProfile, useUpdateUserProfileMutation } from "@haystackdev/cicf-content-api";
import Organization from "@haystackdev/cicf-content-api/lib/types/Organization";
import DeepPartial from "@haystackdev/cicf-content-api/lib/types/UtilDeepPartial";
import CICFButton from "components/CICFButton";
import IconWithText from "components/IconWithText";
import LearningProgressCard from "components/LearningProgressCard";
import ProfilePhoto from "components/ProfilePhoto";
import Spacer from "components/Spacer";
import { EthnicityCard } from "features/UserProfile/UserProfile/EthnicityCard";
import { GenderCard } from "features/UserProfile/UserProfile/GenderCard";
import InterestsCard from "features/UserProfile/UserProfile/InterestsCard";
import { OrganizationCard } from "features/UserProfile/UserProfile/OrganizationCard";
import PledgeSignatureCard from "features/UserProfile/UserProfile/PledgeSignatureCard";
import { useEffect, useState } from "react";
import styled from "styled-components";
import { text } from "styleUtilities";

export default function UserProfileContent({
    profile,
}: {
    profile: UserProfile
}) {
    const [updateInterests, setUpdateInterests] = useState<null | string[]>(null);
    const [updateRacialIdentitiesSet, setUpdateRacialIdentitiesSet] = useState<null | Set<string>>(null);
    const [updateGenderIdentity, setUpdateGenderIdentity] = useState<null | string>(null);
    const [updateAffiliatedOrganizationsSet, setUpdateAffiliatedOrganizationsSet] = useState<null | Set<string>>(null);

    const updateProfileMutation = useUpdateUserProfileMutation();

    const [isEditing, setIsEditing] = useState<boolean>(false);


    function onEditSaveClick() {
        if (isEditing) {
            var update: Partial<UserProfile> = {};
            update.demographics = {
                interests: [],
            };
            if (updateInterests !== null) update.demographics.interests = updateInterests;
            if (updateRacialIdentitiesSet !== null) update.demographics.ethnicities = Array.from(updateRacialIdentitiesSet);
            if (updateGenderIdentity !== null) update.demographics.gender = updateGenderIdentity;
            if (updateAffiliatedOrganizationsSet !== null) update.demographics.affiliatedOrganizations = Array.from(updateAffiliatedOrganizationsSet);
            updateProfileMutation.mutateAsync({
                ...profile,
                ...update,
                demographics: {
                    ...profile.demographics,
                    ...update.demographics
                }
            }).then(() => {
                setUpdateInterests(null);
                setUpdateRacialIdentitiesSet(null);
                setUpdateGenderIdentity(null);
                setUpdateAffiliatedOrganizationsSet(null);
                setIsEditing(false);
            }).catch(() => {
                alert(`Couldn't update profile, try again.`)
            })
        } else {
            setIsEditing(true);
        }
    }

    return (
        <>
            <ProfilePhoto
                size={165}
                url={profile.profileImageUrl}

            />
            <NameText>
                {`${profile.firstName} ${profile.lastName}`}
            </NameText>
            <Spacer
                v={24}
            />
            {(profile.address.city || profile.address.state) &&
                <>
                    <IconWithText
                        icon='wt-location'
                        text={`${profile.address.city}${profile.address.city && profile.address.state ? ', ' : ''}${profile.address.state?.name}`}
                        iconSize={20}
                        textSize={[400, 18, 24]}
                    />
                    <Spacer v={12} />
                </>
            }
            <IconWithText
                icon='wt-award'
                text={`Committed since ${(new Date(profile.commitedTimestamp)).getFullYear()}`}
                iconSize={20}
                textSize={[400, 18, 24]}
            />
            <CardsContainer>
                <InterestsCard
                    profile={profile}
                    editingInterests={updateInterests}
                    onInterestsChange={setUpdateInterests}
                    isEditing={isEditing}
                />
                <Spacer v={24} />
                <EthnicityCard
                    isEditing={isEditing}
                    editingEthnicitiesSet={updateRacialIdentitiesSet}
                    profile={profile}
                    onEthnicitiesChange={setUpdateRacialIdentitiesSet}
                />
                <Spacer v={24} />
                <GenderCard
                    isEditing={isEditing}
                    selectedGender={updateGenderIdentity}
                    profile={profile}
                    onChangeSelectedGender={setUpdateGenderIdentity}
                />
                <Spacer v={24} />
                <OrganizationCard
                    isEditing={isEditing}
                    editingOrganizations={updateAffiliatedOrganizationsSet}
                    profile={profile}
                    onOrganizationsChange={setUpdateAffiliatedOrganizationsSet}
                />
                {profile.signedPledge &&
                    <>
                        <Spacer v={24} />
                        <PledgeSignatureCard
                            pledgeSignature={profile.signedPledge}
                            name={`${profile.firstName} ${profile.lastName}`}
                        />
                    </>

                }
                <Spacer v={52} />
                <CICFButton
                    text={!isEditing ? 'Edit' : 'Save Changes'}
                    onClick={onEditSaveClick}
                    loading={updateProfileMutation.isLoading}
                    style={{minWidth: 120,}}
                />
            </CardsContainer>
            <Spacer v={100}/>
        </>
    )
}

const NameText = styled.h1`
  ${text(600, 32, 44, 'page-header')}
  margin-top: 24px;
`

const CardsContainer = styled.div`
    width: 554px;
    display: flex;
    flex-direction: column;
    align-items: stretch;
`