import { useState, useEffect, useRef } from "react";
import { useUserProfileQuery } from "@haystackdev/cicf-content-api";

// Images
import DownChevron from "assets/down-chevron.svg";
import ProfilePicPlaceholder from "assets/profilePic.png";
import useAuth from "../../state/authState";
import { routeDisplayName, protectedRoutesWithNav, PROFILE_PATH } from "topLevelRoutes";
import Logo from 'assets/nav-logo.svg';
import styled from "styled-components";
import { Link, useLocation } from "react-router-dom";
import Icon from "components/Icon";
import { fakeProfile } from "@haystackdev/cicf-content-api/lib/types/UserProfile";
import { dimensions } from "theme";
import UserDropdownMenu from "components/Navbar/UserDropdownMenu";
import { IS_TABLET_BREAKPOINT } from "constants/overlayBreakpoints";

const Navbar = () => {
  const {pathname} = useLocation();
  const [isOpen, setIsOpen] = useState(false);
  const isLoggedIn = useAuth((s)=>s.loggedIn);
  const logout = useAuth((s)=>s.logout)
  const toggling = () => setIsOpen(!isOpen);

  // Scroll to top any time the user navigates. Not sure where I should put this. 
  useEffect(()=>{
    window.scrollTo(0, 0,)
  }, [pathname])

  return (
    <div style={{
      width: '100vw',

      height: dimensions.navbarHeight
    }}>
      <Nav>
        <NavLink
          style={{
            color: "#fafafa",
            display: "flex",
            alignItems: "center",
            textDecoration: "none",
            cursor: "pointer",
            marginLeft: 90,
            fontSize: "1rem",
          }}
          to="/"
        >
          <LogoImage
            src={Logo}

          />
        </NavLink>
        {isLoggedIn ? (
          <NavMenu style={{ marginRight: 0 }}>
            {Object.keys(protectedRoutesWithNav).map((routeName)=>{
              return (
                <NavLink 
                  to={'/'+routeName}
                  key={routeName}
                >
                  {routeDisplayName(routeName)}
                </NavLink>
              )
            })}
            <UserDropdownMenu />
          </NavMenu>
        ) : (
          <NavMenu style={{ marginRight: 90 }}>
            <NavLink
              style={{
                display: "flex",
                alignItems: "center",
                textDecoration: "none",
                cursor: "pointer",
                fontSize: "1rem",
              }}
              to="/create-account"
            >
              Sign Up
            </NavLink>
            <NavLink
              style={{
                display: "flex",
                alignItems: "center",
                textDecoration: "none",
                cursor: "pointer",
                fontSize: "1rem",
              }}
              to="/login"
            >
              Log In
            </NavLink>
          </NavMenu>
        )}
      </Nav>
    </div>
  );
};



export const Nav = styled.nav`
  background: #fff;
  height: ${dimensions.navbarHeight}px;
  width: 100%;
  z-index: 10000;
  position: absolute;
  display: flex;
  justify-content: space-between;
  white-space: no-wrap;
  align-self: stretch;
  filter: drop-shadow(0px 6px 12px rgba(0, 0, 0, 0.06));
  @media (max-width: ${IS_TABLET_BREAKPOINT}px) {
    display: none;
  }
`;

export const NavLink = styled(Link)`
    color: black;
    display: flex;
    alignItems: center;
    text-decoration: none;
    cursor: pointer;
    font-size: 1rem;
    margin-right: 24px;
`;

export const NavMenu = styled.div`
  display: flex;
  align-items: center;
  justify-content: flex-end;
`;

export const LogoImage = styled.img`
  width: 85px;
  height: 64px;
`

export default Navbar;
