import {default as PledgeSignatureType} from "@haystackdev/cicf-content-api/lib/types/PledgeSignature";
import styled from "styled-components";
import { text } from "styleUtilities";
import { borderColors } from "theme";


export default function PledgeSignature({
    signature,
    name
} : {
    signature?: PledgeSignatureType,
    name?: string,
}) {
    return (
        <Container>
            <PledgeText>
                {signature?name:` `}
            </PledgeText>
            <SignedDateText>
                {signature && name? `Signed on ${(new Date(signature.timestamp)).toLocaleDateString('en-us', {
                    month: 'long',
                    day: 'numeric',
                    'year': 'numeric'
                })} by ${name}`:''}
            </SignedDateText>
        </Container>
    )
}

const Container = styled.div`
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    align-self: stretch;
`

const PledgeText = styled.span`
    font-family: "SignPainter";
    ${text(400, 44, 44, 'pledge-signature')}
    height: 44px;
    border-bottom-style: solid;
    border-bottom-width: 1px;
    border-bottom-color: ${borderColors['pledge-signature-underline']};
    width: 382px;
    padding-bottom: 10px;

`

const SignedDateText = styled.span`
    ${text(400, 12, 14, 'pledge-signature')}
    margin-top: 12px;
    height: 15px;

`