import { useResendChangeEmailVerificationMutation } from "@haystackdev/cicf-content-api";
import { CardCentered } from "components/Card";
import CICFButton from "components/CICFButton";
import EmailGraphic from "components/Graphics/EmailGraphic";
import Page from "components/Page";
import Spacer from "components/Spacer";
import { useParams } from "react-router-dom";
import styled from "styled-components";
import { text } from "styleUtilities";
import ChangeEmailConfirmUrlParameters from "types/UrlParameters/ChangeEmailConfirmUrlParameters";

export default function ChangeEmailConfirm() {
    const params = useParams<ChangeEmailConfirmUrlParameters>();
    const resendChangeEmail = useResendChangeEmailVerificationMutation();

    function onResendClick() {
        resendChangeEmail.mutateAsync().then(()=>{
            alert('Verification email resent.')
        })
    }

    return (
        <Page
            style={{alignItems: 'center',}}
        >
            <CardCentered
                variant="primary"
            >
                <Title>
                    Check Your Email
                </Title>
                <Subtitle>
                    {`We've sent a confirmation email to ${params.email}`}
                </Subtitle>
                <Spacer v={24}/>
                <EmailGraphic />
                <Spacer v={32}/>
                <CheckSpamText>
                    Didn't receive an email? Check your spam folder.
                </CheckSpamText>
                <Spacer v={24}/>
                <CICFButton 
                    text="Resend Email"
                    onClick={onResendClick}
                    loading={resendChangeEmail.isLoading}
                    variant="outline"
                    hideShadow={true}
                />
            </CardCentered>
        </Page>
    )
}

const Title = styled.h1`
    ${text(600, 32, 44, 'card-title')}

`

const Subtitle = styled.p`
    ${text(400, 16, 24, 'card-paragraph')}
    margin-top: 8px;
    max-width: 70%;
    text-align: center;
`

const CheckSpamText = styled.p`
    ${text(400, 14, 24, 'card-paragraph')}
`