import { Event } from "@haystackdev/cicf-content-api";
import EventType from "@haystackdev/cicf-content-api/lib/types/EventType";
import IconWithText from "components/IconWithText";
import { IconWithTextIcon } from "components/Icon";

export default function EventLocationIconWithText({
    event,
}: {
    event: Event
}) {
    
    return (
        <IconWithText
            {...locationIconsForEvent(event)}
            text={displayTextForEvent(event)}
        />
    )
}

function locationIconsForEvent(event: Event): {
    icon: IconWithTextIcon,
    icon2?: IconWithTextIcon,
} {
    var icon: IconWithTextIcon;
    var icon2: undefined | IconWithTextIcon = undefined;
    switch(event.eventType) {
        case 'hybrid':
            icon = 'wt-location';
            icon2 = 'wt-online';
            break;
        case 'in-person':
            icon = 'wt-location';
            break;
        case 'online':
            icon = 'wt-online';
            break;
    }
    return {
        icon,
        icon2,
    }
}

function displayTextForEvent(event: Event) {
    switch(event.eventType) {
        case 'hybrid':
            return 'Online + In Person';
        case 'online':
            return 'Online';
        case 'in-person':
            return event.location!;
    }
}