import { useState, useEffect } from "react";


//Imports
import CheckEmail from "../CheckEmail/index";
import { contentCopy, queryClient, useLoginMutation } from "@haystackdev/cicf-content-api";

//Images

import Lock from "assets/lock.svg";
import EyeSlash from "assets/eye-off.svg";
import Eye from "assets/eye.svg";

import styled from "styled-components";
import useAuth from "../../../state/authState";
import Page from "components/Page";
import CICFInput from "components/CICFInput";
import CICFButton from "components/CICFButton";
import { text } from "styleUtilities";
import Spacer from "components/Spacer";
import { useNavigate } from "react-router-dom";
import { CONFIRM_EMAIL_BASE_PATH, CONFIRM_EMAIL_PATH, PASSWORD_RESET_PATH } from "topLevelRoutes";
import { Link } from "react-router-dom";
import ShimmerPlaceholder from "components/ShimmerPlaceholder";
import MVMTShimmer from "components/MVMTShimmer/MVMTShimmer";

const Login = () => {
  const [email, setEmail] = useState<string>('');
  const [password, setPassword] = useState<string>('');
  const [emailValid, setEmailValid] = useState<boolean>(false);
  const [passwordValid, setPasswordValid] = useState<boolean>(false);


  const [rememberMe, setRememberMe] = useState<boolean>(false);
  const [errorMessage, setErrorMessage] = useState<string>('');


  const loginMutation = useLoginMutation();
  const navigate = useNavigate();
  const loginAppState = useAuth((s) => s.login);

  useEffect(()=>{
    queryClient.clear();
  }, [])

  const inputValid = emailValid && passwordValid;

  function onLoginClick() {


    loginMutation.mutateAsync({
      email,
      password,
      clientType: 'web',
    }).then((response) => {
      if (!response.success) {
        console.log("Login response: ")
        console.log(response)
        if (response.profileNeedsVerification) {
          navigate('/' + CONFIRM_EMAIL_BASE_PATH + '/' + email)
        } else {
          setErrorMessage(response.message?response.message:`Couldn't login, try again.`)
        }
      }
      if (response.success) {

        loginAppState(response.token!, response.refreshToken!)

      }
    });
  }

  return (
    <Page>
      <Container>
        <MVMTShimmer />
        <ContainerHeader>
          Log In
        </ContainerHeader>
        <ContainerText>
          {contentCopy.authorization.login.message}
        </ContainerText>


        <CICFInput
          value={email}
          onChange={setEmail}
          placeholder='Email'
          icon='mail-dark'
          setValid={setEmailValid}
          errorMessage={errorMessage}
          validationType='email'
        />
        <Spacer v={20} />
        <CICFInput
          value={password}
          forceVisibleButton={true}
          icon='lock'
          onChange={setPassword}
          placeholder={"Password"}
          validationType='password'
          type='password'
          setValid={setPasswordValid}
        />
        <Spacer v={25} />
        <RememberContainer>
          <RememberText>
            <input
              type="checkbox"
              style={{
                height: "16px",
                width: "16px",
              }}
              checked={rememberMe}
              onChange={(e) => setRememberMe(e.target.checked)}
            ></input>
            <Spacer h={8} />
            <label>Remember me</label>
          </RememberText>
          <ForgotPasswordText>
            <Link
              to={'/' + PASSWORD_RESET_PATH}
              style={{ textDecoration: "none", color: "#ff7c86" }}
            >
              Forgot password?
            </Link>
          </ForgotPasswordText>
        </RememberContainer>
        <Spacer v={60} />
        <CICFButton
          type="submit"
          disabled={!inputValid}
          variant={inputValid ? 'primary' : 'disabled'}
          text='Log In'
          onClick={onLoginClick}
          loading={loginMutation.isLoading}
        />
        <Spacer v={20} />
        <SignUpContainer>
          <SignUpText>
            <p>
              Don't have an account?
              <Link
                to={'/create-account'}
                style={{
                  textDecoration: "none",
                  color: "#211d3a",
                }}
              >
                <strong> Sign up here.</strong>{" "}
              </Link>
            </p>
          </SignUpText>
        </SignUpContainer>
      </Container>
    </Page>
  );
};

export const Container = styled.div`
  width: 560px;
  background: #ffffff;
  border: 1px solid #e6e8ec;
  box-sizing: border-box;
  box-shadow: 0px 6px 24px -6px rgba(0, 0, 0, 0.1);
  border-radius: 20px;
  padding: 40px;
  position: relative;
  margin: auto;
  display: flex;
  flex-direction: column;
`;

export const ContainerHeader = styled.h1`
  display: flex;
  color: #211d3a;
  font-size: 1.125em;
  flex: none;
  order: 0;
  flex-grow: 0;
  font-family: Inter;
  font-style: normal;
  font-weight: 600;
  line-height: 44px;
  margin-bottom: 8px;
  ${text(600, 32, 44, 'card-title')}
`;

export const ContainerText = styled.p`
  display: flex;
  color: #474554;
  font-size: 1em;
  flex: none;
  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  line-height: 24px;
  margin-bottom: 50px;
`;


export const EmailContainer = styled.div`

  height: 48px;
  background: #ffffff;
  border: 1px solid #e6e8ec;
  box-sizing: border-box;
  border-radius: 10px;
  display: flex;
  margin-top: 50px;
`;

export const PasswordContainer = styled.div`
  height: 48px;
  background: #ffffff;
  border: 1px solid #e6e8ec;
  box-sizing: border-box;
  border-radius: 10px;
  display: flex;
  margin-top: 120px;
`;

export const RememberContainer = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-self: stretch;
`;

export const RememberText = styled.p`
  color: #474554;
  font-size: 12px;
  display: flex;
  flex-direction: row;
  align-items: center;
`;

export const SignUpContainer = styled.div`
  position: relative;
  color: #b9b9b9;
  font-size: 14px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
`;

export const SignUpText = styled.span`
  line-height: 24px;
  text-align: center;
`;

export const ForgotPasswordText = styled.p`
  font-size: 14px;
  font-weight: 700;
  display: flex;
`;
export default Login;
