import { useUserProfileQuery, useAsssessmentResultsQuery, usePreviousAssessmentsQuery } from "@haystackdev/cicf-content-api";
import Card from "components/Card";
import QuerySuspended from "components/QuerySuspended";
import AssessmentThankYouContent from "features/Assessment/AssessmentThankYou/AssessmentThankYouContent";
import styled from "styled-components";


export default function AssessmentThankYou() {
    const previousAssessmentsQuery = usePreviousAssessmentsQuery();


    return (
        <QuerySuspended
            queries={[previousAssessmentsQuery]}
        >
            {previousAssessmentsQuery.data &&

                    <AssessmentThankYouContent
                        previousAssessments={previousAssessmentsQuery.data}
                    />

            }
        </QuerySuspended>
    )
}

