import { UserProfile } from "@haystackdev/cicf-content-api";
import { createOnboardingChecklistFromProfile } from "@haystackdev/cicf-content-api/lib/types/OnboardingChecklist";
import OnboardingAssessment from "features/Onboarding/Pages/OnboardingAssessment";
import { OnboardingCompletePage } from "features/Onboarding/Pages/OnboardingComplete";
import OnboardingGenderIdentity from "features/Onboarding/Pages/OnboardingGenderIdentity";
import OnboardingOrganization from "features/Onboarding/Pages/OnboardingOrganization";
import OnboardingProfilePhoto from "features/Onboarding/Pages/OnboardingProfilePhoto";
import OnboardingRacialIdentity from "features/Onboarding/Pages/OnboardingRacialIdentity";
import OnboardingZipCode from "features/Onboarding/Pages/OnboardingZipCode";
import { pathForOnboardingChecklistItem } from "helpers";
import React from "react";
import { Route, Routes, useNavigate } from "react-router-dom";
import { ONBOARDING_PATH } from "topLevelRoutes";
import RouteMap from "types/RouteMap";

export const ONBOARDING_PROFILE_PHOTO_PATH = 'profile-photo';
export const ONBOARDING_ZIP_PATH = 'zip';
export const ONBOARDING_RACIAL_IDENTITY_PATH = 'racial-identity';
export const ONBOARDING_GENDER_IDENTITY_PATH = 'gender-identity';
export const ONBOARDING_ORGANIZATION_PATH = 'organization';
export const ONBOARDING_ASSESSMENT_PATH = 'assessment';
export const ONBOARDING_COMPLETE = 'complete'

const onboardingRouteMap: RouteMap<string> = {
    [ONBOARDING_PROFILE_PHOTO_PATH]: OnboardingProfilePhoto,
    [ONBOARDING_ZIP_PATH]: OnboardingZipCode,
    [ONBOARDING_RACIAL_IDENTITY_PATH]: OnboardingRacialIdentity,
    [ONBOARDING_GENDER_IDENTITY_PATH]: OnboardingGenderIdentity,
    [ONBOARDING_ORGANIZATION_PATH]: OnboardingOrganization,
    [ONBOARDING_ASSESSMENT_PATH]: OnboardingAssessment,
    [ONBOARDING_COMPLETE]: OnboardingCompletePage,
}

export default function OnboardingRoutes() {
    return (
        <Routes>
            {Object.keys(onboardingRouteMap).map((e) => {
                const C = onboardingRouteMap[e as keyof typeof onboardingRouteMap]
                return (
                    <Route
                        path={e}
                        key={e}
                        element={<C />}
                    />
                )
            })}
        </Routes>
    )
}