import { useCurriculumQuery } from "@haystackdev/cicf-content-api";
import QuerySuspended from "components/QuerySuspended";
import { getTouchstoneFromCurriculum } from "helpers";
import TouchstoneDetailContent from "features/Learning/TouchstoneDetail/TouchstoneDetailContent";
import { useMemo } from "react";
import { useParams } from "react-router-dom";
import TouchstoneDetailUrlParameters from "types/UrlParameters/TouchstoneDetailUrlParameters";
import Page from "components/Page";

export default function TouchstoneDetail() {
    const params = useParams<TouchstoneDetailUrlParameters>();

    const curriculumQuery = useCurriculumQuery();

    const touchstone = useMemo(() => {
        if (curriculumQuery.data && params.touchstoneId)
            return getTouchstoneFromCurriculum(curriculumQuery.data.curriculum, params.touchstoneId);
    }, [params.touchstoneId, curriculumQuery.data])

    return (
        <Page>
            <QuerySuspended
                queries={[curriculumQuery]}
            >
                {curriculumQuery.isFetched && curriculumQuery.data &&
                    (touchstone ?
                        <TouchstoneDetailContent
                            touchstone={touchstone}
                            learningPathwayName={touchstone.learningPathwayId in curriculumQuery.data.pathwayNamesMap?curriculumQuery.data.pathwayNamesMap[touchstone.learningPathwayId]:'Learning Pathway'}
                        />
                        :
                        <p>Touchstone not found.</p>
                    )
                }
            </QuerySuspended>
        </Page>
    );
}