import { EventsQueryData } from "@haystackdev/cicf-content-api/lib/queries/query/eventsQuery";
import Card from "components/Card";
import CalendarBody from "features/Events/EventsPage/Calendar/CalendarBody";
import EventCalendarHeader from "features/Events/EventsPage/Calendar/EventCalendarHeader";
import styled from "styled-components";
import Month from "types/Month";

export const DAYS_PADDING_HORIZONTAL = 18;

export default function Calendar({
    queryData,
    selectedDate,
    onChangeDate,
    month,
    onChangeMonth,
} : {
    queryData: EventsQueryData,
    selectedDate: Date,
    month: Month,
    onChangeMonth: (v: Month)=>void,
    onChangeDate: (d: Date)=>void,
}) {
    function headerLeftPress() {
        const newDate = new Date(month.year, month.month);
        newDate.setMonth(month.month - 1);
        onChangeMonth({
            month: newDate.getMonth(),
            year: newDate.getFullYear(),
        })
    }

    function headerRightPress() {
        const newDate = new Date(month.year, month.month);
        newDate.setMonth(month.month + 1);
        onChangeMonth({
            month: newDate.getMonth(),
            year: newDate.getFullYear(),
        })
    }

    return (
        <Container
            variant="primary"
        >
            <EventCalendarHeader 
                month={month}
                onLeftPress={headerLeftPress}
                onRightPress={headerRightPress}
            />
            <CalendarBody 
                month={month}
                selectedDay={selectedDate}
                onDayChange={onChangeDate}
                queryData={queryData}
            />
        </Container>
    )
}

const Container = styled(Card)`
    padding: 0;
`