import { ASSESSMENT_RESULTS_PATH, ASSESSMENT_THANK_YOU_PATH } from "features/Assessment/AssessmentRoutes";
import QuerySuspended from "components/QuerySuspended";

type QuestionCardPosition = 'next' | 'later-than-next' | 'answered' | 'show';

const BAR_HEIGHT = 7;


const SMALL_CARD_SIZE = 0.86544;
const VERY_SMALL_CARD_SIZE = 0.75535;

function ProgressBar({ current, max }: { current: number, max: number, }) {
  return (
    <ProgressContainer
      style={{
        flexDirection: "row",
        alignItems: "center",
        justifyContent: "center",
        position: 'relative',
      }}
    >
      <ProgressBarContainer
        style={{
          width: '90%',
          height: BAR_HEIGHT,
          backgroundColor: "#E6E8EC",
          borderRadius: "100px",
        }}
      >
        <ProgressFilled
          style={{
            width: `calc(100% * ${current} / ${max})`,
            // width: (1066 * current) / max,
            height: "100%",
            backgroundColor: "#FEDB2A",
            borderRadius: "100px",
          }}
        />
      </ProgressBarContainer>
      <Spacer h={30} />
      <ProgressText
        style={{
          display: "flex",
          alignItems: "center",
          justifyContent: "space-between",
          alignSelf: 'center',
        }}
      >
        <b>{`${current}`}</b>
        {`/${max}`}
      </ProgressText>
    </ProgressContainer>
  );
}

function sizeForPosition(position: QuestionCardPosition) {
  return position === "later-than-next"
    ? VERY_SMALL_CARD_SIZE
    : position === "next"
      ? SMALL_CARD_SIZE
      : 1.0;
}

const MOVE_CARD_X = -1500;

function xForPosition(position: QuestionCardPosition) {
  return position === "answered" ? MOVE_CARD_X : 0;
}

function yForPosition(position: QuestionCardPosition) {
  const distance =
    position === "later-than-next" ? 110 : position === "next" ? 60 : 0;
  return distance;
}

const SLIDE_CARD_UP_TIME = 450;
const SLIDE_CARD_AWAY_TIME = 700;

function QuestionCard({
  position,
  question,
  i,
  onChangeAnswer,
  selectedAnswer,
}: {
  position: QuestionCardPosition,
  question: Question,
  i: number,
  onChangeAnswer: (v: number) => void,
  selectedAnswer: number,
}) {
  const previousPosition = useRef(position);
  const [animatedSize, setAnimatedSize] = useState(sizeForPosition(position));
  const [animatedY, setAnimatedY] = useState(yForPosition(position));
  const [animatedX, setAnimatedX] = useState(0.0);

  useEffect(() => {
    if (position === previousPosition.current) return;
    const newSize = sizeForPosition(position);
    const newX = xForPosition(position);
    const newY = yForPosition(position);
    setAnimatedSize(newSize);
    setAnimatedY(newY);
    setAnimatedX(newX);

    previousPosition.current = position;
  }, [position]);

  return (
    <div
      style={{
        transform: `translateX(${animatedX}px) translateY(${animatedY}px) scale(${animatedSize})`,
        transition: "1s all",
        width: "559px",
        height: "600px",
        backgroundColor: "white",
        position: "absolute",
        border: "1px solid #e6e8ec",
        zIndex: 10000 - i,
        top: 0,
        justifyContent: "center",
        margin: "auto",
        boxSizing: "border-box",
        pointerEvents: (position === "show" ? "auto" : "none"),
        boxShadow:
          position == "show" || position == "answered"
            ? "0px 6px 24px -6px rgba(0, 0, 0, 0.1)"
            : undefined,
        borderRadius: "20px",
        padding: "30px 40px 39.64px 40px",
      }}

    >
      <Text
        style={{
          textAlign: "center",
          fontSize: 24,
          fontWeight: 600,
          marginTop: 10,
          marginBottom: 30,
        }}
      >{`Question ${i + 1}`}</Text>
      <Container>
        <Text
          style={{
            textAlign: "center",
            fontStyle: "italic",
            marginTop: 0,
            marginBottom: 24,
            backgroundColor: "#FFF8D4",
            boxShadow: "0px 7px 10px rgba(241, 241, 255, 0.1)",
            borderRadius: 10,
            padding: "16px 20px",
          }}
        >
          {question.text}
        </Text>
      </Container>
      <Container
        style={{
          display: "flex",
          flexDirection: "column",
        }}
      >
        {question.answers.map((a, i) => {
          return (
            <Container
              key={'q' + i}
            >
              <Text
                style={{
                  border: "1px solid #E6E8EC",
                  borderRadius: 10,
                  borderColor: selectedAnswer == i ? "#FEDB2A" : "#E6E8EC",
                  boxSizing: "border-box",
                  width: 479,
                  height: 50,
                  justifyContent: "center",
                  alignItems: "center",
                  display: "flex",
                  fontSize: 16,
                  color: "#211D3A",
                  fontWeight: 500,
                  cursor: "pointer",
                  marginTop: 10,
                  marginBottom: 10,
                }}
                key={"" + i}
                onClick={() => onChangeAnswer(i)}
              >
                {a}
              </Text>
            </Container>
          );
        })}
      </Container>
    </div>
  );
}

function positionForIndex(i: number, currentQuestion: number) {
  if (i == currentQuestion) return "show";
  if (i < currentQuestion) {
    return "answered";
  }
  if (i == currentQuestion + 1) return "next";

  return "later-than-next";
}

function makeResults(selectedAnswers: number[], assessment: Assessment) {
  console.log(Date);
  return {
    assessment,
    answers: selectedAnswers.map((n, i) => {
      return {
        selectedAnswerIndex: n,
        questionId: assessment.questions[i].id,
      };
    }),
    finishedTimestamp: new Date().getTime(),
  };
}

const TakeAssessmentPage = () => {
  const assessmentQuery = useAssessmentQuery();
  const profileQuery = useUserProfileQuery();
  const assessmentResultsMutation = useAssessmentResultsMutation();
  const [currentQuestion, setCurrentQuestion] = useState(0);
  const [selectedAnswers, setSelectedAnswers] = useState<number[]>([]);
  const navigate = useNavigate();


  if (!assessmentQuery.isFetched && !profileQuery.isFetched) return <LoadingScreen />;
  const assessment = assessmentQuery.data;

  function onSubmitClick() {

    const assessment = assessmentQuery.data!
    const newAssessmentResults = selectedAnswers.map((e, i) => {
      return {
        questionId: assessment.questions[i].id,
        selectedAnswerIndex: e
      } as AssessmentAnswer
    });

    assessmentResultsMutation.mutateAsync({
      assessment,
      answers: newAssessmentResults,
      finishedTimestamp: (new Date()).getTime(),
    }).then(() => {
      navigate('/' + ASSESSMENT_PATH + '/' + ASSESSMENT_THANK_YOU_PATH)
    })
  }

  function onBackClick() {
    setCurrentQuestion(old => old - 1)
  }

  return (
    <Container style={{
      paddingTop: 60,
    }}>
      <QuerySuspended
        queries={[assessmentQuery]}
      >
        {assessment &&
          <>
            <ProgressBar
              current={currentQuestion}
              max={assessment.questions.length}
            />
            <Title>

              Implicit Bias Assessment

            </Title>
            <QuestionCardContainer>
              <SubmitResultsContainer
                show={currentQuestion === assessment.questions.length}
              >
                {assessmentResultsMutation.isLoading ?
                  <LoadingIndicator />
                  :
                  <>

                    <SubmitResultsText >
                      Assessment Complete
                    </SubmitResultsText>
                    <Spacer v={32} />
                    <Button
                      text="Submit"
                      onClick={onSubmitClick}
                      variant="primary"

                    />
                    <Spacer v={20} />
                    <Button
                      text="Back"
                      onClick={onBackClick}
                      variant="outline"

                    />
                  </>
                }

              </SubmitResultsContainer>
              {assessment.questions.map((question, i) => {
                return (
                  <QuestionCard
                    i={i}
                    position={positionForIndex(i, currentQuestion)}
                    key={"" + i}
                    question={question}
                    selectedAnswer={selectedAnswers[i]}
                    onChangeAnswer={(n) => {
                      setSelectedAnswers((old) => {
                        var r = [...old];
                        r[i] = n;
                        return r;
                      });
                      setCurrentQuestion(currentQuestion + 1)
                    }}
                  />
                );
              })}
              {currentQuestion > 0 && currentQuestion !== assessment.questions.length ? (
                <button
                  style={{
                    cursor: "pointer",
                    color: "#211D3A",
                    fontSize: 18,
                    fontWeight: 600,
                    position: 'absolute',
                    top: 20,
                    zIndex: 100000000000,
                    left: 20,
                  }}
                  onClick={() => {
                    setCurrentQuestion((c) => c - 1);
                  }}
                >
                  Back
                </button>
              ) : null}
            </QuestionCardContainer>
          </>

        }

      </QuerySuspended>
    </Container>
  );
};

