import Icon from "components/Icon";
import React, { ReactNode } from "react";
import useModalStore from "state/modals";
import styled from "styled-components";
import { border, text } from "styleUtilities";
import { backgroundColor } from "theme";

export default function ModalBase({
    children,
    title,
}: {
    children: ReactNode,
    title?: string,
}) {
    const close = useModalStore((s) => s.hidePresentingModal)

    function onClosePress() {
        close();
    }

    return (
        <Container>
            <CloseButton
                onClick={onClosePress}
            >
                <Icon
                    size={24}
                    icon='x'
                />
            </CloseButton>
            {title &&
                <Title>
                    {title}
                </Title>
            }
            {children}
        </Container>
    )
}

const Container = styled.div`
    width: 560px;
    display: inline-flex;
    flex-direction: column;
    align-items: stretch;
    padding: 40px;
    position: relative;
    background-color: ${backgroundColor['modal']};
    ${border({
    radius: 20,
    width: 1,
    color: 'modal',
})}
`

const CloseButton = styled.button`
    width: 24px;
    height: 24px;
    position: absolute;
    top: 30px;
    right: 30px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    border: 0;
    background-color: inherit;
`

const Title = styled.h3`
    margin-bottom: 16px;
    ${text(600, 18, 32, 'modal-text')}
`