
import { useDropzone } from 'react-dropzone';
import { borderColors } from 'theme';
import fileToBase64String from 'utility/fileToBase64String';

export default function UploadImageDragAndDrop({
    onFileChange,
    imageUrl,
    onBase64Change,
    size = {
        width: 350,
        height: 350,
    }
}: {
    onFileChange: (file: File, url: string) => void,
    imageUrl: string | null,
    size?: {
        width: string | number,
        height: string | number,
    },
    onBase64Change: (base64String: string, fileType: 'image/jpeg' | 'image/png')=>void,
}) {

    const { getRootProps, getInputProps } = useDropzone({
        onDrop: (e) => {
            if (e.length) {
                
                onFileChange(e[0], URL.createObjectURL(e[0]));
                fileToBase64String(e[0]).then((s)=>{
                    onBase64Change(s as string, e[0].type! as 'image/png' | 'image/jpeg')
                })
            }
                
        },
        multiple: false,
        accept: "image/png, image/jpeg"
    })
    return (
        <>

            {
                imageUrl !== null &&
                <img
                    className={'pressable'}
                    {...getRootProps()}
                    style={{
                        ...size,
                        aspectRatio: '1',
                        objectFit: 'cover',
                        display: imageUrl !== null ? 'flex' : 'none',
                        borderRadius: 3,
                        borderWidth: 1,
                        marginTop: 20,
                        marginBottom: 20,
                        borderStyle: 'solid',
                        borderColor: borderColors['modal']
                    }}
                    src={imageUrl}
                />
            }
            <div
                style={{
                    display: imageUrl === null ? 'flex' : 'none',
                    ...size,
                    borderWidth: 1,
                    borderColor: '#F9F9FB',
                    borderStyle: 'dashed',
                    border: '2px dashed #B9B9B9',
                    justifyContent: 'center',
                    alignItems: 'center',
                    borderRadius: 10,
                    position: 'relative',
                    backgroundColor: '#F9F9FB',
                    marginTop: 20,
                    marginBottom: 20,
                }}
                className={'pressable'}
                {...getRootProps()}
            >
                <input
                    {...getInputProps()}
                />
                <span style={{
                    width: 168,
                    fontSize: 14,
                    lineHeight: '24px',
                    color: '#999999',
                    textAlign: 'center',
                    display: 'inline'
                }}>
                    Drag and drop an image or <span style={{
                        color: '#0076FF'
                    }}>choose a file</span>. (png or jpeg)
                </span>
            </div>
        </>
    )
}