import { UserProfile } from "@haystackdev/cicf-content-api";
import OnboardingChecklist, { createOnboardingChecklistFromProfile } from "@haystackdev/cicf-content-api/lib/types/OnboardingChecklist";
import { ONBOARDING_COMPLETE } from "features/Onboarding/OnboardingRoutes";
import { pathForOnboardingChecklistItem } from "helpers";
import { HOME_PATH, ONBOARDING_PATH } from "topLevelRoutes";

export function nextOnboardingPathForChecklist(onboardingChecklist: OnboardingChecklist, checkProfileComplete:boolean=false) {
    if (onboardingChecklist.nextItem) {
        if(checkProfileComplete && onboardingChecklist.profileIsComplete) return (`/${ONBOARDING_PATH}/${ONBOARDING_COMPLETE}`)
        return (`/${ONBOARDING_PATH}/${pathForOnboardingChecklistItem(onboardingChecklist.nextItem)}`);
    } else {
        return (`/${HOME_PATH}`);
    }
}

export function nextOnboardingPath(checkProfileComplete=false) {
    const onboardingChecklist = createOnboardingChecklistFromProfile();
    return nextOnboardingPathForChecklist(onboardingChecklist, checkProfileComplete);
}