import { contentCopy, useUpdateUserProfileMutation, useUserProfileQuery } from "@haystackdev/cicf-content-api";
import CICFButton from "components/CICFButton";
import OrganizationInput from "components/OrganizationInput";
import Spacer from "components/Spacer";
import { ONBOARDING_BUTTON_TEXT } from "constants/onboardingText";
import { nextOnboardingPath } from "features/Onboarding/onboardingHelpers";
import OnboardingPage from "features/Onboarding/Pages/OnboardingPage";
import React, { useState } from "react";
import { useNavigate } from "react-router-dom";

export default function OnboardingOrganization() {
    const [organizations, setOrganizations] = useState<Set<string>>(new Set());
    const navigate = useNavigate();

    const profile = useUserProfileQuery();
    const updateProfileMutation = useUpdateUserProfileMutation();

    function onSavePress() {
        if(!profile) return;
        updateProfileMutation.mutateAsync({
            ...profile.data!,
            demographics: {
                ...profile.data!.demographics,
                affiliatedOrganizations: Array.from(organizations).map((e)=>e)
            }
        }).then((p)=>{
            navigate(nextOnboardingPath(true));
        })
    }

    return (
        <OnboardingPage
            title="Affiliated Organization"
            subtitle={contentCopy.onboarding.addOrganizations.message}
        >
            <OrganizationInput
                selectedOrganizationsSet={organizations}
                onChange={setOrganizations}
            />
            <Spacer
                v={40}
            />
            <CICFButton 
                text={ONBOARDING_BUTTON_TEXT}
                onClick={onSavePress}
                loading={updateProfileMutation.isLoading}
            />
        </OnboardingPage>
    )
}