import { contentCopy, useUpdateUserProfileMutation, useUserProfileQuery } from "@haystackdev/cicf-content-api";
import CICFButton from "components/CICFButton";
import CICFInput from "components/CICFInput";
import QuerySuspended from "components/QuerySuspended";
import Spacer from "components/Spacer";
import { ONBOARDING_BUTTON_TEXT } from "constants/onboardingText";
import { nextOnboardingPath } from "features/Onboarding/onboardingHelpers";
import OnboardingPage from "features/Onboarding/Pages/OnboardingPage";
import React, { useState } from "react";
import { useNavigate } from "react-router-dom";

export default function OnboardingZipCode() {
    const [zip, setZip] = useState<string>('');
    const updateProfileMutation = useUpdateUserProfileMutation();
    const userProfileQuery = useUserProfileQuery();
    const nav = useNavigate();

    const zipValid = zip.length == 5;

    function onSaveClick() {
        updateProfileMutation.mutateAsync({
            ...userProfileQuery.data!,
            address: {
                ...userProfileQuery.data!.address,
                zip,
            }
        }).then((o)=>{
            nav(nextOnboardingPath(true))
        });
    }

    return (
        <OnboardingPage
            title="Enter your Zip Code"
            subtitle={contentCopy.onboarding.zip.message}
        >
            <QuerySuspended
                queries={[userProfileQuery]}
            >
                <CICFInput
                    value={zip}
                    onChange={setZip}
                    placeholder='Zip Code'
                    maxLength={5}
                    integer
                />
                <Spacer
                    v={40}
                />
                <CICFButton
                    text={ONBOARDING_BUTTON_TEXT}
                    disabled={!zipValid}
                    variant={!zipValid ? 'disabled' : 'primary'}
                    onClick={onSaveClick}
                    loading={updateProfileMutation.isLoading}
                />
            </QuerySuspended>
        </OnboardingPage>
    )
}