import Icon from "components/Icon";
import React, { Fragment } from "react";
import { Link } from "react-router-dom";
import styled from "styled-components";
import { text } from "styleUtilities";
import { HOME_PATH } from "topLevelRoutes";

export interface BreadcrumbItem {
    to: string,
    text: string,
}

export default function Breadcrumbs({
    items,
}: {
    items: BreadcrumbItem[]
}) {

    return (
        <Container>
            {items.map((e, i) => (
                <Fragment
                    key={e.to}
                >
                    <Text
                        to={e.to}
                        isLast={i === items.length - 1}
                    >
                        {e.text}
                    </Text>
                    {i !== items.length - 1 &&
                        <Icon
                            size={16}
                            icon='chevron-right-light'
                        />
                    }
                </Fragment>
            ))}
        </Container>
    )
}

const Container = styled.div`
    display: flex;
    flex-direction: row;
    align-items: center;
    align-self: flex-start;
`

interface LinkProps {
    isLast: boolean
}

const Text = styled(Link)<LinkProps>`
    ${({isLast}: LinkProps) => text(isLast ? 600 : 400, 14, 14, 'breadcrumbs')}
`

