import { Event } from "@haystackdev/cicf-content-api";
import Card from "components/Card";
import ContentImage from "components/ContentImage";
import EventLocationIconWithText from "components/EventLocationIconWithText";
import EventPriceIconWithText from "components/EventPriceIconWithText";
import { IconWithTextIcon } from "components/Icon";
import IconWithText from "components/IconWithText";
import Spacer from "components/Spacer";
import styled from "styled-components";
import { maxLines, text } from "styleUtilities";
import { EVENTS_PATH } from "topLevelRoutes";
import EventSectionMode from "types/EventSectionMode";

export default function EventCard({
    event,
    mode='list',
}: {
    event: Event,
    mode?: EventSectionMode
}) {


    return (
        <EventsCardStyled
            variant="primary"
            link={'/'+EVENTS_PATH+'/'+event.id}
        >
            {mode == 'card' &&
                <ContentImage
                    src={event.imageUrl}
                    type='event'
                />
            }

            <BottomContainer>
                <TimeText>
                    {(new Date(event.timestamp)).toLocaleString('en-us', {
                        hour: 'numeric',
                        minute: 'numeric',
                        day: 'numeric',
                        month: 'long',
                        weekday: 'long'
                    })}
                </TimeText>
                <NameText>
                    {event.title}
                </NameText>
                <DescriptionText>
                    {event.description}
                </DescriptionText>
                <BottomRow>
                    <EventLocationIconWithText 
                        event={event}
                    />
                    <Spacer
                        h={13}
                    />
                    <EventPriceIconWithText
                        event={event}
                    />
                </BottomRow>
            </BottomContainer>
        </EventsCardStyled>
    )
}

const EventsCardStyled = styled(Card)`
    padding: 0px;
`

const BottomContainer = styled.div`
    display: flex;
    flex-direction: column;
    padding: 13px 25px 23px 25px;
`

const TimeText = styled.span`
    ${text(400, 14, 24, 'card-time')}
    margin-bottom: 4px;
`

const NameText = styled.h3`
    ${text(600, 18, 24, 'card-title')}
    margin-bottom: 9px;
`

const DescriptionText = styled.p`
    ${text(400, 18, 24, 'card-paragraph')}
    ${maxLines(1)}
    margin-bottom: 11px;
`

const BottomRow = styled.div`
    display: flex;
    flex-direction: row;
    align-items: center;
`
