import { Curriculum, UserProfile } from "@haystackdev/cicf-content-api";

interface CanTakeAsssessmentResult {
    canTakeAssessment: boolean,
    whyNot?: string, // Provided when canTakeAssessment is false.
}

export default function canTakeAssessment(profile: UserProfile, curriculum: Curriculum): CanTakeAsssessmentResult {
    if(profile.previousAssessmentResults.length) {
        if(!curriculum.pathways[0].isCompleted) {
            return {
                canTakeAssessment: false,
                whyNot: "Once you finish the first learning pathway, you can retake the assessment to measure your progress."
            }
        }
    }
    return {
        canTakeAssessment: true,
    }
}