import { Curriculum, CurriculumQueryData, useCurriculumInfo } from "@haystackdev/cicf-content-api";
import LearningPathwayCard from "features/Learning/LearningPathways/LearningPathwayCard";
import styled from "styled-components";
import { flexGridContainer } from "styleUtilities";


export const MARGIN = 14;
export default function LearningPathwaysDisplay({
    curriculumData,
} : {
    curriculumData: CurriculumQueryData,
}) {
    const curriculumInfo = useCurriculumInfo();
    
    return (
        <Container>
            {curriculumData.curriculum.pathways.map((e)=>(
                <LearningPathwayCard 
                    pathway={e}
                    key={e.id}
                    lockedReason={curriculumInfo.lockedPathwayIdsSet.has(e.id)?curriculumInfo.pathwayLockedReasons[e.id]:undefined}
                />
            ))}
        </Container>
    )
}

const Container = styled.div`
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    ${flexGridContainer({
        nColumns: 3,
        margin: MARGIN
    })}
`