import { getDatesOfMonth } from "features/Events/EventsPage/Calendar/calendarUtils";
import EventCalendarCell from "features/Events/EventsPage/Calendar/EventCalendarCell";
import { useEffect, useMemo, useState } from "react";
import styled from "styled-components";
import { flexGridContainer } from "styleUtilities";
import Month from "types/Month";
import { datesAreSame } from 'helpers';
import { EventsQueryData } from "@haystackdev/cicf-content-api/lib/queries/query/eventsQuery";


export default function CalendarBody({
    month,
    selectedDay,
    onDayChange,
    queryData,
}: {
    month: Month,
    selectedDay: Date,
    onDayChange: (v: Date) => void,
    queryData: EventsQueryData,
}) {
    const dates = useMemo(() => {
        return getDatesOfMonth(new Date(month.year, month.month));
    }, [month])

    function onDayClick(day: Date) {
        onDayChange(day);
    }
    
    return (
        <Container>
            {(() => {
                return dates.map((e) => (
                    <EventCalendarCell
                        date={e.d}
                        key={e.d.getDate() + '' + e.d.getMonth()}
                        selected={datesAreSame(e.d, selectedDay)}
                        onClick={()=>onDayClick(e.d)}
                        active={e.active}
                        dayHasEvent={queryData.dateHasEvent(e.d)}
                    />
                ))
            })()}
        </Container>
    )
}

const Container = styled.div`
    padding: 18px 18px 8px 18px;
    ${flexGridContainer({
    nColumns: 7,
    margin: 16,
    marginBottom: 4
})}
`