import { CurriculumItem } from "@haystackdev/cicf-content-api";
import LearningPathwayDetailTouchstoneCard from "features/Learning/LearningPathwayDetailPage/LearningPathwayDetailTouchstoneCard";
import { useMemo } from "react";
import styled from "styled-components";
import { flexGridContainer, text } from "styleUtilities";

export default function LearningPathwayDetailTouchstonesDisplay({
    touchstones,
    noItemsText,
    lockedItemIds,
}: {
    touchstones: CurriculumItem[],
    noItemsText: string,
    lockedItemIds: Set<string>
}) {

    return (
        <OutterContainer>
            <Container>
                {touchstones.map((e) => (
                    <LearningPathwayDetailTouchstoneCard
                        touchstone={e}
                        locked={lockedItemIds.has(e.id)}
                        key={e.id}
                    />
                ))}
                {!touchstones.length &&
                    <NoItemsText>
                        {noItemsText}
                    </NoItemsText>
                }
            </Container>
        </OutterContainer>
    )
}

const Container = styled.div`
    ${flexGridContainer({
    nColumns: 3,
    margin: 17,
})}

`

const OutterContainer = styled.div`
    align-self: stretch;
    display: stretch;
    min-height: 700px;
`

const NoItemsText = styled.h4`
    ${text(400, 20, 32, 'page-header')}
    align-self: stretch;
    margin-top: 80px;
    width: 100%!important;
    text-align: center;
`