import { LearningPathway } from "@haystackdev/cicf-content-api/lib/types/LearningPathway";
import CardLockedOverlay from "components/CardLockedOverlay";
import CardStyledComponentDiv, { CardProps } from "components/Card";
import styled from "styled-components";
import { textColor } from "theme";
import { maxLines } from "styleUtilities";
import CardParagraph from "components/CardParagraph";
import AccessContentIcon from "components/AccessContentIcon";
import Spacer from "components/Spacer";
import IconWithText from "components/IconWithText";
import LearningPathwayCardProgressBar from "features/Learning/LearningPathways/LearningPathwayCardProgressBar";
import { LEARNING_PATHWAY_DETAIL_PATH } from "features/Learning/LearningRoutes";
import { minutesToHoursAndMinutes } from "helpers";

export default function LearningPathwayCard({
    pathway,
    lockedReason,
} : {
    pathway: LearningPathway,
    lockedReason?: string,
}) {
    const {minutes, hours} = minutesToHoursAndMinutes(pathway.timeToCompletionInMinutes)

    return (
        <LearningPathwayCardStyled
            variant="primary"
            hideBorder={true}
            link={LEARNING_PATHWAY_DETAIL_PATH + '/' + pathway.id}
            locked={!!lockedReason}
            lockedMessage={lockedReason}
        >
            <HeaderText>
                {pathway.name}
            </HeaderText>
            <Spacer
                v={12}
            />
            <DescriptionRow>
                <DescriptionParagraph>
                    {pathway.description}
                </DescriptionParagraph>
                <AccessContentIcon/>
            </DescriptionRow>
            <Spacer 
                v={20}
            />
            <MetricsRow>
                <IconWithText 
                    text={`${pathway.items.length} touchstones`}
                    icon='wt-touchstones'
                />
                <Spacer 
                    h={22}
                />
                <IconWithText
                    icon='wt-time'
                    text={`${hours}h ${minutes}m`}
                />
            </MetricsRow>
            <ProgressBarContainer>
                <LearningPathwayCardProgressBar 
                    progress={pathway.itemsCompleted / pathway.items.length}
                />
            </ProgressBarContainer>

        </LearningPathwayCardStyled>
    )
}

const HeaderText = styled.h2`
    font-size: 22px;
    line-height: 32px;
    color: ${textColor['card-title']};
    ${maxLines(2)}
`


const LearningPathwayCardStyled = styled(CardStyledComponentDiv)`
    align-items: flex-start;
    position: relative;
    overflow: hidden;
    height: 251px;
`

const DescriptionRow = styled.div`
    display: flex;
    flex-direction: row;
    justify-content: space-between;
`

const DescriptionParagraph = styled.p`
    color: ${textColor['card-paragraph']};
    max-width: 64%;
    font-size: 16px;
    line-height: 24px;
    ${maxLines(2)}
`

const MetricsRow = styled.div`
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
`

const ProgressBarContainer = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    flex: 1;
    align-self: stretch;
`