import { MVMTShimmerFixed } from "components/MVMTShimmer/MVMTShimmer";
import React, { useEffect } from "react";
import { UseMutationResult, UseQueryResult } from "react-query";
import MoonLoader from 'react-spinners/MoonLoader';
import styled from "styled-components";
import { backgroundColor } from "theme";

export default function QuerySuspended({
    queries,
    children,
    setLoaded,
    forceLoading,
    size = 96,
    loadingElement,
}: {
    queries: { isSuccess: boolean, isError: boolean, }[], // Did this b/c typescript errors.
    children: React.ReactNode,
    setLoaded?: (v: boolean) => void,
    forceLoading?: boolean,
    size?: number,
    loadingElement?: React.ReactElement
}) {
    const loaded = !queries.some((e) => !e.isSuccess)
    const error = queries.some(e=>e.isError);

    

    useEffect(() => {
        if (setLoaded) {
            setLoaded(loaded)
        }
    }, queries.map((e) => e.isSuccess))

    if(error) {
        return <div>Error loading page, try refreshing.</div>
    }

    if (!loaded || forceLoading) {
        if (loadingElement) return loadingElement;
        return (
            <MVMTShimmerFixed/>
        );
    }
    return (
        <>
            {children}
            {queries.some((e) => (e.isError)) &&
                <div>Error loading page, try refreshing the page.</div>
            }
        </>
    )
}


export const ContentLoadingContainer = styled.div`
    align-self: stretch;
    display: flex;
    justify-content: center;
    align-items: center;

`