import { default as PledgeSignatureType } from "@haystackdev/cicf-content-api/lib/types/PledgeSignature";
import Card from "components/Card";
import CICFButton from "components/CICFButton";
import PledgeSignature from "components/PledgeSignature/PledgeSignature";
import Spacer from "components/Spacer";
import { useNavigate } from "react-router-dom";
import { PLEDGE_BASE_PATH } from "topLevelRoutes";


export default function PledgeSignatureCard({
    pledgeSignature,
    name,
} : {
    pledgeSignature: PledgeSignatureType,
    name: string,
}) {
    const navigate = useNavigate();

    function onViewPledgeClick() {
        navigate('/' + PLEDGE_BASE_PATH + '/' + pledgeSignature.pledgeId)
    }

    return (
        <Card
            variant="primary"
            hideBorder={true}
        >
            <PledgeSignature 
                signature={pledgeSignature}
                name={name}
            />
            <Spacer v={16}/>
            <CICFButton 
                text="View Pledge"
                onClick={onViewPledgeClick}
            />
        </Card>
    )
}