import ChangeEmailChange from "features/ChangeEmail/ChangeEmailChange";
import ChangeEmailConfirm from "features/ChangeEmail/ChangeEmailConfirm";
import ChangeEmailSuccess from "features/ChangeEmail/ChangeEmailSuccess";
import React from "react";
import { Navigate, Route, Routes } from "react-router-dom";
export const CHANGE_EMAIL_CONFIRM_BASE_PATH = 'confirm'
export const CHANGE_EMAIL_CONFIRM_PATH = CHANGE_EMAIL_CONFIRM_BASE_PATH+'/:email';
export const CHANGE_EMAIL_SUCCESS_BASE_PATH = 'success';
export const CHANGE_EMAIL_SUCCESS_PATH = CHANGE_EMAIL_SUCCESS_BASE_PATH + '/:email';
export const CHANGE_EMAIL_CHANGE_PATH = 'change/:token';

export const changeEmailRoutesMap: {[routeName: string]: ()=>JSX.Element} = {
    [CHANGE_EMAIL_CONFIRM_PATH]: ChangeEmailConfirm,
    [CHANGE_EMAIL_SUCCESS_PATH]: ChangeEmailSuccess,
    [CHANGE_EMAIL_CHANGE_PATH]: ChangeEmailChange
}

export default function ChangeEmailRoutes() {
    return (
        <Routes>
            {Object.entries(changeEmailRoutesMap).map(([routeName, Component])=>{
                return (
                    <Route 
                        path={routeName}
                        element={<Component />}
                        key={'/'+routeName}
                    />
                )
            })}
            <Route 
                path={'*'}
                element={<Navigate to={'/'}/>}
            />
        </Routes>
    )
}