import Icon from "components/Icon";
import styled from "styled-components";
import { text } from "styleUtilities";
import EventSectionMode from "types/EventSectionMode";



export default function EventsSectionHeader({
    titleText,
    mode,
    onModeToggle,
} : {
    titleText: string,
    mode: EventSectionMode,
    onModeToggle: (v: EventSectionMode)=>void,
}) {
    return (
        <Container>
            <TitleText>
                {titleText}
            </TitleText>
            <Icon 
                onClick={()=>onModeToggle(mode=='list'?'card':'list')}
                icon={mode=='card'?'es-card':'es-list'}
                size={24}
            />
        </Container>
    )
}

const Container = styled.div`
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
`

const TitleText = styled.h2`
    ${text(600, 18, 24, 'page-header')}
`

