import { EventsQueryData } from "@haystackdev/cicf-content-api/lib/queries/query/eventsQuery";
import { GetEventsResponse } from "@haystackdev/cicf-content-api/lib/requests/getEvents";
import Multiswitch from "components/Multiswitch";
import Spacer from "components/Spacer";
import EventsLeftColumn from "features/Events/EventsPage/EventsLeftColumn";
import EventsRightColumn from "features/Events/EventsPage/EventsRightColumn";
import { useEffect, useState } from "react";
import styled from "styled-components";

type EventsTab = 'All' | 'Saved';
export type EventsContentState = 'initial' | 'saved-events' | 'selected-date'
const tabItems: EventsTab[] = ['All', 'Saved',]

export default function EventsContent({
    eventsQueryData,
} : {
    eventsQueryData: EventsQueryData
}) { 
    const [selectedTab, setSelectedTab] = useState<EventsTab>('All');
    const [selectedEventDate, setSelectedEventDate] = useState<Date | undefined>();
    const [eventsContentState, setEventsContentState] = useState<EventsContentState>('initial');


    useEffect(()=>{
        if(selectedTab == 'Saved') {
            setSelectedEventDate(undefined);
            setEventsContentState('saved-events');
        }
        if(selectedTab == 'All' && !selectedEventDate && eventsContentState !== 'initial') setEventsContentState('initial');
    }, [selectedTab])

    useEffect(()=>{
        if(selectedEventDate) {
            setSelectedTab('All');
            setEventsContentState('selected-date');
        }
    }, [selectedEventDate])
    
    return (
        <Container>
            <Multiswitch
                items={tabItems}
                value={selectedTab}
                onChange={setSelectedTab}
                height={35}
                width={343}
                radius={5}
            />
            <Spacer
                v={20}
            />
            <InnerContainer>
                <EventsLeftColumn 
                    eventsQueryData={eventsQueryData}
                    eventsContentState={eventsContentState}
                    selectedEventDate={selectedEventDate?selectedEventDate:new Date()}
                />
                <Spacer 
                    h={44}
                />
                <EventsRightColumn 
                    eventsQueryData={eventsQueryData}
                    selectedDate={selectedEventDate?selectedEventDate:new Date()}
                    onSelectedDateChange={setSelectedEventDate}
                />
            </InnerContainer>
        </Container>
    )
}

const Container = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 20px;
    align-self: stretch;
`

const InnerContainer = styled.div`
    display: flex;
    flex-direction: row;
    align-self: stretch;
`
