function App() {
  const authLogin = useAuth((s) => s.loggedIn);
  const loggedIn = dev.autoLogin || authLogin;
  return (
    <>
      <meta name='viewport' content="width=device-width, initial-scale=1.0, user-scalable=no, maximum-scale=1.0"/>
      <Provider>
        <Router>
          <Navbar />
          {/* <DevStuff /> */}
          <ContentContainerOutter>
            <ContentContainerInner>
              <Routes>
                {loggedIn ?
                  <>
                    {Object.keys(protectedRoutesWithNav).map((routeName: keyof typeof protectedRoutesWithNav) => {
                      const Component = protectedRoutesWithNav[routeName];

                      return <Route
                        path={`/${routeName}/*`}
                        element={<Component />}
                        key={routeName}
                      />
                    })}
                    {Object.keys(protectedRoutesWithoutNav).map((routeName: keyof typeof protectedRoutesWithNav) => {
                      const Component = protectedRoutesWithoutNav[routeName];

                      return <Route
                        path={`/${routeName}/*`}
                        element={<Component />}
                        key={routeName}
                      />
                    })}
                  </>
                  :
                  <>
                    {Object.keys(topLevelAuthRoutes).map((routeName: keyof typeof topLevelAuthRoutes) => {
                      const Component = topLevelAuthRoutes[routeName];
                      return (
                        <Route
                          path={`/${routeName}/*`}
                          element={<Component />}
                          key={routeName}
                        />
                      )
                    })}

                  </>
                }
                {Object.entries(alwaysRenderedRoutes).map(([routeName, Component]) => {
                  return (
                    <Route
                      path={`/${routeName}/*`}
                      element={<Component />}
                      key={routeName}
                    />
                  )
                })

                }
                <Route
                  path='/*'
                  element={<Navigate replace={true} to={loggedIn ? '/home' : '/login'} />}
                />
              </Routes>
            </ContentContainerInner>
          </ContentContainerOutter>
          <ModalContainer />
          <EnsureSupportedDevice />
        </Router>

      </Provider>
    </>

  );
}

