import styled from "styled-components"
import { backgroundColor, borderColors } from "theme";
import range from 'lodash/range';

const HEIGHT = 16;

export default function OnboardingWidgetProgressBar({
    nComplete,
    total,
}: {
    nComplete: number,
    total: number,
}) {
    return (
        <Container>
            {range(total - 1).map((e) => (
                <Separator
                    total={total}
                    index={e + 1}
                    key={''+e}
                />
            ))}
            <Bar
                progress={nComplete / total}
            />

        </Container>
    )
}


const Container = styled.div`
    background-color: white;
    position: relative;
    height: ${HEIGHT}px;
    border-radius: 100px;
    overflow: hidden;
`

interface BarProps {
    progress: number; // 0-1
}

const Bar = styled.div<BarProps>`
    background-color: ${backgroundColor['onboarding-progress-bar']};
    width: ${p => p.progress * 100}%;
    left: 0;
    position: absolute;
    top: 0;
    bottom: 0;
    border-radius: 10px
`

interface SeparatorProps {
    index: number,
    total: number,
}

const Separator = styled.div<SeparatorProps>`
    display: inline-flex;
    height: ${HEIGHT}px;
    position: absolute;
    border-right-width: 1px;
    border-right-style: solid;
    border-right-color: ${borderColors['onboarding-progress-bar-separator']};
    top: 0px;
    bottom: 0px;
    left: ${p => p.index / p.total * 100}%;
`