import { CurriculumItem, CurriculumItemMediaType, touchstoneImageSize, useCurriculumItemCompletionChangeMutation } from "@haystackdev/cicf-content-api";
import useTouchstoneNotesInfiniteQuery from "@haystackdev/cicf-content-api/lib/queries/query/touchstoneNotesQuery";
import CICFButton from "components/CICFButton";
import IconWithText from "components/IconWithText";
import Spacer from "components/Spacer";
import { filledIconForMediaType, openLinkInNewTab, textForMediaType } from "helpers";
import { useMemo, useState } from "react";
import { Link, NavigateOptions, useNavigate } from "react-router-dom";
import styled from "styled-components";
import { text } from "styleUtilities";
import { touchstoneImageAspectRatio, narrowContentWidth } from "theme";
import { useBottomScrollListener } from 'react-bottom-scroll-listener';
import TouchstoneNote from "@haystackdev/cicf-content-api/lib/types/TouchstoneNote";
import ClipLoader from 'react-spinners/ClipLoader';
import TouchstoneDetailNote from "features/Learning/TouchstoneDetail/TouchstoneDetailNote";
import useModalStore from "state/modals";
import Icon from "components/Icon";
import Breadcrumbs, { BreadcrumbItem } from "components/BreadCrumbs";
import { LEARNING_PATH } from "topLevelRoutes";
import { LEARNING_PATHWAY_DETAIL_PATH, TOUCHSTONE_DETAIL_PATH } from "features/Learning/LearningRoutes";


export default function TouchstoneDetailContent({
    touchstone,
    learningPathwayName,

}: {
    touchstone: CurriculumItem,
    learningPathwayName: string,
}) {
    useBottomScrollListener(onBottomReached, {
        triggerOnNoScroll: true,
        offset: 100,
    })
    const showAddNoteModal = useModalStore((s) => s.showAddNoteModal)
    const markCompleteMutation = useCurriculumItemCompletionChangeMutation();
    const touchstoneNotesInfiniteQuery = useTouchstoneNotesInfiniteQuery(touchstone.id);
    const navigate = useNavigate();

    function onAccessContentButtonClick() {
        openLinkInNewTab(touchstone.url!)
    }

    function onCompleteClick() {
        markCompleteMutation.mutateAsync({
            itemId: touchstone.id,
            isCompleted: !touchstone.isCompleted,
            pathwayId: touchstone.learningPathwayId
        })
    }

    function onAddNoteClick() {
        showAddNoteModal(touchstone.id);
    }

    function onBottomReached() {
        if (!touchstoneNotesInfiniteQuery.isFetching && touchstoneNotesInfiniteQuery.hasNextPage) {
            touchstoneNotesInfiniteQuery.fetchNextPage();
        }
    }

    function onContinueClick() {
        navigate('/' + LEARNING_PATH + '/' + LEARNING_PATHWAY_DETAIL_PATH + '/' + touchstone.learningPathwayId,)
    }

    // all loaded notes note literally all of them
    const allLoadedTouchstoneNotes: TouchstoneNote[] = useMemo(() => {
        if (touchstoneNotesInfiniteQuery.data) {
            var r: TouchstoneNote[] = [];
            for (var page of touchstoneNotesInfiniteQuery.data.pages) {
                r = r.concat(page.notes);
            }
            return r;
        }
        return [];
    }, [touchstoneNotesInfiniteQuery.data])

    const breadcrumbItems: BreadcrumbItem[] = [
        {
            to: '/' + LEARNING_PATH,
            text: 'My Learning'
        },
        {
            to: '/' + LEARNING_PATH + '/' + LEARNING_PATHWAY_DETAIL_PATH + '/' + touchstone.learningPathwayId,
            text: learningPathwayName
        },
        {
            to: '/' + LEARNING_PATH + '/' + TOUCHSTONE_DETAIL_PATH + '/' + touchstone.id,
            text: touchstone.title
        }
    ]

    return (
        <>
            <Breadcrumbs
                items={breadcrumbItems}
            />
            <Spacer
                v={12}
            />
            <Container>
                <Header>
                    {touchstone.title}
                </Header>
                <Spacer
                    v={15}
                />
                <SubHeaderContainer>
                    <SubHeaderContainerLeft>
                        <IconWithText
                            icon={filledIconForMediaType(touchstone.mediaType)}
                            text={textForMediaType(touchstone.mediaType)}
                        />
                        <Spacer
                            h={17}
                        />
                        <IconWithText
                            icon='wt-time'
                            text={`${touchstone.estimatedTimeInMinutes} minutes`}
                        />
                    </SubHeaderContainerLeft>
                    {touchstone.isCompleted &&
                        <CompletedContainer>
                            <Icon
                                size={16}
                                icon='touchstone-completed-thumb'
                            />
                            <CompletedText>
                                Completed!
                            </CompletedText>
                        </CompletedContainer>
                    }
                </SubHeaderContainer>
                <Spacer
                    v={30}
                />
                <Image
                    src={touchstone.imageUrl}
                    style={{
                        aspectRatio: `${touchstoneImageSize.width / touchstoneImageSize.height}`
                    }}
                />
                <Spacer
                    v={20}
                />
                <DescriptionLabel>
                    {touchstone.mediaType == 'in-app' ? 'Guidepost' : 'Description'}
                </DescriptionLabel>
                <Spacer
                    v={12}
                />
                <Description>
                    {touchstone.description}
                </Description>
                <Spacer
                    v={60}
                />

                <ButtonsContainer>
                    {touchstone.mediaType !== 'in-app' &&
                        <Button
                            text={linkButtonTextForMediaType(touchstone.mediaType)}
                            onClick={onAccessContentButtonClick}
                        />
                    }

                    <Button
                        variant="outline"
                        text={touchstone.isCompleted ? 'Unmark as Complete' : 'Mark as Complete'}
                        onClick={onCompleteClick}
                        loading={markCompleteMutation.isLoading}
                    />
                    <Button
                        variant="outline"
                        text={"Add Notes"}
                        onClick={onAddNoteClick}
                    />
                    {touchstone.isCompleted &&
                        <Button
                            variant="outline"
                            text={"Return to Pathway"}
                            onClick={onContinueClick}
                        />
                    }

                </ButtonsContainer>
                <NotesLabel>
                    Notes
                </NotesLabel>
                {touchstoneNotesInfiniteQuery.isFetched &&
                    allLoadedTouchstoneNotes.map((e) => (
                        <TouchstoneDetailNote
                            touchstoneNote={e}
                            key={e.id}
                        />
                    ))
                }
                {touchstoneNotesInfiniteQuery.hasNextPage &&
                    <LoadingIndicatorContainer>
                        <ClipLoader
                            size={48}
                        />
                    </LoadingIndicatorContainer>

                }
            </Container>
        </>
    )
}



const Container = styled.div`
    width: ${narrowContentWidth}px;
    display: flex;
    flex-direction: column;
`

const Header = styled.h1`
    ${text(600, 32, 44, 'page-header')}
`

const SubHeaderContainer = styled.div`
    display: flex;
    flex-direction: row;
    justify-content: space-between;
`

const SubHeaderContainerLeft = styled.span`
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
`

const CompletedContainer = styled.span`
    flex-direction: row;
    align-items: center;
`

const CompletedText = styled.span`
    ${text(400, 14, 24, 'touchstone-completed-text')}
    margin-left: 4px;
`

const Image = styled.img`
    width: 100%;
    aspect-ratio: ${touchstoneImageAspectRatio};
    object-fit: cover;
    border-radius: 7px;
`

const DescriptionLabel = styled.h2`
    ${text(400, 14, 24, 'page-content')}
`

const Description = styled.p`
    ${text(400, 16, 24, 'page-content')}
    white-space: break-spaces;
`

const SourceLabel = styled.h3`
    ${text(400, 12, 16, 'page-content')}

`

const SourceLink = styled(Link)`
    ${text(400, 12, 16, 'page-content')}
    margin-top: 8px;
`

const ButtonsContainer = styled.div`
    display: inline-flex;
    align-self: center;
    width: 228px;
    flex-direction: column;
`

const Button = styled(CICFButton)`
    align-self: stretch;
    margin-bottom: 20px;
    width: 100%;
`

const NotesLabel = styled.h2`
    ${text(400, 14, 24, 'page-content')}
    margin-bottom: 16px;
`

const LoadingIndicatorContainer = styled.div`
    align-self: center;
    margin-top: 10px;
    margin-bottom: 100px;
`

function linkButtonTextForMediaType(mediaType: CurriculumItemMediaType): string {
    switch (mediaType) {
        case 'article':
            return 'Read Article';
        case 'podcast':
            return 'Listen to Podcast';
        case 'video':
            return 'Watch Video';

    }
    return '';
}

