import IS_MOBILE_BREAKPOINT, { IS_TABLET_BREAKPOINT } from "constants/overlayBreakpoints";
import PhoneOverlay from "features/EnsureSupportedDevice/PhoneOverlay";
import TabletOverlay from "features/EnsureSupportedDevice/TabletOverlay";
import useWindowSize from "hooks/useWindowSize";
import { useEffect } from "react";

import { useLocation, useMatch } from "react-router-dom";
import styled from "styled-components";
import { EMAIL_CONFIRMED_BASE_PATH } from "topLevelRoutes";

const mobileEnabledRoutes = [EMAIL_CONFIRMED_BASE_PATH];

export default function EnsureSupportedDevice() {
    const {pathname} = useLocation();
    
    if(mobileEnabledRoutes.map(e=>pathname.includes(e)).some(e=>e)) {
        return null;
    }

    return (
        <SupportedDevicesOverlayContainer>
            <InnerContainer>
                <TabletOverlay />
            </InnerContainer>
        </SupportedDevicesOverlayContainer>
    )
}




const SupportedDevicesOverlayContainer = styled.div`
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    z-index: 100000;
    @media (min-width: ${IS_TABLET_BREAKPOINT}px) {
        display: none;
    }
`

const InnerContainer = styled.div`
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    padding:0;
    background-color: white;
    @media (min-width: ${IS_TABLET_BREAKPOINT}px) {
        display: none;
    }
`