import { useUserProfileQuery } from "@haystackdev/cicf-content-api"
import Icon from "components/Icon";
import ProfilePhoto from "components/ProfilePhoto";
import QuerySuspended from "components/QuerySuspended";
import ShimmerPlaceholder from "components/ShimmerPlaceholder";
import { useOnClickOutside } from "hooks/useOnClickOutside";
import { useState } from "react";
import { useNavigate } from "react-router-dom";
import useAuth from "state/authState";
import styled from "styled-components"
import { border, text } from "styleUtilities";
import { backgroundColor, textColor } from "theme";
import { PROFILE_PATH, SETTINGS_PATH } from "topLevelRoutes";


export default function UserDropdownMenu() {
    const profileQuery = useUserProfileQuery();
    const [open, setOpen] = useState<boolean>(false);
    const auth = useAuth();

    const navigate = useNavigate();

    const {props} = useOnClickOutside(()=>{
        setOpen(false);
    })

    const menuItems: {
        label: string,
        onClick: ()=>void,
    }[] = [
        {
            label: "Profile",
            onClick: ()=>{
                navigate(`/${PROFILE_PATH}`)
            }
        },
        {
            label: 'Settings',
            onClick: ()=>{
                navigate(`/${SETTINGS_PATH}`)
            }
        },
        {
            label: 'Logout',
            onClick: ()=>{
                auth.logout();
                navigate('/')
            }
        }
    ]

    function onMenuClick() {
        setOpen(v => !v);
    }

    function onMenuItemClick(index: number) {
        menuItems[index].onClick();
        setOpen(false);
    }

    return (
        <Container>
            <QuerySuspended
                queries={[profileQuery]}
                loadingElement={<ShimmerPlaceholder height={50} width={130}/>}
            >
                {profileQuery.data &&
                    <InnerContainer {...props}>
                        <Dropdown
                            onClick={onMenuClick}
                        >
                            <DropdownLeft>
                                <ProfilePhoto
                                    size={34}
                                    url={profileQuery.data.profileImageUrl}
                                />
                                <NameText>
                                    {`${profileQuery.data.firstName} ${profileQuery.data.lastName}`}
                                </NameText>
                            </DropdownLeft>
                            <Icon
                                size={24}
                                icon='chevron-down-black'
                            />

                        </Dropdown>
                        {open &&
                            <DropdownMenu>
                                {menuItems.map((item, i)=>(
                                    <DropdownMenuItem 
                                        key={i+''}
                                        onClick={()=>onMenuItemClick(i)}
                                    >
                                        {item.label}
                                    </DropdownMenuItem>
                                ))}
                            </DropdownMenu>
                        }
                    </InnerContainer>

                }
            </QuerySuspended>
        </Container>
    )
}

const Container = styled.span`
    padding-right: 90px;
`

const InnerContainer = styled.div`
    position: relative;
`

const Dropdown = styled.button`
    width: 187px;
    height: 55px;
    display: inline-flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
    padding: 13px;
    position: relative;
    ${border({
    radius: 10,
    color: 'profile-menu',
    width: 1,
})}
`


const NameText = styled.span`
    ${text(400, 16, 24)}
    flex: 1;
    margin-left: 8px;
`

const DropdownLeft = styled.span`
    display: inline-flex;
    justify-content: flex-start;
    align-items: center;
    flex: 1;
`

const DropdownMenu = styled.div`
    width: 100%;
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    background-color: ${backgroundColor['profile-dropdown-menu']};
    display: inline-flex;
    flex-direction: column;
    align-items: stretch;
    border-radius: 10px;
    overflow: hidden;
`

const DropdownMenuItem = styled.button`
    text-align: left;
    height: 48px;
    color: ${textColor['profile-dropdown-menu-item']};
    padding-left: 16px;
    &:hover {
        background-color: ${backgroundColor['profile-dropdown-item-hover']};

    }
`