import React, { useState } from "react";
import { useNavigate } from "react-router-dom";

//Imports
import PasswordMeter from "../../../components/PasswordMeter/index";
import RulesOfEngagement from "../../../components/RulesOfEngagement";

import { contentCopy, useCheckEmailAvailableQuery, useCreateAccountMutation, useLoginMutation, usePasswordStrength } from "@haystackdev/cicf-content-api";
import styled from "styled-components";
import { text } from "styleUtilities";
import Page from "components/Page";
import { CONFIRM_EMAIL_BASE_PATH, CONFIRM_EMAIL_PATH } from "topLevelRoutes";
import LabelledCheckbox from "components/LabelledCheckbox";
import CICFInput from "components/CICFInput";
import checkEmailAvailable from "@haystackdev/cicf-content-api/lib/requests/checkEmailAvailable";
import CICFButton from "components/CICFButton";
import Spacer from "components/Spacer";
import { EMAIL_PLACEHOLDER, MAX_EMAIL_LENGTH } from "constants/inputContants";

const CreateAccount = () => {
  const [firstName, setFirstName] = useState("");
  const [lastName, setLastName] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');

  const [emailValid, setEmailValid] = useState(false);
  const [passwordValid, setPasswordValid] = useState(false);

  const navigate = useNavigate();
  const [is18, setIs18] = useState<boolean>(false);

  const emailAvailableQuery = useCheckEmailAvailableQuery(email);
  const createAccountMutation = useCreateAccountMutation();
  const passwordStrengthResult = usePasswordStrength(password);

  const [openModal, setOpenModal] = useState(false);



  function onCreateAccountClick() {
    createAccountMutation.mutateAsync({
      email,
      password,
      firstName,
      lastName,
      clientType: 'web'
    })
      .then((response) => {
        console.log(firstName);
        console.log("Create account success")
        if (!response.accountCreated) {
          console.log("Couldn't create account.");
          return;
        }
        navigate('/' + CONFIRM_EMAIL_BASE_PATH + '/' + email)
      });
  }

  const onCreateAccountButtonClick = () => {
    emailAvailableQuery.refetch().then((queryData) => {
      const available = queryData.data?.available;
      if (!available) {
        alert(
          "Email unavailable. That email is already taken. You can try logging in with the email, or create an account with a different email."
        );
        ; //this was causing an error that was pointing at .length in the password meter component
        return;
      } else {
        setOpenModal(true);
      }
    });
  }

  const canSubmit =
    passwordStrengthResult.valid && emailValid && passwordValid && firstName && lastName && is18;

  return (
    <Page>
      <CreateContainer>
        <ContainerHeader>
          Create An Account

        </ContainerHeader>
        <ContainerText>

          {contentCopy.authorization.createAccount.message}
        </ContainerText>
        <FormContainer>
          <CICFInput
            value={firstName}
            onChange={setFirstName}
            placeholder='First Name'
            icon='input-user'
          />
          <Spacer v={20} />
          <CICFInput
            value={lastName}
            onChange={setLastName}
            placeholder='Last Name'
            icon='input-user'
          />
          <Spacer v={20} />
          <CICFInput
            value={email}
            onChange={setEmail}
            placeholder={EMAIL_PLACEHOLDER}
            icon='mail-dark'
            validationType="email"
            setValid={setEmailValid}
            type='email'
            maxLength={MAX_EMAIL_LENGTH}
          />
          <Spacer v={30} />
          <CICFInput
            value={password}
            onChange={setPassword}
            placeholder='Password'
            type='password'
            validationType='password'
            setValid={setPasswordValid}
            forceVisibleButton={true}
            icon='lock'
          />
          <Spacer v={16} />
          <PasswordMeter testPasswordResult={passwordStrengthResult.testResult}/>
          <Spacer v={20} />
          <LabelledCheckbox
            label="I am over 18 years old (required)"
            onChange={setIs18}
            value={is18}
          />
          <Spacer v={30} />
          <CICFButton
            disabled={!canSubmit}
            variant={canSubmit ? 'primary' : 'disabled'}
            onClick={onCreateAccountButtonClick}
            loading={createAccountMutation.isLoading || passwordStrengthResult.busy}
            text="Create Account"
          />
          {openModal && (
            <RulesOfEngagement
              createAccount={onCreateAccountClick}
              loading={createAccountMutation.isLoading}
            />
          )}

        </FormContainer>
        <TermsContainer>
          <TermsText>
            <p>
              By creating an account, you agree to our{" "}
              <a
                href="www.example.com"
                style={{ textDecoration: "none", color: "#211d3a" }}
              >
                <strong>Terms &amp; Conditions</strong>{" "}
              </a>{" "}
              and{" "}
              <a
                href="www.example.com"
                style={{ textDecoration: "none", color: "#211d3a" }}
              >
                <strong>Privacy Policy</strong>
              </a>
              .
            </p>
          </TermsText>
        </TermsContainer>
      </CreateContainer>
    </Page>
  );
};



export const CreateContainer = styled.div`
  width: 560px;
  background: #ffffff;
  border: 1px solid #e6e8ec;
  box-sizing: border-box;
  box-shadow: 0px 6px 24px -6px rgba(0, 0, 0, 0.1);
  border-radius: 20px;
  justify-content: center;
  margin-top: 60px;
  position: relative;
  margin: auto;
  display: flex;
  padding: 50px;
  flex-direction: column;
  align-items: flex-start;
`;

export const ContainerHeader = styled.h1`
  display: flex;
  color: #211d3a;
  flex: none;
  order: 0;
  flex-grow: 0;
  font-family: Inter;
  font-style: normal;
  font-weight: 600;
  line-height: 44px;
  margin-bottom: 8px; 
  ${text(600, 32, 44, 'card-title')}
`;

export const ContainerText = styled.span`
  display: flex;
  color: #474554;
  font-size: 1em;
  flex: none;
  flex-grow: 0;
  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  line-height: 24px;
  padding-top: 8px;
  margin-bottom: 40px;

`;

export const FormContainer = styled.div`
  display: flex;
  align-self: stretch;
  flex-direction: column;
`;

export const TermsContainer = styled.div`
  color: #b9b9b9;
  text-align: center;
  font-size: 14px;
  width: 70%;
  align-self: center;
  margin-top: 30px;
`;

export const TermsText = styled.p`
  text-decoration: none;
  line-height: 24px;
`;

export const Button = styled.button`
  background-color: ${({ disabled }) => (disabled ? "#BDBDBD" : "#fedb2a")};
  color: ${({ disabled }) => (disabled ? "white" : "#black")};
  border: none;
  border-radius: 10px;
  flex-direction: row;
  display: flex;
  justify-content: center;
  width: 135px;
  height: 55px;
  align-items: center;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  box-shadow: 0px 12px 40px -12px rgba(0, 0, 0, 0.2);
  flex: none;
  text-align: center;
  position: absolute;
  top: 485px;
  margin-left: 28%;
`;


export default CreateAccount;
