import { LearningPathway } from "@haystackdev/cicf-content-api";
import Multiswitch from "components/Multiswitch";
import Spacer from "components/Spacer";
import LearningPathwayDetailTouchstonesDisplay from "features/Learning/LearningPathwayDetailPage/LearningPathwayDetailTouchstonesDisplay";
import { useMemo, useState } from "react";
import styled from "styled-components";
import { text } from "styleUtilities";

type MultiswitchState = 'Incomplete' | 'Complete' | 'All';

const multiswitchStates: MultiswitchState[] = ['Incomplete', 'Complete', 'All']

export default function LearningPathwayDetailTouchstones({
    pathway,
    lockedItemIds,
} : {
    pathway: LearningPathway,
    lockedItemIds: Set<string>
}) {
    const [multiswitchState, setMultiswitchState] = useState<MultiswitchState>('All');

    const touchstones = useMemo(()=>{
        switch(multiswitchState) {
            case 'All':
                return pathway.items;
            case 'Complete':
                return pathway.items.filter((e)=>e.isCompleted);
            case 'Incomplete':
                return pathway.items.filter((e)=>!e.isCompleted);
        }
    }, [multiswitchState])

    return (
        <Container>
            <Header>
                Touchstones
            </Header>
            <Spacer
                v={43}
            />
            <Multiswitch
                onChange={setMultiswitchState}
                value={multiswitchState}
                items={multiswitchStates}
            />
            <Spacer 
                v={17}
            />
            <LearningPathwayDetailTouchstonesDisplay 
                touchstones={touchstones}
                lockedItemIds={lockedItemIds}
                noItemsText={noTouchstonesText(multiswitchState)}
            />
        </Container>
    )
}

const Header = styled.h3`
    ${text(600, 24, 32, 'page-header')}
`

const Container = styled.div`
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    align-self: stretch;
`

function noTouchstonesText(multiswitchState: MultiswitchState) {
    switch(multiswitchState) {
        case 'All':
            return 'No touchstones found for this pathway';
        case 'Complete':
            return 'No touchstones completed';
        case 'Incomplete':
            return 'No incomplete touchstones'
    }
}