import { useUpdateUserProfileMutation, useUserProfileQuery, useUsStatesQuery } from "@haystackdev/cicf-content-api";
import ServerUSState from "@haystackdev/cicf-content-api/lib/types/Server/ServerUSState";
import { CardCentered } from "components/Card";
import CICFButton from "components/CICFButton";
import CICFInput from "components/CICFInput";
import DropdownSelect from "components/DropdownSelect";
import Page from "components/Page";
import QuerySuspended from "components/QuerySuspended";
import Spacer from "components/Spacer";
import SettingsCard from "features/Settings/SettingsCard";
import { useEffect, useState } from "react";
import styled from "styled-components";
import { border } from "styleUtilities";

export default function SettingsAddress() {
    const [city, setCity] = useState<string>('');

    const [zip, setZip] = useState<string>('');


    const [zipValid, setZipValid] = useState<boolean>(true);

    const [selectedState, setSelectedState] = useState<ServerUSState | null>(null)

    const updateProfileMutation = useUpdateUserProfileMutation();
    const profileQuery = useUserProfileQuery();
    const usStatesQuery = useUsStatesQuery();
    const profile = profileQuery.data;

    const inputValid = !!zipValid;

    useEffect(() => {

        if (profile) {
            setCity(profile.address.city);
            if (profile.address.state)
                setSelectedState(profile.address.state);
            setZip(profile.address.zip);
        }
    }, [profile])

    function onSavePress() {
        updateProfileMutation.mutateAsync({
            ...profileQuery.data!,
            address: {
                city,
                state: selectedState ? selectedState : undefined,
                zip,
            }
        }).then(() => alert('Address updated'))
    }
    return (


            <QuerySuspended
                queries={[profileQuery, usStatesQuery]}
            >
                {profile && usStatesQuery.data &&
                    <SettingsCard title='Address'>
                        <CICFInput
                            placeholder='City'
                            value={city}
                            onChange={setCity}
                        />
                        <Spacer v={16} />
                        <StateSelect
                            placeholder="Select State"
                            value={selectedState?.id}
                            onChange={event => {
                                setSelectedState(usStatesQuery.data.find(e => e.id + '' == event.target.value)!);
                            }}
                        >
                            {usStatesQuery.data.map(e => (
                                <option
                                    value={e.id + ''}
                                    label={e.code}
                                    key={e.id}
                                >
                                    {e.code}
                                </option>
                            ))

                            }
                        </StateSelect>
                        <Spacer v={16} />
                        <CICFInput
                            placeholder='Zip'
                            value={zip}
                            onChange={setZip}
                            setValid={setZipValid}
                            validationType="zip"
                            integer={true}
                        />
                        <Spacer v={48} />
                        <CICFButton
                            text="Save Address"
                            variant={inputValid ? 'primary' : 'disabled'}
                            disabled={!inputValid}
                            onClick={onSavePress}
                            loading={updateProfileMutation.isLoading}
                        />
                    </SettingsCard>
                }
            </QuerySuspended>
    )
}

const StateSelect = styled.select`
    ${border({
    radius: 10,
    width: 1,
    color: 'text-input'
})}
    height: 50px;
    padding-left: 10px;
    padding-right: 10px;
`