import React from "react";
import styled from "styled-components";
import { backgroundColor } from "theme";

export default function LearningPathwayCardProgressBar({
    progress,
} : {
    progress: number,
}) {
    return (
        <Container>
            <Bar
                progress={progress}
            />
        </Container>
    )
}


const Container = styled.div`
    position: relative;
    background-color: ${backgroundColor['primary-progress-bar-background']};
    height: 8px;
    border-radius: 8px;
    overflow: hidden;
    align-self: stretch;
`

interface BarProps {
    progress: number,
}

const Bar = styled.div<BarProps>`
    height: 100%;
    width: ${p=>p.progress * 100}%;
    background-color: ${backgroundColor['primary-progress-bar']};
    border-radius: 8px;
`