import { shareStaticContent, useUserProfileQuery } from "@haystackdev/cicf-content-api";
import Icon from "components/Icon";
import FacebookIcon from "components/SocialIcons/FacebookIcon";
import TwitterIcon from "components/SocialIcons/TwitterIcon";
import Spacer from "components/Spacer";
import ModalBase from "modals/ModalBase";

import EmailShareButton from 'react-share/lib/EmailShareButton'
import FacebookShareButton from 'react-share/lib/FacebookShareButton';
import TwitterShareButton from 'react-share/lib/TwitterShareButton';
import styled from "styled-components";
import { border, text } from "styleUtilities";
import { backgroundColor } from "theme";


export default function SharePledgeModal() {
    const userProfileQuery = useUserProfileQuery();
    const username = userProfileQuery.data?`${userProfileQuery.data.firstName} ${userProfileQuery.data.lastName}`:'';
    return (
        <ModalBase>
            <SharePledgeText>
                Share Pledge
            </SharePledgeText>
            <Container>
                <FBShareButton
                    url={shareStaticContent.shareUrl}
                    quote={shareStaticContent.shareFacebookStaticContent.getQuote(username)}
                    style={{ border: '' }}
                >
                    <FacebookIcon />
                </FBShareButton>
                <TwtrShareButton
                    url={shareStaticContent.shareUrl}
                    title={shareStaticContent.shareTwitterStaticContent.getMessage(username)}
                    via={shareStaticContent.shareTwitterStaticContent.twitterHandle}
                    hashtags={shareStaticContent.shareTwitterStaticContent.hashtags}

                    style={{ border: '' }}
                >
                    <TwitterIcon />
                </TwtrShareButton>
                <MailShareButton
                    style={{ border: '' }}
                    url={shareStaticContent.shareUrl}
                    subject={shareStaticContent.shareEmailStaticContent.getSubject(username)}
                    body={shareStaticContent.shareEmailStaticContent.getBody(username)}
                >
                    <Icon
                        icon='mail-dark'
                        size={24}
                    />
                </MailShareButton>
            </Container>
            <Spacer v={20}/>
        </ModalBase>
    )
}

const SharePledgeText = styled.h1`
    ${text(500, 18, 32, 'card-title')}
    align-self: center;
    text-align: center;
    margin-bottom: 56px;
`

const FBShareButton = styled(FacebookShareButton)`
    width: 48px;
    height: 48px;
    ${border({
        radius: 24,
        color: 'access-content-icon',
        width: 1
    })}
    display: inline-flex;
    padding: 0;
    justify-content: center;
    align-items: center;
    &:hover {
        background-color: ${backgroundColor['dropdown-item-hover']};
    }
`

const MailShareButton = styled(EmailShareButton)`
    width: 48px;
    height: 48px;
    ${border({
        radius: 24,
        color: 'access-content-icon',
        width: 1
    })}
    display: inline-flex;
    padding: 0;
    justify-content: center;
    align-items: center;
    &:hover {
        background-color: ${backgroundColor['dropdown-item-hover']};
    }
`

const TwtrShareButton = styled(TwitterShareButton)`
    width: 48px;
    height: 48px;
    ${border({
        radius: 24,
        color: 'access-content-icon',
        width: 1
    })}
    display: inline-flex;
    padding: 0;
    justify-content: center;
    align-items: center;
    &:hover {
        background-color: ${backgroundColor['dropdown-item-hover']};
    }
`

const Container = styled.div`
    display: flex;
    align-items: center;
    flex-direction: row;
    justify-content: space-between;
    padding-left: 100px;
    padding-right: 100px;
`