import { useDeleteAccountMutation } from "@haystackdev/cicf-content-api";
import CICFButton from "components/CICFButton";
import CICFInput from "components/CICFInput";
import Spacer from "components/Spacer";
import SettingsCard from "features/Settings/SettingsCard";
import { useState } from "react";
import { useNavigate } from "react-router-dom";
import useAuth from "state/authState";
import styled from "styled-components";
import { text } from "styleUtilities";

export default function SettingsDeleteAccount() {
    const [deleteText, setDeleteText] = useState<string>('');
    const deleteAccountMutation = useDeleteAccountMutation();
    const logout = useAuth((s)=>s.logout);
    const navigate = useNavigate();

    function onDeletePress() {
        
        if(confirm('Do you really want to delete your account? This cannot be undone.')) {
            deleteAccountMutation.mutateAsync().then(()=>{
                logout();
                navigate('/')
            })
        }
    }

    const disabled = deleteText !== 'DELETE';

    return (
        <SettingsCard
            title="Delete Account"
        >   
            <Text>
                Deletion will permenantly delete all your account information as well as all saved events and learning progress. Type "DELETE" into the input below and press the delete button if you're sure you want to delete your account.
            </Text>
            <Spacer 
                v={50}
            />
            <CICFInput 
                value={deleteText}
                onChange={setDeleteText}
                placeholder={`Enter 'DELETE'`}
            />
            <Spacer 
                v={20}
            />
            <CICFButton 
                text="Delete Account"
                onClick={onDeletePress}
                disabled={disabled}
                variant={disabled?'disabled':'primary'}
            />
        </SettingsCard>
    )
}

const Text = styled.p`
    ${text(400, 14, 24, 'card-paragraph')}
`