import { usePledgeQuery, useSignPledgeMutation, useUserProfileQuery } from "@haystackdev/cicf-content-api";
import CICFButton from "components/CICFButton";
import PledgeGraphic from "components/Graphics/PledgeGraphic";
import Page from "components/Page";
import PledgeSignature from "components/PledgeSignature/PledgeSignature";
import QuerySuspended from "components/QuerySuspended";
import Spacer from "components/Spacer";
import PledgeUrlParams from "features/Pledge/PledgeUrlParams";
import React, { useState } from "react";
import { useNavigate, useParams } from "react-router-dom";
import useModalStore from "state/modals";
import styled from "styled-components";
import { HOME_PATH } from "topLevelRoutes";

export default function Pledge() {

  const params = useParams<PledgeUrlParams>();
  const pledgeQuery = usePledgeQuery(params.pledgeId);
  const userProfileQuery = useUserProfileQuery();
  const signPledgeMutation = useSignPledgeMutation();
  const showShareModal = useModalStore((s) => s.showSharePledgeModal)
  const navigate = useNavigate();

  function onPledgeClick() {
    signPledgeMutation.mutate({
      pledgeVersionId: pledgeQuery.data!.pledgeVersionId
    })
  }

  function onBackClick() {
    navigate('/' + HOME_PATH)
  }

  function onShareClick() {
    showShareModal();
  }

  return (
    <Page>
      <PledgeContainer>
        <Spacer
          v={-30}
        />
        <PledgeGraphic />
        <Spacer
          v={30}
        />
        <QuerySuspended
          queries={[pledgeQuery, userProfileQuery]}
        >

          {pledgeQuery.data && userProfileQuery.data &&
            <>
              <PledgeTitle>
                Pledge for Equality and Inclusion
              </PledgeTitle>
              <PledgeText>
                {pledgeQuery.data.pledgeText.replace('[insert name]', userProfileQuery.data.firstName + ' ' + userProfileQuery.data.lastName)}
              </PledgeText>
              <Spacer v={30}/>
              <PledgeSignature
                name={`${userProfileQuery.data.firstName} ${userProfileQuery.data.lastName}`}
                signature={userProfileQuery.data!.signedPledge}
              />
              <Spacer v={30}/>
              {!userProfileQuery.data!.signedPledge ?
                <CICFButton
                  onClick={onPledgeClick}
                  text="Sign Pledge"
                />
                :
                <>
                  <CICFButton
                    onClick={onShareClick}
                    text='Share'
                    variant="primary"
                  />
                  <Spacer v={16} />
                  <CICFButton
                    onClick={onBackClick}
                    text='Back to Home'
                    variant="outline"
                  />
                </>
              }
              <Spacer
                v={100}
              />
            </>
          }
        </QuerySuspended>
      </PledgeContainer>
    </Page>
  );
}


export const PledgeContainer = styled.div`
  position: relative;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
`;

export const PledgeTitle = styled.h2`
  color: #211d3a;
  font-size: 44px;
  line-height: 56px;
  font-weight: 600;
  width: 537px;
  margin-bottom: 30px;
`;

export const PledgeText = styled.p`
  color: #474554;
  width: 548px;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
`;

