import { useCheckChangeEmailVerfiedQuery, useConfirmEmailChangeMutation } from "@haystackdev/cicf-content-api";
import { CardCentered } from "components/Card";
import LoadingIndicator from "components/LoadingIndicator";
import Page from "components/Page";
import Spacer from "components/Spacer";
import { CHANGE_EMAIL_CONFIRM_PATH, CHANGE_EMAIL_SUCCESS_BASE_PATH, CHANGE_EMAIL_SUCCESS_PATH } from "features/ChangeEmail/ChangeEmailRoutes";
import { useEffect, useState } from "react";
import { useNavigate, useParams } from "react-router-dom";
import styled from "styled-components";
import { text } from "styleUtilities";
import { CHANGE_EMAIL_PATH } from "topLevelRoutes";
import ChangeEmailChangeUrlParameters from "types/UrlParameters/ChangeEmailChangeUrlParameters";

export default function ChangeEmailChange() {
    const params = useParams<ChangeEmailChangeUrlParameters>();
    const [error, setError] = useState<string>('');
    const confirmEmailChangeMutation = useConfirmEmailChangeMutation();
    const navigate = useNavigate();

    useEffect(()=>{
        confirmEmailChangeMutation.mutateAsync({
            token: params.token!
        }).then((r)=>{
            if(!r.success) {
                setError("Email couldn't be changed, your link may have expired. Try resending the email in account settings.");
                return;
            }
            navigate(`/${CHANGE_EMAIL_PATH}/${CHANGE_EMAIL_SUCCESS_BASE_PATH}/${r.changedTo}`)
        }).catch(()=>{
            setError("Couldn't confirm email, please check your internet connection.")
        })
    }, [])

    return (
        <Page>
            <CardCentered
                variant="primary"
            >
                <Title>
                    Email Change Confirmation
                </Title>
                <Spacer v={24}/>
                {confirmEmailChangeMutation.isLoading && 
                    <LoadingIndicator />
                }
                {error && 
                    <ErrorText>
                        {error}
                    </ErrorText>
                }
            </CardCentered>
        </Page>
    )
}

const Title = styled.h1`
    ${text(600, 32, 44, 'card-title')}
    text-align: center;
`

const ErrorText = styled.p`
    ${text(400, 14, 24, 'card-paragraph')}
`