import styled from "styled-components";
import { eventImageAspectRatio, touchstoneImageAspectRatio } from "theme";


const ContentImage = styled.img<{type: 'event' | 'touchstone'}>`
    width: 100%;
    aspect-ratio: ${p=>p.type=='event'?touchstoneImageAspectRatio:eventImageAspectRatio};
    object-fit: cover;
`

export default ContentImage;