import { useEventsQuery, useSavedEventIdsQuery } from "@haystackdev/cicf-content-api";
import QuerySuspended from "components/QuerySuspended";
import EventDetailContent from "features/Events/EventDetailPage/EventDetailContent";
import { useParams } from "react-router-dom";
import styled from "styled-components";
import { narrowContentWidth } from "theme";
import EventDetailUrlParameters from "types/UrlParameters/EventDetailUrlParameters";

export default function EventDetailPage() {
    const eventId = useParams<EventDetailUrlParameters>().eventId!;
    const eventsQuery = useEventsQuery();
    const savedEventIdsQuery = useSavedEventIdsQuery();

    return (
        <Container>
            <QuerySuspended
                queries={[eventsQuery, savedEventIdsQuery]}
            >
                {eventsQuery.data && savedEventIdsQuery.data &&
                    (
                        eventId in eventsQuery.data.idToEventMap ?
                            <EventDetailContent
                                event={eventsQuery.data.idToEventMap[eventId]}
                                isSaved={savedEventIdsQuery.data.has(eventId)}
                            />
                            :
                            <div>Event not found.</div>
                    )
                }
            </QuerySuspended>
        </Container>
    )
}


const Container = styled.div`
    width: ${narrowContentWidth}px;
    display: flex;
    flex-direction: column;
    padding-bottom: 60px;

`
