import { UserProfile, useUserProfileQuery } from "@haystackdev/cicf-content-api";
import Organization from "@haystackdev/cicf-content-api/lib/types/Organization";
import OrganizationInput from "components/OrganizationInput";

import { Div } from "./styles";

const ORGANIZATION_SEARCH_RESULT_ROW_HEIGHT = 60;
const MAX_ORGANIZATION_SEARCH_RESULTS = 3;

export function OrganizationCard({
  isEditing,
  editingOrganizations,
  profile,
  onOrganizationsChange,
}: {
  isEditing: boolean,
  editingOrganizations: Set<string> | null,
  profile: UserProfile,
  onOrganizationsChange: (v: Set<string>)=>void,
}) {
  // https://crmbusiness.wordpress.com/2014/12/08/why-code-readability-is-important/#:~:text=Code%20readability%20is%20one%20of,to%20debug%2C%20maintain%20and%20extend.
  const showOrganizations: Set<string> = editingOrganizations?editingOrganizations:(profile.demographics.affiliatedOrganizations?new Set(profile.demographics.affiliatedOrganizations.map((e)=>e)):new Set())

  return (
    <div style={{ display: "flex", justifyContent: "center", zIndex: 1000 }}>
      <div
        style={{
          width: "551px",
          padding: 20,
          backgroundColor: "#FFF",
          borderRadius: 10,
          boxShadow: "0px 8px 15px rgba(0, 0, 0, 0.05)",
        }}
      >
        <h3 style={{marginBottom: 10, fontSize: "18px", fontWeight: 600 }}>
          Affiliated Organization
        </h3>

        {isEditing ? (
          <OrganizationInput
            selectedOrganizationsSet={showOrganizations}
            onChange={onOrganizationsChange}
          />
        ) : (
          <div
            style={{
              marginTop: 12,
              whiteSpace: 'pre',
            }}
          >
            {profile.demographics.affiliatedOrganizations && profile.demographics.affiliatedOrganizations.length
              ? profile.demographics.affiliatedOrganizations
                  .map((e) => e)
                  .join("\n")
              : "You haven't selected an organization."}
          </div>
        )}
      </div>
    </div>
  );
}
