import CICFButton from "components/CICFButton";
import CICFTextArea from "components/CICFTextArea";
import Spacer from "components/Spacer";
import ToggleSwitch from "components/ToggleSwitch";
import ModalBase from "modals/ModalBase";
import { useState } from "react";
import styled from "styled-components";
import { text } from "styleUtilities";
import {contentCopy, usePostTouchstoneNoteMutation, useUserProfileQuery} from '@haystackdev/cicf-content-api';
import useModalStore from "state/modals";

export default function AddNoteModal() {
    const modalState = useModalStore((s)=>s.addNoteModalState);
    if(!modalState) throw new Error("Add note modal opened with no state.");
    const hide = useModalStore((s)=>s.hidePresentingModal)

    const {touchstoneId} = modalState;


    const [text, setText] = useState<string>('');
    const [makePublic, setMakePublic] = useState<boolean>(false);


    const userProfileQuery = useUserProfileQuery();
    const postNoteMutation = usePostTouchstoneNoteMutation();


    function onChangeText(target: HTMLTextAreaElement) {
        setText(target.value);
    }

    function onAddPress() {
        const profile = userProfileQuery.data!; // Button will be disabled if we don't have the data. 
        postNoteMutation.mutateAsync({
            createTime: (new Date()).getTime(),
            createdBy: {
                profileImageUrl: profile.profileImageUrl,
                id: profile.id,
                name: `${profile.firstName} ${profile.lastName}`
            },
            text,
            associatedWithContentId: touchstoneId,
            isPublic: makePublic,
        }).then((response)=>{
            // if(response.wasFlagged) {
            //     window.alert(`${contentCopy.noteFlaggedMessage.title}. ${contentCopy.noteFlaggedMessage.message}`)
            // }
            hide();
        }).catch(()=>{
            window.alert("Couldn't add note, check your internet and/or try again.")
        });
    }

    const addDisabled = !text || !userProfileQuery.data;

    return (
        <ModalBase
            title='Add Notes'
        >
            <CICFTextArea 
                value={text}
                onChange={(e)=>onChangeText(e.target as HTMLTextAreaElement)}
            />
            <Spacer 
                v={30}
            />
            <MakePublicRow>
                <MakePublicLabel>
                    Make this note public
                </MakePublicLabel>
                <ToggleSwitch 
                    value={makePublic}
                    onChange={setMakePublic}
                />
            </MakePublicRow>
            <Spacer 
                v={31}
            />
            <Button
                text="Add Note"
                onClick={onAddPress}
                variant={addDisabled?'disabled':'primary'}
                disabled={addDisabled}
            />
        </ModalBase>
    )
}

const MakePublicRow = styled.div`
    display: flex;
    align-self: stretch;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
`

const MakePublicLabel = styled.span`
    ${text(400, 16, 19, 'card-title')}
`

const Button = styled(CICFButton)`
    width: 156px;
`