import { useEventsQuery } from "@haystackdev/cicf-content-api";
import Page from "components/Page";
import QuerySuspended from "components/QuerySuspended";
import EventsContent from "features/Events/EventsPage/EventsContent";
import useWindowSize from "hooks/useWindowSize";
import styled from "styled-components";
import { text } from "styleUtilities";


export default function EventsPage() {
    const eventsQuery = useEventsQuery();
    const {width} = useWindowSize({
        width: 1113,
        height: 0,
    });
    return (
        <Container
            // min width 100 max width 1300;
            width={Math.max(1000 - 60, Math.min(1300, width - 60))}
        >
            <QuerySuspended
                queries={[eventsQuery]}
            >   
                <Header>
                    Events
                </Header>
                {eventsQuery.data &&
                    <EventsContent 
                        eventsQueryData={eventsQuery.data!}
                    />
                }
            </QuerySuspended>
        </Container>
    )
}

const Container = styled(Page)`
    
`

const Header = styled.h1`
    ${text(600, 32, 44, 'page-header')}
`