import {
  useRacialIdentitiesQuery,
  UserProfile,
  useUserProfileQuery
} from "@haystackdev/cicf-content-api";
import LabelledCheckbox from "components/LabelledCheckbox";
import QuerySuspended from "components/QuerySuspended";
import { updateSet } from "helpers";


export function EthnicityCard({
  isEditing,
  profile,
  editingEthnicitiesSet,
  onEthnicitiesChange,
}: {
  isEditing: boolean,
  profile: UserProfile,
  editingEthnicitiesSet: Set<string> | null,
  onEthnicitiesChange: (v: Set<string>) => void,
}) {
  const racialIdentities = useRacialIdentitiesQuery();
  const showEthnicities: Set<string> = editingEthnicitiesSet ? editingEthnicitiesSet : (profile.demographics.ethnicities ? new Set(profile.demographics.ethnicities) : new Set());


  return (
    <div style={{ display: "flex", justifyContent: "center" }}>
      <div
        style={{
          width: "551px",
          paddingTop: 20,
          paddingBottom: 20,
          paddingLeft: 20,
          backgroundColor: "#FFF",
          borderRadius: 10,
          boxShadow: "0px 8px 15px rgba(0, 0, 0, 0.05)",
        }}
      >
        <QuerySuspended
          size={40}
          queries={[racialIdentities]}
        >
          {racialIdentities.data &&
            <>
              <h3 style={{ margin: 0, fontSize: "18px", fontWeight: 600 }}>
                Racial Identity
              </h3>

              {isEditing ? (
                racialIdentities.data.map((identity)=>(
                  <LabelledCheckbox 
                    style={{width: '50%', height: 60,}}
                    value={showEthnicities.has(identity)}
                    onChange={()=>{
                      if(showEthnicities.size == 1 && showEthnicities.has(identity)) return;
                      onEthnicitiesChange(updateSet(showEthnicities, identity, !showEthnicities.has(identity)))
                    }}
                    label={identity}
                    key={identity}
                  />
                ))
              ) : (
                <div
                  style={{
                    marginTop: 12,
                  }}
                >
                  {showEthnicities.size ?
                    Array.from(showEthnicities).join(", ")
                    : "You haven't selected an ethnicity."}
                </div>
              )}
            </>
          }

        </QuerySuspended>
      </div>


    </div>
  );
}
