import { Event } from "@haystackdev/cicf-content-api";
import IconWithText from "components/IconWithText";

export default function EventPriceIconWithText({
    event
}: {
    event: Event
}) {
    return (
        <IconWithText
            icon='wt-ticket-price'
            text={event.price ? `$${event.price.toFixed(2)}` : 'Free'}
        />
    )
}
