import styled from "styled-components";
import { border } from "styleUtilities";
import { backgroundColor } from "theme";

const WIDTH = 33;
const HEIGHT = 18;

export default function ToggleSwitch({
    value,
    onChange,
}: {
    value: boolean,
    onChange: (value: boolean) => void,
}) {
    return (
        <Label
            switchOn={value}
        >
            <input type="checkbox" checked={value} onChange={() => onChange(!value)} />
            <Switch
                switchOn={value}

            />
        </Label>
    );
}

interface Props {
    switchOn: boolean,
}

const Label = styled.label<Props>`
    position: relative;
    display: inline-block;
    width: ${WIDTH}px;
    height: ${HEIGHT}px;
    background-color: ${p => backgroundColor[p.switchOn ? 'toggle-switch-on' : 'toggle-switch-off']};
    box-sizing: content-box;
    transition: all 0.2s;
    ${p => border({
    width: 2,
    radius: HEIGHT,
    color: p.switchOn ? 'toggle-switch-on' : 'toggle-switch-off'
})}
    & input[type="checkbox"] {
        display: none;
    }
`

const Switch = styled.span<Props>`
    width: ${HEIGHT}px;
    height: ${HEIGHT}px;
    border-radius: 1000px;
    background-color: ${backgroundColor['toggle-switch-knob']};
    position: absolute;
    left: 0px;
    top:0px;
    transition: all 0.2s;
    transform: translateX(${p => p.switchOn ? WIDTH - HEIGHT : 0}px);
`