import { useEffect, MouseEventHandler } from "react";

export function useOnClickOutside(f: () => void) {
    useEffect(() => {
        document.addEventListener('click', () => {
            f();
        });
        return () => document.removeEventListener('click', f);
    }, [])
    const onClick: MouseEventHandler<HTMLDivElement> = ((e: MouseEvent) => e.stopPropagation()) as any;
    return {
        props: {
            onClick
        }
    }
}