import CICFButton from "components/CICFButton";
import CICFTextArea from "components/CICFTextArea";
import Spacer from "components/Spacer";
import ToggleSwitch from "components/ToggleSwitch";
import ModalBase from "modals/ModalBase";
import { useState } from "react";
import styled from "styled-components";
import { text } from "styleUtilities";
import { useDeleteTouchstoneNoteMutation,  } from '@haystackdev/cicf-content-api';
import useModalStore from "state/modals";
import Icon from "components/Icon";

export default function DeleteTouchstoneNoteModal() {
    const modalState = useModalStore((s) => s.deleteTouchstoneNoteModalState);
    if (!modalState) throw new Error("Add note modal opened with no state.");
    const hide = useModalStore((s) => s.hidePresentingModal)
    const note = modalState.note

    const deleteNoteMutation = useDeleteTouchstoneNoteMutation();

    function onCancelPress() {
        hide();
    }

    function onDeletePress() {
        deleteNoteMutation.mutateAsync({
            noteId: note.id,
            touchstoneId: note.associatedWithContentId,
        }).then(() => {
            hide();
        }).catch(() => {
            window.alert("Couldn't add note, check your internet and/or try again.")
        })
    }


    return (
        <ModalBase title="Delete Note">
            <TopContainer>
                <AreYouSureSubtext>
                    The action cannot be undone.
                </AreYouSureSubtext>
            </TopContainer>
            <CICFTextArea
                value={note.text}
                disabled={true}
            />
            <Spacer
                v={30}
            />
            <ButtonsRow>
                <Button 
                    onClick={onCancelPress}
                    text="Cancel"
                    variant='outline'
                    hideShadow={true}
                />
                <Spacer h={50}/>
                <Button
                    text="Delete"
                    onClick={onDeletePress}
                    variant='primary'
                    loading={deleteNoteMutation.isLoading}
                />
            </ButtonsRow>
            {/* <TitleContainer>
                <Icon
                    size={24}
                    icon='trash'
                />
                <Spacer 
                    h={16}
                />
                <Title>
                    Delete Note
                </Title>
            </TitleContainer> */}
        </ModalBase>
    )
}


const TopContainer = styled.div`
    display: flex;
    align-self: stretch;
    flex-direction: column;
    align-items: center;
`

const Button = styled(CICFButton)`

`

const AreYouSureText = styled.h1`
    ${text(600, 18, 24, 'card-title')}
    margin-bottom: 16px;
`

const AreYouSureSubtext = styled.p`
    ${text(400, 16, 24, 'card-paragraph')}
    margin-bottom: 16px;
    align-self: flex-start;
    
`

const ButtonsRow = styled.div`
    display: flex;
    align-self: stretch;
    justify-content: center;
    flex-direction: row;
`

const TitleContainer = styled.div`
    display: inline-flex;
    position: absolute;
    top: 20px;
    left: 20px;
    flex-direction: row;
    align-items: center;
`

const Title = styled.span`
    ${text(500, 14, 17, 'card-paragraph')}
`