import { Curriculum, homescreenStateFromProfile, useCurriculumInfo, useCurriculumQuery, useEventsQuery, useSavedEventIdsQuery, useUserProfileQuery } from "@haystackdev/cicf-content-api";
import { createOnboardingChecklistFromProfile } from "@haystackdev/cicf-content-api/lib/types/OnboardingChecklist";
import FlexColumn from "components/FlexColumn";
import MVMTShimmer, { MVMTShimmerFixed } from "components/MVMTShimmer/MVMTShimmer";
import Page from "components/Page";
import QuerySuspended from "components/QuerySuspended";
import Spacer from "components/Spacer";
import dev from "dev";
import EventsColumn from "features/Home/Home/EventsColumn";
import HomeColumnTitle from "features/Home/Home/HomeColumnTitle";
import HomeContentCard from "features/Home/Home/HomeContentCard";
import HomeContentCardAssessment from "features/Home/Home/HomeContentCardAssessment";
import HomeContentCardLearningItem from "features/Home/Home/HomeContentCardLearningItem";
import HomeDataCard from "features/Home/Home/HomeDataCard";
import HomeScreenTopSection from "features/Home/Home/HomeScreenTopSection";
import OnboardingWidget from "features/Home/Home/OnboardingWidget";
import SignPledgeCard from "features/Home/Home/SignPledgeCard";
import React, { useMemo, useState } from "react";
import styled from "styled-components";
import { getFeaturedLearningItem } from "ux/getFeaturedLearningItem";

export default function Home() {
  const userProfileQuery = useUserProfileQuery();
  const curriculumQuery = useCurriculumQuery();
  const eventsQuery = useEventsQuery();
  const savedEventIdsQuery = useSavedEventIdsQuery();
  const curriculumInfo = useCurriculumInfo();

  const featuredLearningItem = useMemo(() => {
    if (!curriculumQuery.data) return null;
    return getFeaturedLearningItem(curriculumQuery.data.curriculum);
  }, [curriculumQuery.data])
  console.log("curr: " + !!curriculumQuery.data);
  console.log("events: " + !!eventsQuery.data);
  console.log("saved events: " + !! savedEventIdsQuery.data);
  console.log("Profile: ")
  console.log(userProfileQuery.data)
  
  return (
    <QuerySuspended
      queries={[curriculumQuery, userProfileQuery, savedEventIdsQuery]}
      loadingElement={<MVMTShimmerFixed />}
    >
      {curriculumQuery.data && userProfileQuery.data && savedEventIdsQuery.data &&
        <Page>
          <Header>{`Welcome ${userProfileQuery.data!.firstName}`}</Header>
          
          <HomeScreenTopSection 
            savedEventsCount={savedEventIdsQuery.data.size}
            touchstonesCompleteCount={curriculumInfo.itemsCompleted}
            userProfile={userProfileQuery.data}
          />
          <Spacer
            v={40}
          />
          <ColumnContainer>
            <FlexColumn>
              <HomeColumnTitle>
                {learningColumnTitleText(curriculumQuery.data!.curriculum)}
              </HomeColumnTitle>
              {featuredLearningItem &&
                <HomeContentCardLearningItem
                  item={featuredLearningItem.item}
                />}

            </FlexColumn>
            <Spacer h={24}/>
            <FlexColumn>
              <HomeColumnTitle>
                Measure Your Progress
              </HomeColumnTitle>
              <HomeContentCardAssessment
                userProfile={userProfileQuery.data!}
                curriculum={curriculumQuery.data!.curriculum}
              />
            </FlexColumn>
            <Spacer h={24} />
            <EventsColumn 
              query={eventsQuery}
            />
          </ColumnContainer>
        </Page>
      }
    </QuerySuspended>

  );
};



const Header = styled.h1`
  font-size: 32px;
  line-height: 44px;
  font-weight: 600;
  margin-bottom: 30px;

`

const ColumnContainer = styled.div`
  display: flex;
  flex-direction: row;
  padding-bottom: 20px;
`
function learningColumnTitleText(curriculum: Curriculum) {
  return 'Start Your Learning Pathway Here'
}