import LearningPathwayDetailPage from 'features/Learning/LearningPathwayDetailPage';
import LearningPathwaysPage from 'features/Learning/LearningPathways';
import TouchstoneDetail from 'features/Learning/TouchstoneDetail';
import {Routes, Route, Navigate} from 'react-router-dom';
import RouteMap from 'types/RouteMap';

export const LEARNING_PATHWAY_DETAIL_PATH = 'pathway';
export const TOUCHSTONE_DETAIL_PATH = 'touchstone';

type RouteName = '' | 'pathway/:pathwayId' | 'touchstone/:touchstoneId';

const learningRoutesMap: RouteMap<RouteName> = {
    'pathway/:pathwayId': ()=><LearningPathwayDetailPage/>,
    'touchstone/:touchstoneId': ()=><TouchstoneDetail />,
    '': ()=><LearningPathwaysPage/> // Make sure this is last since it's the default.
}

export default function LearningRoutes() {
    return (
        <Routes>
            {Object.keys(learningRoutesMap).map((routeName: any)=>{
                const Component = learningRoutesMap[routeName as RouteName];
                return (
                    <Route
                        key={routeName}
                        element={<Component/>}
                        path={'/'+routeName}
                    />
                )
            })}
        </Routes>
    )
}